<template>
<div class="Scale">
  <ecg-box class="d-flex mb-20" style="background: #A7A7B1">
    <div class="chart mr-40">
      <ecg-heart-beat
        height="100%"
        :lines="[
          { color: '#808080', width: 1, value: 0 },
          { color: '#808080', width: 1, value: 90 },
          { color: '#808080', width: 1, value: -90 }
        ]"
        :dataFunc="genData"/>
      <span class="t-1">Vent Mode: VCV</span>
      <span class="t-2">Paw</span>
      <span class="t-3">50</span>
      <span class="t-4">0</span>
    </div>
    <ecg-slider-double
      :minValue="20"
      :maxValue="80"
      height="400px"
      :dotOptions="{ min: 10, max: 90 }"
      :marks="{0: '0', 50: '50', 100: '100'}"/>
    <div class="flex-center ml-auto">
      <div>
        <ecg-counter/>
        <ecg-counter class="mt-40"/>
      </div>
    </div>
  </ecg-box>
  <ecg-label>Wave</ecg-label>
  <ecg-button-list class="mb-20" :list="[['Paw', 'Flow', 'Vol']]"/>
  <ecg-label>Scale</ecg-label>
  <div class="d-flex">
    <ecg-button-list defaultActive="0-3" :list="[['0-10', '0-20', '0-30', '0-50', '0-100']]"/>
    <ecg-button class="custom-btn">
      <div>Custom</div>
      <div>0-50</div>
    </ecg-button>
  </div>
</div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
export default {
  methods: {
    genData () {
      const data1 = _map(new Array(12 * 2), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return _random(-50, -45)
          case 1:
            return _random(-35, -30)
          case 2:
            return _random(-20, -15)
          case 3:
            return _random(-50, -45)
          case 4:
            return -75
          case 5:
            return -79
          case 6:
            return -82
          case 7:
            return -85
          case 8:
            return -85
          case 9:
            return -85
          case 10:
            return -85
          case 11:
            return -85
        }
      })
      return [
        {
          color: '#fff',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.Scale {
  .chart {
    width: 660px; height: 400px;
    position: relative;
    span {
      position: absolute;
      font-weight: bold;
      &.t-1 { left: 50%; bottom: 0px; transform: translateX(-50%); }
      &.t-2 { right: 80px; top: 25px; }
      &.t-3 { right: 10px; top: 25px; }
      &.t-4 { right: 10px; bottom: 25px; }
    }
  }
  .custom-btn {
    flex-direction: column;
    div:nth-child(2) { background: #000; width: 90%; }
  }
}
</style>
