<template>
  <div class="main-5">
    <draggable @choose="handleChoose" @unchoose="handleUnchoose" @change="handleChange" :list="draggablePart" class="part-wrapper" :delay="1000">
      <component v-for="c of draggablePart" :key="c[0]" :is="c[1]" ref="part"></component>
    </draggable>
    <div class="interbed">
      <div class="interbed-item">
        <div class="interbed-item-info1 c-white">TELE-01</div>
        <div class="interbed-item-info2 c-black bg-white d-flex flex-center">NO Patient Name</div>
        <div class="interbed-item-info3 c-green flex-center">HR<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info4 c-green">80</div>
        <div class="interbed-item-info5 c-atrovirens flex-center">VPC<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info6 c-atrovirens flex-center">ST-Ⅱ<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info7 c-atrovirens">0</div>
        <div class="interbed-item-info8 c-atrovirens">0.06</div>
        <div class="interbed-item-chart1">
          <ecg-heart-beat
            width="100%"
            height="100%"
            :dataFunc="genData"/>
          <div class="interbed-item-chart1-info1">Ⅱ</div>
          <div class="interbed-item-chart1-info2">x1</div>
          <div class="interbed-item-chart1-info3">1mV</div>
          <div class="interbed-item-chart1-info4"></div>
        </div>
      </div>
      <div class="interbed-item">
        <div class="interbed-item-info1 c-white">Room-15</div>
        <div class="interbed-item-info2 c-black bg-white d-flex flex-center">NO Patient Name</div>
        <div class="interbed-item-info3 c-green flex-center">HR<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info4 c-green">80</div>
        <div class="interbed-item-info5 c-atrovirens flex-center">VPC<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info6 c-atrovirens flex-center">ST-Ⅱ<img v-if="hiqviewMode" width="30" src="./main-5/images/wifi.jpg"></div>
        <div class="interbed-item-info7 c-atrovirens">0</div>
        <div class="interbed-item-info8 c-atrovirens">0.06</div>
        <div class="interbed-item-chart1">
          <ecg-heart-beat
            width="100%"
            height="100%"
            :dataFunc="genData"/>
          <div class="interbed-item-chart1-info1">Ⅱ</div>
          <div class="interbed-item-chart1-info2">x1</div>
          <div class="interbed-item-chart1-info3">1mV</div>
          <div class="interbed-item-chart1-info4"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import { random as _random, map as _map } from 'lodash'
import { mapState } from 'vuex'

export default {
  components: { draggable },
  data () {
    return {
      draggablePart: [
        [1, require('./main-5/part1').default],
        [2, require('./main-5/part2').default],
        [3, require('./main-5/part3').default],
        [4, require('./main-5/part4').default],
        [5, require('./main-5/part5').default],
        [6, require('./main-5/part6').default],
        [7, require('./main-5/part7').default],
        [8, require('./main-5/part8').default]
      ]
    }
  },

  computed: {
    ...mapState('app', ['hiqviewMode'])
  },

  methods: {
    handleChoose (e) {
      this.$refs.part[e.oldIndex].drag = true
    },
    handleUnchoose (e) {
      this.$refs.part[e.oldIndex].drag = false
    },
    handleChange (e) {
      const moveEl = this.$refs.part.splice(e.moved.oldIndex, 1)[0]
      this.$refs.part.splice(e.moved.newIndex, 0, moveEl)
    },
    genData () {
      const data1 = _map(new Array(10 * 20), (item, idx) => {
        if (idx % 20 === 15) {
          return _random(88, 90)
        } else {
          return _random(0, 25)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.main-5{
  height: 100%;
  .part-wrapper{
    height: calc(100% * 11 / 17);
    .part1{ height: calc(100% * 2 / 11); }
    .part2{ height: calc(100% * 3 / 11); }
    .part3{ height: calc(100% * 1 / 11); }
    .part4{ height: calc(100% * 1 / 11); }
    .part5{ height: calc(100% * 1 / 11); }
    .part6{ height: calc(100% * 1 / 11); }
    .part7{ height: calc(100% * 1 / 11); }
    .part8{ height: calc(100% * 1 / 11); }
  }
  .interbed{
    height: calc(100% * 6 / 17);
    display: flex;
    &-item{
      width: 50%;
      position: relative;
      box-sizing: border-box;
      border-top: 3px solid #5C5C63;
      border-bottom: 3px solid #5C5C63;
      &:first-of-type{
        border-right: 3px solid #5C5C63;
      }
      >div{
        position: absolute;
      }
      &-info1{ font-size: 24px; left: 10px; top: 30px;}
      &-info2{ font-size: 24px; right: 40px; top: 10px; height: 40px;}
      &-info3{ font-size: 16px; left: 10px; top: 70px;}
      &-info4{ font-size: 60px; left: 80px; top: 70px;}
      &-info5{ font-size: 16px; left: 10px; top: 140px;}
      &-info6{ font-size: 16px; left: 10px; top: 175px;}
      &-info7{ font-size: 24px; left: 110px; top: 140px;}
      &-info8{ font-size: 24px; left: 90px; top: 170px;}
      &-chart1{
        width: 80%;
        height: 120px;
        right: 0px;
        top: 70px;
        >div{
          position: absolute;
        }
        &-info1{ font-size: 14px; right: 150px; top: 20px; }
        &-info2{ font-size: 14px; right: 80px; top: 20px; }
        &-info3{ font-size: 14px; right: 20px; top: 20px; }
        &-info4{
          right: 10px; top: 50%; transform: translateY(-50%);
          height: 50%;
          border-left: 1px dashed #fff;
          position: absolute;
          &::before{
            content: '';
            display: block;
            width: 10px;
            height: 1px;
            background-color: #fff;
            position: absolute;
            top: 0px;
            left: 50%;
            transform: translateX(-50%);
          }
          &::after{
            content: '';
            display: block;
            width: 10px;
            height: 1px;
            background-color: #fff;
            position: absolute;
            bottom: 0px;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }
    }
  }
}
</style>
