<template>
  <div class="part1 d-flex">
    <div class="info" @click="$dialog.open('BasicParameter', [0, 0])">
      <div class="info-1 c-green">HR-Ⅱ</div>
      <div class="info-2 c-green">80</div>
      <div class="info-11 c-darken-green" @click.stop="openBPM">[bpm]</div>
      <div class="info-3 c-darken-green" @click.stop="openBPM">140<br>50</div>
      <div class="info-4 c-atrovirens flex-center">ST-Ⅱ<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-5 c-atrovirens">0.5<span class="small c-darken-atrovirens">[mm]</span></div>
      <div class="info-6 c-atrovirens flex-center">ST-V1<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-7 c-atrovirens">0.7<span class="small c-darken-atrovirens">[mm]</span></div>
      <div class="info-8 c-atrovirens flex-center">VPC<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-9 c-atrovirens">0</div>
      <div class="info-10" v-if="hiqviewMode"><img src="./images/wifi.jpg"></div>
      <div class="drag-info" v-if="drag">ECG</div>
    </div>
    <div class="chart" @click="handleThumbnail">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"/>
      <div class="chart-info c-white">
        <div class="chart-info-1">Adult</div>
        <div class="chart-info-2">FiltMon</div>
        <div class="chart-info-3" @click.stop="openLead">Ⅱ</div>
        <div class="chart-info-4" @click.stop="openSensitivity">x1<br>Auto</div>
        <div class="chart-info-5">1mV</div>
        <div class="chart-info-6"></div>
        <div class="chart-info-7">V1</div>
        <div class="chart-info-8">x1<br>Auto</div>
        <div class="chart-info-9">1mV</div>
        <div class="chart-info-10"></div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <div class="thumbnail-part1">
          <ecg-heart-beat-static
            width="100%"
            height="100%"
            :dataFunc="thumbnailGenData1"/>
          <div class="thumbnail-part1-line1"></div>
          <div class="thumbnail-part1-line2"></div>
          <div class="thumbnail-part1-line3"></div>
          <div class="thumbnail-part1-info1 c-purple">PR</div>
          <div class="thumbnail-part1-info2 c-atrovirens">ST-Ⅱ</div>
          <div class="thumbnail-part1-info3 c-purple">200</div>
          <div class="thumbnail-part1-info4 c-atrovirens">0.50</div>
          <div class="thumbnail-part1-info5 c-purple">0</div>
          <div class="thumbnail-part1-info6 c-atrovirens">-0.50</div>
        </div>
        <div class="thumbnail-part2">
          <ecg-heart-beat-static
            width="100%"
            height="100%"
            :dataFunc="thumbnailGenData2"/>
          <div class="thumbnail-part2-line1"></div>
          <div class="thumbnail-part2-line2"></div>
          <div class="thumbnail-part2-line3"></div>
          <div class="thumbnail-part2-info1 c-atrovirens">ST-V4</div>
          <div class="thumbnail-part2-info2 c-atrovirens">0.50</div>
          <div class="thumbnail-part2-info3 c-atrovirens">-0.50</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { random as _random, map as _map } from 'lodash'
export default {
  data () {
    return {
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['hiqviewMode', 'thumbnail'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    ...mapMutations('home', ['SET_BPMSWITCH', 'SET_SENSITIVITYSWITCH', 'SET_LEADSWITCH']),
    genData () {
      const data1 = _map(new Array(20 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(88, 90)
        } else {
          return _random(45, 50)
        }
      })
      const data2 = _map(new Array(20 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(-90, -88)
        } else {
          return _random(-50, -45)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data1
        },
        {
          color: '#00FF00',
          width: 2,
          data: data2
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    },
    thumbnailGenData1 () {
      return [
        {
          color: '#68E8A8',
          width: 2,
          data: [20, 20, 18, 18, 19, 19, 18, 18, 21, 21, 18, 18, 17, 17, 18, 18, 19, 19, 18, 18, 19, 19, 19, 19, 20, 20, 19, 19, 20, 20, 21, 21, 20, 20, 20, 20, 0, 0, -25, -25, -27, -27, -25, -25, 0, 0, -4, -4, 0, 0, -4, -4, 0, 0, -4, -4, -4, -4, 0, 0, -4, -4]
        }
      ]
    },
    thumbnailGenData2 () {
      return [
        {
          color: '#68E8A8',
          width: 2,
          data: [40, 40, 38, 38, 38, 38, 36, 36, 44, 44, 34, 34, 32, 32, 36, 36, 38, 38, 34, 34, 38, 38, 40, 40, 38, 38, 40, 40, 44, 44, 48, 48, 38, 38, 0, 0, -38, -38, -42, -42, -38, -38, -34, -34, -30, -30, -28, -28, -30, -30, -28, -28, -30, -30, -28, -28, -28, -28, -30, -30, -28, -28]
        }
      ]
    },
    openBPM () {
      this.SET_BPMSWITCH(true)
    },
    openSensitivity () {
      this.SET_SENSITIVITYSWITCH(true)
    },
    openLead () {
      this.SET_LEADSWITCH(true)
    }
  }
}
</script>

<style lang="scss" scoped>
.part1{
  position: relative;
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    border: 1px solid #808080;
    box-sizing: border-box;
    >div{
      position: absolute;
      text-align: right;
      display: flex;
      align-items: flex-start;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #00FF00;
      background-color: #333;
      color: #00FF00;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: 5px; top: 5px; }
    &-2{ font-size: 140px; right: 40px; top: 0px; }
    &-3{ font-size: 14px; right: 5px; top: 20px; }
    &-4{ font-size: 16px; left: 5px; top: 160px; }
    &-5{ font-size: 24px; left: 180px; top: 160px;}
    &-6{ font-size: 16px; left: 260px; top: 160px; }
    &-7{ font-size: 24px; right: 5px; top: 160px; }
    &-8{ font-size: 16px; left: 260px; top: 190px; }
    &-9{ font-size: 24px; right: 45px; top: 190px; }
    &-10{ left: 55px; top: 0px; >img{ width: 30px; }}
    &-11{ font-size: 14px; right: 5px; top: 5px; }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 340px; top: 20%; transform: translateY(-50%); }
      &-2{ right: 220px; top: 20%; transform: translateY(-50%); }
      &-3{ right: 170px; top: 20%; transform: translateY(-50%); }
      &-4{ right: 90px; top: 36px; }
      &-5{ right: 20px; top: 20%; transform: translateY(-50%); }
      &-6{
        right: 10px; top: 25%; transform: translateY(-50%);
        height: 12.5%;
        border-left: 1px dashed #fff;
        position: relative;
        &::before{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
        &::after{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          bottom: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      &-7{ right: 170px; top: 70%; transform: translateY(-50%); }
      &-8{ right: 90px; top: calc(50% + 36px); }
      &-9{ right: 20px; top: 70%; transform: translateY(-50%); }
      &-10{
        right: 10px; top: 75%; transform: translateY(-50%);
        height: 12.5%;
        border-left: 1px dashed #fff;
        position: relative;
        &::before{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
        &::after{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          bottom: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    .thumbnail{
      width: calc(100% / 6);
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      &-part1{
        box-sizing: border-box;
        border-top: 2px solid #5D558C;
        border-right: 2px solid #5D558C;
        border-bottom: 2px solid #5D558C;
        height: calc((100% - 20px) / 2);
        margin-bottom: 20px;
        position: relative;
        &-line1{ position: absolute; height: 100%; border-left: 2px solid #5D558C; left: 45%; top: 0px;}
        &-line2{ position: absolute; height: 100%; border-left: 2px dashed #5D558C; left: calc(45% / 2); top: 0px;}
        &-line3{ position: absolute; height: 100%; border-left: 2px dashed #5D558C; left: calc(45% + (55% / 2)); top: 0px;}
        &-info1{ position: absolute; font-size: 16px; left: 5px; top: 5px; }
        &-info2{ position: absolute; font-size: 16px; left: calc(45% / 2); top: 5px; }
        &-info3{ position: absolute; font-size: 16px; right: 22%; top: 5px; }
        &-info4{ position: absolute; font-size: 16px; right: 5px; top: 5px; }
        &-info5{ position: absolute; font-size: 16px; right: 22%; bottom: 5px; }
        &-info6{ position: absolute; font-size: 16px; right: 5px; bottom: 5px; }
      }
      &-part2{
        box-sizing: border-box;
        border-top: 2px solid #3A7F5D;
        border-right: 2px solid #3A7F5D;
        border-bottom: 2px solid #3A7F5D;
        height: calc((100% - 20px) / 2);
        position: relative;
        &-line1{ position: absolute; height: 100%; border-left: 2px solid #3A7F5D; left: 45%; top: 0px;}
        &-line2{ position: absolute; height: 100%; border-left: 2px dashed #3A7F5D; left: calc(45% / 2); top: 0px;}
        &-line3{ position: absolute; height: 100%; border-left: 2px dashed #3A7F5D; left: calc(45% + (55% / 2)); top: 0px;}
        &-info1{ position: absolute; font-size: 16px; left: 5px; top: 5px; }
        &-info2{ position: absolute; font-size: 16px; right: 5px; top: 5px; }
        &-info3{ position: absolute; font-size: 16px; right: 5px; bottom: 5px; }
      }
    }
  }
}
</style>
