<template>
<div class="DrugCalc-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        {
          type: 'group',
          text: 'Drug<br>Calc',
          children: [
            { type: 'item', text: 'Drug', component: require('@/dialogs/DrugCalc/Drug').default },
            { type: 'item', text: 'Data<br>Entry', component: require('@/dialogs/DrugCalc/DataEntry').default },
            { type: 'item', text: 'Titration<br>Table', component: require('@/dialogs/DrugCalc/TitrationTable').default },
            { type: 'item', text: 'Drug<br>Registration', component: require('@/dialogs/DrugCalc/DrugRegistration').default }
          ]
        }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
