export default {
  namespaced: true,

  state () {
    return {
      timeInterval: 3600,
      hasResult: false
    }
  },

  mutations: {
    SET_TIME_INTERVAL (state, interval) {
      state.timeInterval = interval
    },

    SET_HAS_RESULT (state, val) {
      state.hasResult = val
    }
  }
}
