<template>
<div class="ppt">
  <div v-if="loading" class="loading">LOADING...</div>
  <div v-else class="ppt-container">
    <img :src="currentImageSrc" @click="next">

    <ecg-button class="skip" width="160px" height="80px" @click="next">SKIP</ecg-button>
  </div>
</div>
</template>

<script>
import { range as _range, get as _get } from 'lodash'
const PPT_IMAGES = require.context('@/images/ppt', false, /\.jpg$/)

export default {
  name: 'ppt',

  data () {
    return {
      loading: false,
      images: [],
      currentIdx: 0
    }
  },

  created () {
    this.preload()
  },

  computed: {
    currentImageSrc () {
      return _get(this.images, [this.currentIdx, 'src'], null)
    }
  },

  methods: {
    async preload () {
      this.loading = true
      const loadList = _range(1, 3).map(n => this.loadImage(n))

      await Promise.all(loadList)
      this.loading = false

      this.images.sort((a, b) => a.n - b.n)
    },

    loadImage (n) {
      return new Promise((resolve, reject) => {
        const image = new Image()
        const src = PPT_IMAGES(`./${n}.jpg`)

        image.onload = () => {
          this.images.push({ n, src })
          resolve()
        }

        image.onerror = () => {
          // eslint-disable-next-line
          console.error(`[PPT ERROR] ${n}`)
          resolve()
        }

        image.src = src
      })
    },

    next () {
      if (this.currentIdx >= this.images.length - 1) {
        this.$parent.pptVisible = false
      } else {
        this.currentIdx += 1
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.ppt {
  height: 100%;

  .loading {
    height: 100%;
    font-size: 72px;
    display: flex; justify-content: center; align-items: center;
  }

  &-container {
    height: 100%;
    position: relative;
    img {
      display: block;
      width: 100%; height: 100%;
      object-fit: contain;
      -webkit-user-drag: none;
    }
    .skip {
      position: absolute; right: 50px; bottom: 50px;
      font-size: 24px;
    }
  }
}
</style>
