<template>
<div class="Patient-dialog">
  <ecg-tree-tab :tabTree="tabTree()" ref="treeTab"/>
</div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState('app', ['hiqviewMode'])
  },

  methods: {
    tabTree () {
      const transportDataSection = this.hiqviewMode
        ? {
          type: 'group',
          text: 'Transport<br>Data',
          children: [{ type: 'item', text: 'Remove', component: require('@/dialogs/Patient/AdmitDischarge/TransportDataRemove').default }]
        }
        : { type: 'item', text: 'Transport<br>Data', component: require('@/dialogs/Patient/AdmitDischarge/TransportData').default }

      return [
        {
          type: 'group',
          text: 'Admit<br>Discharge',
          children: [
            { type: 'item', text: 'Admit', component: require('@/dialogs/Patient/AdmitDischarge/Admit').default },
            { type: 'item', text: 'Discharge', component: require('@/dialogs/Patient/AdmitDischarge/Discharge').default },
            transportDataSection,
            { type: 'item', text: 'HiQ-View', component: require('@/dialogs/Patient/AdmitDischarge/HiQ-View').default }
          ]
        },
        {
          type: 'group',
          text: 'Limits<br>Alarm',
          children: [
            { type: 'item', text: 'Basic', component: require('@/dialogs/Patient/LimitsAlarm/Basic').default },
            { type: 'item', text: 'ECG', component: require('@/dialogs/Patient/LimitsAlarm/ECG').default },
            {
              type: 'group',
              text: 'Press',
              children: [
                { type: 'item', text: 'Page1', component: require('@/dialogs/Patient/LimitsAlarm/Press/Page1').default },
                { type: 'item', text: 'Page2', component: require('@/dialogs/Patient/LimitsAlarm/Press/Page2').default },
                { type: 'item', text: 'Page3', component: require('@/dialogs/Patient/LimitsAlarm/Press/Page3').default },
                { type: 'item', text: 'Page4', component: require('@/dialogs/Patient/LimitsAlarm/Press/Page4').default },
                { type: 'item', text: 'Page5', component: require('@/dialogs/Patient/LimitsAlarm/Press/Page5').default }
              ]
            },
            {
              type: 'group',
              text: 'Temp',
              children: [
                { type: 'item', text: 'Page1', component: require('@/dialogs/Patient/LimitsAlarm/Temp/Page1').default },
                { type: 'item', text: 'Page2', component: require('@/dialogs/Patient/LimitsAlarm/Temp/Page2').default }
              ]
            },
            {
              type: 'group',
              text: 'Gas',
              children: [
                { type: 'item', text: 'Page1', component: require('@/dialogs/Patient/LimitsAlarm/Gas/Page1').default },
                { type: 'item', text: 'Page2', component: require('@/dialogs/Patient/LimitsAlarm/Gas/Page2').default }
              ]
            },
            {
              type: 'group',
              text: 'Other<br>Parameter',
              children: [
                { type: 'item', text: 'Page1', component: require('@/dialogs/Patient/LimitsAlarm/OtherParameter/Page1').default }
              ]
            }
          ]
        },
        {
          type: 'group',
          text: 'Arrhyth<br>Alarm',
          children: [
            { type: 'item', text: 'Arrhyth<br>Alarm', component: require('@/dialogs/Patient/ArrhythAlarm/ArrhythAlarm').default }
          ]
        },
        {
          type: 'group',
          text: 'Master<br>Copy',
          children: [
            { type: 'item', text: 'Master<br>Copy', component: require('@/dialogs/Patient/MasterCopy/MasterCopy').default }
          ]
        }
      ]
    },

    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
