<template>
<ecg-embeded-dialog class="blood-dlg" ref="dlg" title="Blood" width="500px" height="300px">
  <div class="wrapper">
    <ecg-button
      v-for="(b, idx) of bloodList" :key="idx"
      width="100px" height="50px"
      :active="blood === b"
      @click="set(b)">{{ b }}</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      bloodList: ['A Rh+', 'B Rh+', 'AB Rh+', 'O Rh+', 'A Rh-', 'B Rh-', 'AB Rh-', 'O Rh-', 'Unknown']
    }
  },

  computed: {
    ...mapState('patient', ['blood'])
  },

  methods: {
    ...mapMutations('patient', ['SET_BLOOD']),

    open () {
      this.$refs.dlg.open()
    },

    set (blood) {
      this.SET_BLOOD(blood)
      this.$refs.dlg.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.blood-dlg {
  .wrapper {
    height: 100%;
    display: flex; flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between;
  }
}
</style>
