export default {
  namespaced: true,

  state () {
    return {
      hiqviewMode: false,
      blackScreen: false,
      thumbnail: false
    }
  },

  mutations: {
    SET_HIQVIEW_MODE (state, enabled) {
      state.hiqviewMode = enabled
    },
    SET_BLACK_SCREEN (state, status) {
      state.blackScreen = status
    },
    SET_THUMBNAIL (state) {
      state.thumbnail = !state.thumbnail
    }
  },

  actions: {
    flashBlackScreen (context) {
      context.commit('SET_BLACK_SCREEN', true)
      setTimeout(() => {
        context.commit('SET_BLACK_SCREEN', false)
      }, 1000)
    }
  }
}
