<template>
<div id="app" :style="pageStyle">

  <ppt v-if="pptVisible"/>
  <template v-else>
    <main-screen v-if="!blackScreen"/>
  </template>

  <dialog-manager/>
  <alarm-info/>
</div>
</template>

<script>
import { debounce as _debounce } from 'lodash'
import { PAGE_WIDTH, PAGE_HEIGHT } from '@/libs/constant'
import Ppt from './ppt'
import MainScreen from './main-screen'
import DialogManager from './dialog-manager'
import AlarmInfo from './alarm-info'
import { mapState } from 'vuex'
export default {
  components: { Ppt, MainScreen, DialogManager, AlarmInfo },

  data () {
    return {
      pptVisible: true,
      pageStyle: {
        width: `${PAGE_WIDTH}px`,
        height: `${PAGE_HEIGHT}px`,
        transform: '',
        left: 0,
        top: 0
      }
    }
  },

  computed: {
    ...mapState('app', ['blackScreen'])
  },

  created () {
    this.scalePage()

    this._scalePage = _debounce(this.scalePage, 500)
    window.addEventListener('resize', this._scalePage)
  },

  beforeDestroy () {
    window.removeEventListener('resize', this._scalePage)
  },

  methods: {
    scalePage () {
      const width = window.innerWidth
      const height = window.innerHeight

      if ((width / height) >= (PAGE_WIDTH / PAGE_HEIGHT)) {
        this.pageStyle.transform = `scale(${height / PAGE_HEIGHT}) translateX(-50%)`
        this.pageStyle.left = '50%'
        this.pageStyle.top = 0
      } else {
        this.pageStyle.transform = `scale(${width / PAGE_WIDTH}) translateY(-50%)`
        this.pageStyle.left = 0
        this.pageStyle.top = '50%'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
#app {
  position: absolute;
  transform-origin: left top;
}
</style>
