export default {
  namespaced: true,
  state () {
    return {
      transportdataSwitch: false,
      standbySwicth: false,
      timeSwitch: false,
      currentMain: 'main-1',
      bpmSwitch: false,
      sensitivitySwitch: false,
      leadSwitch: false
    }
  },
  mutations: {
    SET_TIME_SWITCH (state) {
      state.timeSwitch = !state.timeSwitch
    },
    SET_STANDBY_SWITCH (state, status) {
      state.standbySwicth = status
    },
    SET_CURRENT_MAIN (state, main) {
      state.currentMain = main
    },
    SET_TRANSPORTDATA_SWITCH (state, status) {
      state.transportdataSwitch = status
    },
    SET_BPMSWITCH (state, status) {
      state.bpmSwitch = status
    },
    SET_SENSITIVITYSWITCH (state, status) {
      state.sensitivitySwitch = status
    },
    SET_LEADSWITCH (state, status) {
      state.leadSwitch = status
    }
  }
}
