  methods: {
    genData () {
      const data1 = _map(new Array(19 * 4), (item, idx) => {
        switch (idx % 4) {
          case 0:
            return _random(23, 27)
          case 1:
            return _random(10, 15)
          case 2:
            return 0
          case 3:
            return _random(68, 72)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FF4848',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
