import EventEmiter from 'events'

class Alarm extends EventEmiter {
  openRed () { this.emit('open:red') }
  openYellow () { this.emit('open:yellow') }
  openBlue () { this.emit('open:blue') }

  closeRed () { this.emit('close:red') }
  closeYellow () { this.emit('close:yellow') }
  closeBlue () { this.emit('close:blue') }

  silence (msg, icon) { this.emit('silence', msg, icon) }
}

export default new Alarm()
