<template>
<div class="ecg-line-box" :style="{width: width, height: height}">
  <div class="xScale" :style="{width: width}">
    <div
      class="xScale-item"
      v-for="(item, idx) of xScale"
      :key="idx + 'xScale'">
      {{ item }}
    </div>
  </div>
  <div class="yScale" :style="{height: height}">
    <div
      class="yScale-item"
      v-for="(item, idx) of yScale"
      :key="idx + 'yScale'">
      {{ item.toFixed(2) }}
    </div>
  </div>
  <div class="lineScale" :style="{width: width}">
    <div class="lineScale-item"
      v-for="(item, idx) of line"
      :key="idx + 'lineScale'"
      :style="{
        left: calcLeft(item.position)
      }">
      {{ item.position }}
    </div>
  </div>
  <div
    class="chart"
    :style="{
      width: width,
      height: height
    }">
    <div
      class="square"
      v-for="(item, idx) of formatSquare"
      :key="idx + 'square'"
      :style="{
        width: item.width,
        height: item.height,
        left: item.left,
        top: item.top,
        border: item.border
      }"></div>
    <div
      class="line" v-for="(item, idx) of formatLine"
      :key="idx + 'line'"
      :style="{
        width: item.width,
        height: item.height,
        left: item.left,
        top: item.top,
        background: item.lineColor
      }"></div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '400px'
    },
    height: {
      type: String,
      default: '200px'
    },
    xScale: {
      type: Array,
      default: function () {
        return []
      }
    },
    yScale: {
      type: Array,
      default: function () {
        return []
      }
    },
    square: {
      type: Array,
      default: function () {
        return []
      }
    },
    line: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  /* data () {
    return {
      width: '400px',
      height: '200px',
      xScale: [0, 18, 36],
      yScale: [0.00, 2.20, 4.40],
      square: [
        {
          borderColor: '#EF8787',
          position: [0, 4.40],
          size: [18, 2.20]
        }
      ],
      line: [
        {
          lineColor: '#FFFFFF',
          size: 4.40,
          direction: 'vertical',// vertical/horizontal 垂直/水平
          position: 18
        }
      ]
    }
  }, */
  computed: {
    formatLine () {
      const xLength = this.xScale[this.xScale.length - 1] - this.xScale[0] // -26
      const yLength = this.yScale[this.xScale.length - 1] - this.yScale[0] // 4.40
      return this.line.map((item, idx) => {
        if (item.direction === 'vertical') {
          return {
            width: '1px',
            height: (item.size * 100 / yLength) + '%',
            top: '0px',
            left: xLength < 0 ? (-xLength - item.position) * 100 / (-xLength) + '%' : (item.position * 100 / xLength) + '%',
            lineColor: item.lineColor
          }
        } else {
          return {
            width: xLength < 0 ? (item.size * 100 / -xLength) + '%' : (item.size * 100 / xLength) + '%',
            height: '1px',
            top: (yLength - item.position) * 100 / yLength + '%',
            left: '0px',
            lineColor: item.lineColor
          }
        }
      })
    },
    formatSquare () {
      const xLength = this.xScale[this.xScale.length - 1] - this.xScale[0] // -26
      const yLength = this.yScale[this.xScale.length - 1] - this.yScale[0] // 4.40
      return this.square.map((item, idx) => {
        return {
          width: xLength < 0 ? item.size[0] * 100 / -xLength + '%' : item.size[0] * 100 / xLength + '%',
          height: item.size[1] * 100 / yLength + '%',
          left: xLength < 0 ? (-xLength - item.position[0]) * 100 / (-xLength) + '%' : (item.position[0] * 100 / xLength) + '%',
          top: (yLength - item.position[1]) * 100 / yLength + '%',
          border: `1px solid ${item.borderColor}`
        }
      })
    }
  },
  methods: {
    calcLeft (x) {
      const xLength = this.xScale[this.xScale.length - 1] - this.xScale[0] // -26
      return xLength < 0 ? (-xLength - x) * 100 / (-xLength) + '%' : (x * 100 / xLength) + '%'
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-line-box{
  position: relative;
  padding-left: 40px;
  padding-bottom: 24px;
  .xScale{
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .yScale{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: flex-end;
  }
  .lineScale{
    position: absolute;
    right: 0;
    bottom: 0;
    &-item{
      position: absolute;
      bottom: 0;
      transform: translateX(-50%);
    }
  }
  .chart{
    background-color: #000;
    position: relative;
    &::before{
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 1px;
      background-color: #fff;
      top: 50%;
      left: 0px;
      transform: translateY(-50%);
    }
    &:after{
      content: '';
      position: absolute;
      display: block;
      width: 1px;
      height: 100%;
      background-color: #fff;
      top: 0px;
      left: 50%;
      transform: translateX(-50%);
    }
    .square{
      position: absolute;
      box-sizing: border-box;
      z-index: 100;
    }
    .line{
      position: absolute;
      box-sizing: border-box;
      z-index: 10;
    }
  }
}
</style>
