    <div class="pannel">
      <div class="pannel-info d-flex flex-center">
        &lt;CAUTION&gt;<br>
        Screen will be dimmed.
      </div>
      <div class="pannel-btns d-flex">
        <ecg-button width="100px" height="50px" @click="handleStandby">Yes</ecg-button>
        <ecg-button width="100px" height="50px" @click="$dialog.close()">No</ecg-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  methods: {
    ...mapMutations('home', ['SET_STANDBY_SWITCH']),
    handleStandby () {
      this.$dialog.close()
      this.SET_STANDBY_SWITCH(true)
    }
  }
}
</script>

