<template>
  <div class="part4 d-flex">
    <div class="info"  @click="$dialog.open('BasicParameter', [3, 1])">
      <template v-if="!hiqviewMode">
        <div class="info-1 c-white">CO<sub>2</sub>-E</div>
        <div class="info-2 c-darken-white">[mmHg]</div>
        <div class="info-3 c-white">RR</div>
        <div class="info-4 c-white">{{ hiqviewMode ? '---' : '20' }}</div>
        <div class="info-5 c-darken-white">Off<br>Off</div>
        <div class="info-6 c-white">{{ hiqviewMode ? '---' : '42' }}</div>
        <div class="info-7 c-darken-white">Off<br>Off</div>
        <div class="drag-info" v-if="drag">CO<sub>2</sub>-E</div>
      </template>
    </div>
    <div class="chart" @click="handleThumbnail">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#FFFFFF', width: 1, value: 0 },
          { color: '#FFFFFF', width: 1, value: 94 },
          { color: '#FFFFFF', width: 1, value: -94 },
        ]"/>
      <div class="chart-info">
        <div class="chart-info-1">CO<sub>2</sub></div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <ecg-heart-beat-static
          width="100%"
          height="100%"
          :dataFunc="thumbnailGenData1"/>
        <div class="thumbnail-line1"></div>
        <div class="thumbnail-line2"></div>
        <div class="thumbnail-line3"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { random as _random, map as _map } from 'lodash'
export default {
  data () {
    return {
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['hiqviewMode', 'thumbnail'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    genData () {
      const data1 = _map(new Array(9 * 12), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return -98
          case 1:
            return -98
          case 2:
            return -98
          case 3:
            return -98
          case 4:
            return -98
          case 5:
            return -98
          case 6:
            return -98
          case 7:
            return _random(85, 90)
          case 8:
            return _random(90, 95)
          case 9:
            return 100
          case 10:
            return _random(90, 95)
          case 11:
            return _random(85, 90)
          default:
            return 0
        }
      })
      const data2 = []
      return [
        {
          color: '#FFFFFF',
          width: 2,
          data: this.hiqviewMode ? data2 : data1,
          area: -100
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    },
    thumbnailGenData1 () {
      return [
        {
          color: '#FFFFFF',
          width: 2,
          data: [100],
          area: -100
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part4{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    border: 1px solid #808080;
    border-top: none;
    box-sizing: border-box;
    >div{
      position: absolute;
      text-align: right;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FFFFFF;
      background-color: #333;
      color: #FFFFFF;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; right: calc(50% + 20px); top: 30px; }
    &-2{ font-size: 14px; right: 5px; top: 30px; }
    &-3{ font-size: 16px; left: 5px; bottom: calc(50% + 10px); }
    &-4{ font-size: 48px; left: 40px; bottom: 40px; }
    &-5{ font-size: 14px; left: 110px; bottom: 60px; }
    &-6{ font-size: 140px; right: 40px; bottom: 20px; }
    &-7{ font-size: 14px; right: 5px; top: 60px }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
    }
    .thumbnail{
      width: calc(100% / 6);
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      border-top: 2px solid #ffffff;
      border-right: 2px solid #ffffff;
      border-bottom: 2px solid #ffffff;
      box-sizing: border-box;
      &-line1{ position: absolute; height: 100%; border-left: 2px solid #ffffff; left: 45%; top: 0px;}
      &-line2{ position: absolute; height: 100%; border-left: 2px dashed #ffffff; left: calc(45% / 2); top: 0px;}
      &-line3{ position: absolute; height: 100%; border-left: 2px dashed #ffffff; left: calc(45% + (55% / 2)); top: 0px;}
    }
  }
}
</style>
