<template>
  <vue-slider
    class="ecg-slider"
    :value="value"
    direction="btt"
    :marks="formatMarks"
    tooltip="none"
    :enable-cross="false"
    :dotOptions="dotOptions"
    :process="dotsPos => [[0, 100, { backgroundColor: '#999' }]]"
    :style="{ height, width: '8px' }"
    @change="handleChange">
    <template v-slot:dot>
      <img class="index" :src="indexImg[dotType]">
    </template>
  </vue-slider>
</template>

<script>
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
import index1 from '@/images/index1.png'
import index2 from '@/images/index2.png'
import index3 from '@/images/index3.png'
export default {
  components: { VueSlider },
  props: {
    value: {
      type: Number,
      default: 0
    },
    height: {
      type: String,
      default: '300px'
    },
    dotOptions: {
      type: Object,
      default: function () {
        return { min: 0, max: 100 }
      }
    },
    marks: {
      type: Object,
      default: function () {
        return {}
      }
    },
    dotType: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      indexImg: [index1, index2, index3]
    }
  },
  computed: {
    formatMarks () {
      return Object.keys(this.marks).reduce((result, key) => {
        result[key] = { label: this.marks[key], labelStyle: { color: '#000' } }
        return result
      }, {})
    }
  },
  methods: {
    handleChange (value) {
      this.$emit('update:value', value)
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-slider{
  display: block;
  /deep/{
    .index{
      position: absolute;
      left: 50%;
      transform: translateX(-70%);
      top: -5px;
    }
  }
}
</style>
