<template>
  <div class="main-2">
    <draggable @choose="handleChoose" @unchoose="handleUnchoose" @change="handleChange" :list="draggablePart" class="part-wrapper" :delay="1000">
      <component v-for="c of draggablePart" :key="c[0]" :is="c[1]" ref="part"></component>
    </draggable>
    <part4/>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import part4 from './main-2/part4'
export default {
  components: { draggable, part4 },
  data () {
    return {
      draggablePart: [
        [1, require('./main-2/part1').default],
        [2, require('./main-2/part2').default],
        [3, require('./main-2/part3').default]
      ]
    }
  },
  methods: {
    handleChoose (e) {
      this.$refs.part[e.oldIndex].drag = true
    },
    handleUnchoose (e) {
      this.$refs.part[e.oldIndex].drag = false
    },
    handleChange (e) {
      const moveEl = this.$refs.part.splice(e.moved.oldIndex, 1)[0]
      this.$refs.part.splice(e.moved.newIndex, 0, moveEl)
    }
  }
}
</script>

<style lang="scss" scoped>
.main-2{
  height: 100%;
  .part-wrapper{
    height: calc(100% * 11 / 17);
    .part1{ height: calc(100% * 3 / 11) }
    .part2{ height: calc(100% * 5 / 11) }
    .part3{ height: calc(100% * 3 / 11) }
  }
  .part4{
    height: calc(100% * 6 / 17);
  }
}
</style>
