<template>
<div class="Test">
  <ecg-box class="content-box">
    <div class="t-1 mb-20">Current Alarm Settings</div>
    <div class="px-20">
      <div style="margin-bottom: 80px">
        The following limits are currently set and will be used for this test.<br>
        Use the Alarm Limits screen to change limits if needed.
      </div>
      <ecg-row>
        <ecg-col :span="3">Low HR Limit</ecg-col>
        <ecg-col :span="9">100 [bpm]</ecg-col>
      </ecg-row>
      <ecg-row class="my-10">
        <ecg-col :span="3">Low SpO<sub>2</sub> Limit</ecg-col>
        <ecg-col :span="9">85</ecg-col>
      </ecg-row>
      <ecg-row>
        <ecg-col :span="3">Apnea Time</ecg-col>
        <ecg-col :span="9">20 [s]</ecg-col>
      </ecg-row>
      <div class="duration mb-40">
        <div class="mr-40">Test Duration</div>
        <div class="time-box mr-20">
          <div class="box">{{ hours }}</div>
          <div class="mr-10">
            <ecg-button width="40px" height="40px" @click="incHours"><div class="triangle-up"></div></ecg-button>
            <ecg-button width="40px" height="40px" @click="decHours"><div class="triangle-down"></div></ecg-button>
          </div>
          <div>[hour]</div>
        </div>
        <div class="time-box">
          <div class="box">{{ minutes }}</div>
          <div class="mr-10">
            <ecg-button width="40px" height="40px" @click="incMinutes"><div class="triangle-up"></div></ecg-button>
            <ecg-button width="40px" height="40px" @click="decMinutes"><div class="triangle-down"></div></ecg-button>
          </div>
          <div>[min]</div>
        </div>
      </div>
      <div class="d-flex">
        <ecg-button class="ml-auto" @click="start">Start</ecg-button>
      </div>
    </div>
  </ecg-box>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      hours: 1,
      minutes: 0
    }
  },

  mounted () {
    this.hours = Math.floor(this.timeInterval / 3600)
    this.minutes = Math.floor((this.timeInterval - this.hours * 3600) / 60)
  },

  computed: {
    ...mapState('carseat', ['timeInterval'])
  },

  methods: {
    ...mapMutations('carseat', ['SET_TIME_INTERVAL']),

    incHours () {
      this.hours += 1
    },

    decHours () {
      if (this.hours >= 1) { this.hours -= 1 }
    },

    incMinutes () {
      if (this.minutes < 59) { this.minutes += 1 }
    },

    decMinutes () {
      if (this.minutes >= 1) { this.minutes -= 1 }
    },

    start () {
      this.SET_TIME_INTERVAL(this.hours * 3600 + this.minutes * 60)
      this.$dialog.open('CarSeatChallengeTimeCutDown')
    }
  }
}
</script>

