import {
  // random as _random,
  map as _map
} from 'lodash'
export default {
  methods: {
    genData () {
      // 48-60-72-84-96
      const data1 = _map(new Array(12 * 5.5), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return 52
          case 1:
            return 51
          case 2:
            return 50
          case 3:
            return 50
          case 4:
            return 50
          case 5:
            return 50
          case 6:
            return 70
          case 7:
            return 71
          case 8:
            return 72
          case 9:
            return 72
          case 10:
            return 72
          case 11:
            return 72
        }
      })
      // 0-12-24-36-48
      const data2 = _map(new Array(12 * 5.5), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return 12
          case 1:
            return 18
          case 2:
            return 24
          case 3:
            return 24.5
          case 4:
            return 24.5
          case 5:
            return 25
          case 6:
            return 42
          case 7:
            return 33
          case 8:
            return 24.5
          case 9:
            return 24.5
          case 10:
            return 24
          case 11:
            return 2
        }
      })
      // -48 - -36 - -24 - -12 - 0
      const data3 = _map(new Array(10 * 5.5), (item, idx) => {
        switch (idx % 10) {
          case 0:
            return -36
          case 1:
            return -47
          case 2:
            return -47.5
          case 3:
            return -47.5
          case 4:
            return -48
          case 5:
            return -24
          case 6:
            return -5
          case 7:
            return -5.5
          case 8:
            return -5.5
          case 9:
            return -6
        }
      })
      return [
        {
          color: '#FFF',
          width: 2,
          data: data1
        },
        {
          color: '#FFF',
          width: 2,
          data: data2
        },
        {
          color: '#FFF',
          width: 2,
          data: data3
        }
      ]
    }
  }
}
</script>

