<template>
  <div class="part1 d-flex">
    <div class="info" @click="$dialog.open('BasicParameter', [0, 0])">
      <div class="info-1 c-green">HR-Ⅱ</div>
      <div class="info-2 c-green">80</div>
      <div class="info-3 c-darken-green">140<br>50</div>
      <div class="info-4 c-atrovirens flex-center">ST-Ⅱ<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-5 c-atrovirens">0.05<span class="small c-darken-atrovirens">[mV]</span></div>
      <div class="info-6 c-atrovirens flex-center">ST-V1<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-7 c-atrovirens">0.08<span class="small c-darken-atrovirens">[mV]</span></div>
      <div class="info-8 c-atrovirens flex-center">VPC<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-9 c-atrovirens">0</div>
      <div class="info-10" v-if="hiqviewMode"><img src="./images/wifi.jpg"></div>
      <div class="drag-info" v-if="drag">ECG</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"/>
      <div class="chart-info c-white">
        <div class="chart-info-1">Adult</div>
        <div class="chart-info-2">FiltMon</div>
        <div class="chart-info-3">Ⅱ</div>
        <div class="chart-info-4">x1<br>Auto</div>
        <div class="chart-info-5">1mV</div>
        <div class="chart-info-6"></div>
        <div class="chart-info-7">V1</div>
        <div class="chart-info-8">x1<br>Auto</div>
        <div class="chart-info-9">1mV</div>
        <div class="chart-info-10"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { random as _random, map as _map } from 'lodash'
export default {
  data () {
    return {
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['hiqviewMode'])
  },
  methods: {
    genData () {
      const data1 = _map(new Array(20 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(88, 90)
        } else {
          return _random(45, 50)
        }
      })
      const data2 = _map(new Array(20 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(-90, -88)
        } else {
          return _random(-50, -45)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data1
        },
        {
          color: '#00FF00',
          width: 2,
          data: data2
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part1{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    border: 1px solid #808080;
    box-sizing: border-box;
    >div{
      position: absolute;
      text-align: right;
      display: flex;
      align-items: flex-start;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #00FF00;
      background-color: #333;
      color: #00FF00;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: 5px; top: 5px; }
    &-2{ font-size: 100px; right: 40px; top: 0px; }
    &-3{ font-size: 14px; right: 5px; top: 20px; }
    &-4{ font-size: 16px; left: 5px; top: 100px; }
    &-5{ font-size: 24px; left: 165px; top: 100px;}
    &-6{ font-size: 16px; left: 260px; top: 100px; }
    &-7{ font-size: 24px; right: 5px; top: 100px; }
    &-8{ font-size: 16px; left: 260px; top: 130px; }
    &-9{ font-size: 24px; right: 45px; top: 130px; }
    &-10{ left: 55px; top: 0px; >img{ width: 30px; }}
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 340px; top: 20%; transform: translateY(-50%); }
      &-2{ right: 220px; top: 20%; transform: translateY(-50%); }
      &-3{ right: 170px; top: 20%; transform: translateY(-50%); }
      &-4{ right: 90px; top: 24px; }
      &-5{ right: 20px; top: 20%; transform: translateY(-50%); }
      &-6{
        right: 10px; top: 25%; transform: translateY(-50%);
        height: 25%;
        border-left: 1px dashed #fff;
        position: relative;
        &::before{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
        &::after{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          bottom: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      &-7{ right: 170px; top: 70%; transform: translateY(-50%); }
      &-8{ right: 90px; top: calc(50% + 24px); }
      &-9{ right: 20px; top: 70%; transform: translateY(-50%); }
      &-10{
        right: 10px; top: 75%; transform: translateY(-50%);
        height: 25%;
        border-left: 1px dashed #fff;
        position: relative;
        &::before{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
        &::after{
          content: '';
          display: block;
          width: 10px;
          height: 1px;
          background-color: #fff;
          position: absolute;
          bottom: 0px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
}
</style>
