<template>
  <div class="interbed" v-if="show">
    <div class="interbed-header">
      <div class="title">Individual Bed</div>
      <div class="btn" @click="show = false"></div>
    </div>
    <div class="interbed-container">
      <div class="interbed-container-1">
        <div class="interbed-container-1-info1 c-white">TELE-01</div>
        <div class="interbed-container-1-info2 c-black bg-white d-flex flex-center">No Patient Name</div>
        <div class="interbed-container-1-info3 c-green">HR</div>
        <div class="interbed-container-1-info4 c-green">80</div>
        <div class="interbed-container-1-info5 c-atrovirens">VPC</div>
        <div class="interbed-container-1-info6 c-atrovirens">ST-Ⅱ</div>
        <div class="interbed-container-1-info7 c-atrovirens">0</div>
        <div class="interbed-container-1-info8 c-atrovirens">0.06</div>
        <div class="interbed-container-1-chart1">
          <ecg-heart-beat
            width="500px"
            height="100px"
            :dataFunc="genData"/>
          <div class="interbed-container-1-chart1-info1">Ⅱ</div>
          <div class="interbed-container-1-chart1-info2">x1</div>
          <div class="interbed-container-1-chart1-info3">1mV</div>
          <div class="interbed-container-1-chart1-info4"></div>
        </div>
      </div>
      <div class="interbed-container-2">
        <div class="interbed-container-2-info1 c-blue">SpO<sub>2</sub></div>
        <div class="interbed-container-2-info2 c-white">RR-lmp</div>
        <div class="interbed-container-2-info3 c-blue">92</div>
        <div class="interbed-container-2-info4 c-white">12</div>

      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'

export default {
  data () {
    return {
      show: false
    }
  },
  methods: {
    genData () {
      const data1 = _map(new Array(7 * 15), (item, idx) => {
        if (idx % 15 === 0) {
          return _random(88, 90)
        } else {
          return _random(0, 25)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
  .interbed{
    position: absolute;
    width: 100%;
    height: 700px;
    left: 0;
    top: 0;
    background: #C0C0CE;
    &-header{
      height: 40px;
      background:linear-gradient(to bottom, #696977, #5c5c63);
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      margin-bottom: 5px;
      .title{
        font-size: 16px;
      }
      .btn{
        box-sizing: border-box;
        position: absolute;
        width: 50px;
        height: 38px;
        background: linear-gradient(to bottom, #696977, #5c5c63);
        border: 1px outset #ccc;
        right: 2px;
        top: 50%;
        transform: translateY(-50%);
        &::before{
          position: absolute;
          left: 50%;
          top: 50%;
          display: block;
          content: '';
          width: 5px;
          height: 40px;
          background: #fff;
          transform-origin:50% 50%;
          transform: translateX(-50%) translateY(-45%) rotateZ(45deg);
        }
        &::after{
          position: absolute;
          left: 50%;
          top: 50%;
          display: block;
          content: '';
          width: 5px;
          height: 40px;
          background: #fff;
          transform-origin:center center;
          transform: translateX(-50%) translateY(-50%) rotateZ(-45deg);
        }
      }
    }
    &-container{
      height: calc(100% - 45px);
      &-1{
        height: calc((100% - 5px) / 2);
        background: #000;
        margin-bottom: 5px;
        position: relative;
        >div{
          position: absolute;
        }
        &-info1{ font-size: 24px; left: 10px; top: 30px;}
        &-info2{ font-size: 24px; right: 40px; top: 10px; height: 40px;}
        &-info3{ font-size: 16px; left: 10px; top: 60px;}
        &-info4{ font-size: 40px; left: 60px; top: 80px;}
        &-info5{ font-size: 16px; left: 10px; top: 130px;}
        &-info6{ font-size: 16px; left: 90px; top: 130px;}
        &-info7{ font-size: 24px; left: 40px; top: 150px;}
        &-info8{ font-size: 24px; left: 100px; top: 150px;}
        &-chart1{
          right: 0px;
          top: 70px;
          >div{
            position: absolute;
          }
          &-info1{ font-size: 14px; right: 150px; top: 20px; }
          &-info2{ font-size: 14px; right: 80px; top: 20px; }
          &-info3{ font-size: 14px; right: 20px; top: 20px; }
          &-info4{
            right: 10px; top: 50%; transform: translateY(-50%);
            height: 50%;
            border-left: 1px dashed #fff;
            position: absolute;
            &::before{
              content: '';
              display: block;
              width: 10px;
              height: 1px;
              background-color: #fff;
              position: absolute;
              top: 0px;
              left: 50%;
              transform: translateX(-50%);
            }
            &::after{
              content: '';
              display: block;
              width: 10px;
              height: 1px;
              background-color: #fff;
              position: absolute;
              bottom: 0px;
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
      }
      &-2{
        height: calc((100% - 5px) / 2);
        background: #000;
        position: relative;
        >div{
          position: absolute;
        }
        &-info1{ font-size: 16px; left: 5px; top: 5px;}
        &-info2{ font-size: 16px; left: 200px; top: 5px;}
        &-info3{ font-size: 36px; left: 80px; top: 30px;}
        &-info4{ font-size: 36px; left: 260px; top: 30px;}
      }
    }
  }
</style>
