<template>
  <vue-slider
    class="ecg-slider"
    :value="[minValue, maxValue]"
    direction="btt"
    :marks="formatMarks"
    tooltip="none"
    :enable-cross="false"
    :dotOptions="dotOptions"
    :process="dotsPos => [[dotsPos[0], dotsPos[1], { backgroundColor: '#999' }]]"
    :style="{ height, width: '8px' }"
    @change="handleChange">
    <template v-slot:dot="{ index }">
      <img :class="['index', `index${index}`]" :src="indexImg[index]">
    </template>
  </vue-slider>
</template>

<script>
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
import index1 from '@/images/index1.png'
import index2 from '@/images/index2.png'
export default {
  components: { VueSlider },
  props: {
    minValue: {
      type: Number,
      default: 0
    },
    maxValue: {
      type: Number,
      default: 100
    },
    height: {
      type: String,
      default: '300px'
    },
    dotOptions: {
      type: Object,
      default: function () {
        return { min: 0, max: 100 }
      }
    },
    marks: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      indexImg: [index1, index2]
    }
  },
  computed: {
    formatMarks () {
      return Object.keys(this.marks).reduce((result, key) => {
        result[key] = { label: this.marks[key], labelStyle: { color: '#000' } }
        return result
      }, {})
    }
  },
  methods: {
    handleChange (value) {
      this.$emit('update:minValue', value[0])
      this.$emit('update:maxValue', value[1])
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-slider{
  display: block;
  /deep/{
    .index{
      position: absolute;
      left: 50%;
      transform: translateX(-70%);
      &.index0{
        top: 7px;
      }
      &.index1{
        bottom: 7px;
      }
    }
  }
}
</style>
