<template>
<div class="BasicSettings">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box class="h-100">
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData"/>
          <span class="t-1 c-green">O<sub>2</sub></span>
          <span class="t-2 c-green">[%]</span>
        </div>

        <ecg-label>Cal</ecg-label>
        <ecg-label>-- - -- - ---- --:-- Air</ecg-label>
        <ecg-button-list :list="[['Air', '100% O<sub>2</sub>']]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="h-100">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['', '16:00', '18:00']" :y="[0, 25, 50, 75, 100]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10 c-green" content="" text1="O<sub>2</sub>-I" text2="[%]"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box title="O<sub>2</sub>-I" content="" text1="Off" text2="18" active/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { waveData } from '@/libs/helper'

export default {
  methods: {
    leftData () {
      return [
        {
          color: '#999',
          area: 40,
          data: waveData(30, 2, [45])
        }
      ]
    },

    rightData () {
      return [
        {
          color: '#fff',
          width: 2,
          data: [-50]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.BasicSettings {
  .left-chart {
    height: 330px;
    position: relative;
    span {
      position: absolute;
      font-weight: bold; font-size: 16px;
      &.t-1 { left: 10px; top: 10px; }
      &.t-2 { right: 10px; top: 10px; }
    }
  }
}
</style>
