<script>
const GRAPHIMAGES_PATH = require.context('../GraphImages/', false, /(\.png)$/)
const GRAPHIMAGES = {}
GRAPHIMAGES_PATH.keys().forEach(key => {
  const keyname = key.split('/').slice(1)[0].split('.')[0]
  GRAPHIMAGES[keyname] = GRAPHIMAGES_PATH(key)
})
export default {
  data () {
    return {
      iamges: GRAPHIMAGES
    }
  }
}
