<template>
<div class="BasicSettings">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box class="h-100">
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData" :lines="[
            { color: 'rgba(150, 150, 150, 0.3)', width: 1, value: 80 },
            { color: 'rgba(150, 150, 150, 0.3)', width: 1, value: 0 },
            { color: 'rgba(150, 150, 150, 0.3)', width: 1, value: -80 }
          ]"/>
          <span class="t-1 c-gray">CO<sub>2</sub></span>
          <span class="t-2">40</span>
          <span class="t-3">0</span>
        </div>

        <ecg-label>Wave</ecg-label>
        <ecg-button-list :list="[['CO<sub>2</sub>', 'O<sub>2</sub>', 'N<sub>2</sub>O']]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="h-100">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['', '16:00', '18:00']" :y="[0, 50, 100, 150]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10" content="38" text1="CO<sub>2</sub>-E" text2="[mmHg]"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <div class="d-flex">
          <ecg-alarm-box class="c-alarm-box f-white" title="CO<sub>2</sub>-E" content="38" text1="Off" text2="Off" active/>
          <ecg-alarm-box class="c-alarm-box f-green b-blue" title="O<sub>2</sub>-E" content="32" text1="Off" text2="Off"/>
          <ecg-alarm-box class="c-alarm-box f-purple b-blue" title="N<sub>2</sub>O-E" content="46" text1="Off" text2="Off"/>
        </div>
        <div class="d-flex">
          <ecg-alarm-box class="c-alarm-box" title="CO<sub>2</sub>-I" content="0" text1="Off" text2=""/>
          <ecg-alarm-box class="c-alarm-box f-green" title="O<sub>2</sub>-I" content="40" text1="Off" text2="18"/>
          <ecg-alarm-box class="c-alarm-box f-purple" title="N<sub>2</sub>O-I" content="58" text1="80" text2="Off"/>
        </div>
        <div class="d-flex">
          <ecg-alarm-box title="RR" content="12" text1="Off" text2="Off"/>
          <ecg-alarm-box title="Apnea" content="" text1="30" text2=""/>
        </div>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { waveData } from '@/libs/helper'

export default {
  methods: {
    leftData () {
      return [
        {
          color: '#fff',
          area: -80,
          data: waveData(3, 2, [60, 60, 65, 70, 70, 65, 60, 60])
        }
      ]
    },

    rightData () {
      return [
        {
          color: '#fff',
          width: 2,
          data: [-50]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.BasicSettings {
  .left-chart {
    height: 330px;
    position: relative;
    span {
      position: absolute;
      font-weight: bold; font-size: 16px;
      &.t-1 { left: 10px; top: 35px; }
      &.t-2 { left: 100px; top: 35px; }
      &.t-3 { left: 100px; bottom: 35px; }
    }
  }
  .c-alarm-box {
    &.f-white /deep/ .ecg-alarm-box-content { color: #fff; }
    &.f-green /deep/ .ecg-alarm-box-content { color: #0f0; }
    &.f-purple /deep/ .ecg-alarm-box-content { color: #A89AFF; }
    &.b-blue /deep/ .ecg-alarm-box-text .b { background: #00F8F8; }
  }
}
</style>
