<template>
  <div class="DataEntry">
    <div class="DataEntry-left">
      <div class="input" v-for="(item, idx) of inputs" :key="idx">
        <div v-if="item.type === 'label'" v-html="item.text" class="input-label"></div>
        <ecg-button width="160px" height="40px" v-else :active="item.id === currentInput" v-html="item.text" @click="changeInput(item.id)"></ecg-button>
        <div class="input-box">{{ item.value ? item.value : '---' }}{{ item.unit }}</div>
      </div>
    </div>
    <div class="DataEntry-right">
      <component :is="currentComponent"></component>
    </div>
  </div>
</template>

<script>
import { find as _find } from 'lodash'
export default {
  data () {
    return {
      currentInput: 'Amount',
      inputs: [
        { id: 'Drug', text: 'Drug', type: 'label', value: 'Amrinone' },
        { id: 'Amount', text: 'Amount', type: 'button', value: '500.00', component: require('./DataEntry/Amount').default, unit: '[mg]' },
        { id: 'BaseVolume', text: 'Base<br>Volume', type: 'button', value: '250', component: null, unit: '[mL]' },
        { id: 'Concentration', text: 'Concentration', type: 'label', value: '2.000' },
        { id: 'Dose', text: 'Dose', type: 'button', value: '5.00', component: null },
        { id: 'Dose Unit', text: 'Dose Unit', type: 'button', value: 'mcg/kg/min', component: require('./DataEntry/DoseUnit').default },
        { id: 'InfusionRate', text: 'Infusion<br>Rate', type: 'button', value: '', component: require('./DataEntry/InfusionRate').default, unit: '[mL /h]' },
        { id: 'Weight', text: 'Weight', type: 'button', value: '', component: require('./DataEntry/Weight').default, unit: '[kg]' }
      ]
    }
  },
  computed: {
    currentComponent () {
      return _find(this.inputs, o => o.id === this.currentInput).component
    }
  },
  methods: {
    changeInput (id) {
      this.currentInput = id
    }
  }
}
</script>

<style lang="scss" scoped>
.DataEntry{
  display: flex;
  align-items: center;
  padding: 60px 0 0 10px;
  &-left{
    width: 50%;
    .input{
      display: flex;
      align-items: center;
      font-weight: bold;
      margin-bottom: 16px;
      &-label{
        width: 160px;
        text-align: center;
      }
      &-box{
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #333;
        width: 240px;
        height: 40px;
        background: #fff;
        color: #000;
      }
    }
  }
  &-right{
    width: 50%;
  }
}
</style>
