<template>
<div class="BasicSettings">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box class="h-100">
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData"
            :lines="[{ color: '#999', width: 1, value: 80 }, { color: '#999', width: 1, value: 0 }, { color: '#999', width: 1, value: -90 }]"/>
          <span class="t-1 c-gray">CO<sub>2</sub></span>
          <span class="t-2">40</span>
          <span class="t-3">0</span>
        </div>

        <ecg-label>Zero Cal</ecg-label>
        <ecg-label>-- - -- - ---- --:-- Air</ecg-label>
        <ecg-button-list :list="[['Cal (Air)', 'N<sub>2</sub> Cal']]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="mb-10">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['16:00', '18:00']" :y="[0, 50, 100, 150]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10" content="36" text1="CO<sub>2</sub>-R" text2="[mmHg]"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box-list :list="[
          [
            { title: 'CO<sub>2</sub>-R', content: '36', text1: 'Off', text2: 'Off' },
            { title: 'RR', content: '12', text1: 'Off', text2: 'Off' },
            { title: 'Apnea', content: '', text1: '30', text2: '' }
          ],
          [
            { title: 'CO<sub>2</sub>-I', content: '', text1: 'Off', text2: '' }
          ]
        ]"/>
      </ecg-box>
      <ecg-box>
        <ecg-info-box-2 title="Inst CO<sub>2</sub>"/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { waveData } from '@/libs/helper'

export default {
  methods: {
    leftData () {
      return [
        {
          color: '#fff',
          area: -90,
          data: waveData(3, 2, [-85, -85, -85, -85, -85, 70, -85, -85])
        }
      ]
    },

    rightData () {
      return [
        {
          color: '#fff',
          width: 2,
          data: [-50]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.BasicSettings {
  .left-chart {
    background: #000;
    height: 330px;
    position: relative;
    span {
      font-weight: bold; font-size: 16px;
      position: absolute;
      &.t-1 { left: 10px; top: 35px; }
      &.t-2 { left: 100px; top: 35px; text-align: right; }
      &.t-3 { left: 100px; bottom: 20px; }
    }
  }
}
</style>
