import { padStart as _padStart } from 'lodash'

export default {
  namespaced: true,

  state () {
    return {
      id: '123',
      name: 'Nihon Kohden',
      birthDate: ['1993', '1', '7'],
      age: ['27', '8', '3'],
      type: 'Adult',
      height: '180',
      weight: '80',
      bsa: '20',
      gender: 'Unknown',
      blood: 'Unknown',
      pacemaker: true,
      notes: ''
    }
  },

  getters: {
    birthDateText (state) {
      if (state.birthDate.some(a => !a)) {
        return ''
      } else {
        return `${state.birthDate[0]}-${_padStart(state.birthDate[1], 2, 0)}-${_padStart(state.birthDate[2], 2, 0)}`
      }
    },

    pacemakerText (state) {
      return state.pacemaker ? 'On' : 'Off'
    }
  },

  mutations: {
    SET_ID (state, id) { state.id = id },
    SET_NAME (state, name) { state.name = name },
    SET_BIRTH_DATE (state, birthDate) { state.birthDate = birthDate },
    SET_AGE (state, age) { state.age = age },
    SET_TYPE (state, type) { state.type = type },
    SET_HEIGHT (state, height) { state.height = height },
    SET_WEIGHT (state, weight) { state.weight = weight },
    SET_BSA (state, bsa) { state.bsa = bsa },
    SET_GENDER (state, gender) { state.gender = gender },
    SET_BLOOD (state, blood) { state.blood = blood },
    SET_PACEMAKER (state, pacemaker) { state.pacemaker = pacemaker },
    SET_NOTES (state, notes) { state.notes = notes },

    RESET (state) {
      state.id = ''
      state.name = ''
      state.birthDate = ['', '', '']
      state.age = ['', '', '']
      state.type = 'Adult'
      state.height = ''
      state.weight = ''
      state.bsa = ''
      state.gender = 'Unknown'
      state.blood = 'Unknown'
      state.pacemaker = true
      state.notes = ''
    }
  }
}
