<template>
  <div class="ecg-slider-step d-flex">
    <ecg-button width="50px" height="50px" :disabled="!leftEnabled" @click="stepLeft">
      <div class="arrow top" :class="{ disabled: !leftEnabled }"></div>
    </ecg-button>
    <div class="wrapper">
      <vue-slider
        direction="btt"
        style="padding: 0px;"
        width="50px"
        height="100%"
        :contained="false"
        v-model="value"
        tooltip="none"
        :process="dotsPos => [[0, 100, { backgroundColor: '#111' }]]"
        ref="slider">
        <template v-slot:dot>
          <div class="custom-dot"></div>
        </template>
      </vue-slider>
    </div>
    <ecg-button width="50px" height="50px" :disabled="!rightEnabled" @click="stepRight">
      <div class="arrow bottom" :class="{ disabled: !rightEnabled }"></div>
    </ecg-button>
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
export default {
  components: { VueSlider },
  data () {
    return {
      value: 0,
      leftEnabled: true,
      rightEnabled: true
    }
  },
  methods: {
    stepLeft () {
      if (this.value > 0) {
        this.value -= 1
      }
    },
    stepRight () {
      if (this.value < 100) {
        this.value += 1
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-slider-step{
  width: 50px;
  height: 100%;
  flex-direction: column;
  .wrapper{
    flex: 1;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #666;
    box-sizing: border-box;
    background-color: #111;
    /deep/ .vue-slider{
      &-rail{
        border-radius: 0px;
        .vue-slider-process{
          border-radius: 0px;
        }
      }
      .custom-dot{
        height: 20px;
        width: 50px;
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        left: 50%;
        top: 50%;
        background: #ccc;
        border: 2px outset #666;
        box-sizing: border-box;
      }
    }
  }
  .arrow{
    width: 0; height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    &.top{
      border-bottom: 20px solid #fff;
      &.disabled { border-bottom-color: #95959D; }
    }
    &.bottom{
      border-top: 20px solid #fff;
      &.disabled { border-top-color: #95959D; }
    }
  }
}
</style>
