<template>
<div class="SoundDisplay">
  <ecg-row>
    <ecg-col :span="6">
      <ecg-box class="mb-10">
        <ecg-label>Stimulation Signal Sound</ecg-label>
        <ecg-button-list :list="[['On', 'Off']]"/>
      </ecg-box>
      <ecg-box>
        <ecg-label>TOF Response Graph on Measurement Window</ecg-label>
        <div class="d-flex">
          <ecg-button :active="displayMode === 'wave' || displayMode === 'wave-none'" @click="changeDisplayMode('wave')">EMG<br>Waveform</ecg-button>
          <ecg-button :active="displayMode === 'bar' || displayMode === 'bar-none'" @click="changeDisplayMode('bar')">Twitch Bar</ecg-button>
        </div>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="6">
      <ecg-box>
        <ecg-label style="font-size: 14px">Time after End of Stimulation until TOF Values are Dimmed</ecg-label>
        <ecg-button-list :list="[['2 min', 'Off']]" :defaultActive="defaultActive" @change="handleChange"/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('tof', ['displayMode', 'twoMin']),
    defaultActive () {
      return this.twoMin ? '0-0' : '0-1'
    }
  },

  methods: {
    ...mapMutations('tof', ['SET_DISPLAY_MODE', 'SET_TWOMIN', 'SET_TRANSPARENT']),

    changeDisplayMode (mode) {
      if (mode === 'bar') {
        if (this.displayMode === 'wave') {
          this.SET_DISPLAY_MODE('bar')
        } else if (this.displayMode === 'wave-none') {
          this.SET_DISPLAY_MODE('bar-none')
        }
      } else if (mode === 'wave') {
        if (this.displayMode === 'bar') {
          this.SET_DISPLAY_MODE('wave')
        } else if (this.displayMode === 'bar-none') {
          this.SET_DISPLAY_MODE('wave-none')
        }
      }
    },
    handleChange (active) {
      console.log(active)
      if (active === '0-0') {
        this.SET_TWOMIN(true)
      } else {
        this.SET_TWOMIN(false)
        this.SET_TRANSPARENT(false)
      }
    }
  }
}
</script>
