<script>
import {
  map as _map,
  random as _random
} from 'lodash'
export default {
  methods: {
    genData1 () {
      const data = _map(new Array(40 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(60, 70)
        } else {
          return _random(0, 10)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data
        }
      ]
    },
    genData2 () {
      const data = _map(new Array(40 * 10), (item, idx) => {
        if (idx % 10 === 5) {
          return _random(-70, -60)
        } else {
          return _random(-10, 0)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data
        }
      ]
    }
  }
}
</script>

