<template>
  <div class="Select d-flex">
    <div class="master">Master(*)</div>
    <div class="input mb-10">
      <ecg-button :active="currentInput === 1 && open" width="120px" height="40px" @click="open = true; currentInput = 1">Display 1</ecg-button>
      <div class="input-box">
        <div class="xing">*</div>
        <div class="value">{{ input1 }}</div>
      </div>
    </div>
    <div class="input mb-10">
      <ecg-button :active="currentInput === 2 && open" width="120px" height="40px" @click="open = true; currentInput = 2">Display 2</ecg-button>
      <div class="input-box">
        <div class="xing">*</div>
        <div class="value">{{ input2 }}</div>
      </div>
    </div>
    <div class="input mb-40">
      <ecg-button :active="currentInput === 3 && open" width="120px" height="40px" @click="open = true; currentInput = 3">Display 3</ecg-button>
      <div class="input-box">
        <div class="xing">*</div>
        <div class="value">{{ input3 }}</div>
      </div>
    </div>
    <div class="info mb-40">
      You can select the layout of the home screen
    </div>
    <div class="info">
      The home screen returns to the default layout in the following situation
    </div>
    <div class="info ml-5"> · After admit or discharge operation</div>
    <div class="info ml-5"> · 30 minutes after power off and "Show Admit Confirmation Window" is set to Off</div>
    <div class="dialog-wrapper" v-if="open" @click="open = false">
      <div class="dialog">
        <component :is="currentDisplay"></component>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      open: false,
      currentInput: 1,
      input1: 'L NONE',
      input2: 'L NONE',
      input3: 'L NONE'
    }
  },
  computed: {
    currentDisplay () {
      return require(`./Select/Display${this.currentInput}`).default
    }
  }
}
</script>

<style lang="scss" scoped>
.Select{
  height: 600px;
  flex-direction: column;
  justify-content: center;
  padding-left: 20px;
  color: #000;
  position: relative;
  .master{
    text-align: center;
    font-weight: bold;
    font-size: 16px;
  }
  .input{
    display: flex;
    &-box{
      width: 480px;
      height: 40px;
      border: 1px solid #808080;
      background: #fff;
      position: relative;
      font-weight: bold;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      .xing{
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .info{
    font-weight: bold;
    font-size: 16px;
  }
  .dialog-wrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    .dialog{
      position: absolute;
      width: 80%;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      background: #9E9EA9;
      border: 1px solid #333;
      padding: 40px 20px;
      box-sizing: border-box;
    }
  }
}
</style>
