<template>
<ecg-embeded-dialog class="birth-date-dlg" ref="dlg" title="Birth Date">
  <div class="wrapper">
    <div class="row-1">
      <div class="input-box" :class="{ active: activeInput === 1 }" @click="activeInput = 1">
        {{ input1 }}
        <div class="tip">[Y]</div>
      </div>
      <span class="divider"></span>
      <div class="input-box" :class="{ active: activeInput === 2 }" @click="activeInput = 2">
        {{ input2 }}
        <div class="tip">[M]</div>
      </div>
      <span class="divider"></span>
      <div class="input-box" :class="{ active: activeInput === 3 }" @click="activeInput = 3">
        {{ input3 }}
        <div class="tip">[D]</div>
      </div>
    </div>
    <div class="row-2">
      <div class="input-box" :class="{ active: activeInput === 4 }" @click="activeInput = 4">
        {{ input4 }}
        <div class="tip">[Yr]</div>
      </div>
      <div class="input-box" :class="{ active: activeInput === 5 }" @click="activeInput = 5">
        {{ input5 }}
        <div class="tip">[Mo]</div>
      </div>
      <div class="input-box" :class="{ active: activeInput === 6 }" @click="activeInput = 6">
        {{ input6 }}
        <div class="tip">[D]</div>
      </div>
      <div class="age">Age</div>
    </div>
    <ecg-box class="row-3">
      <ecg-number-keyboard @press="handlePress"/>
    </ecg-box>
    <ecg-button class="set" @click="set">Set</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { startsWith as _startsWith } from 'lodash'
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      activeInput: 1,
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      input5: '',
      input6: ''
    }
  },

  computed: {
    ...mapState('patient', ['birthDate', 'age'])
  },

  methods: {
    ...mapMutations('patient', ['SET_BIRTH_DATE', 'SET_AGE']),

    open () {
      this.$refs.dlg.open()
      this.activeInput = 1
      this.input1 = this.birthDate[0]
      this.input2 = this.birthDate[1]
      this.input3 = this.birthDate[2]
      this.input4 = this.age[0]
      this.input5 = this.age[1]
      this.input6 = this.age[2]
    },

    set () {
      this.SET_BIRTH_DATE([this.input1, this.input2, this.input3])
      this.SET_AGE([this.input4, this.input5, this.input6])
      this.$refs.dlg.close()
    },

    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this[`input${this.activeInput}`] = ''
        } else if (key === 'CTRL:BS') {
          this[`input${this.activeInput}`] = this[`input${this.activeInput}`].slice(0, this[`input${this.activeInput}`].length - 1)
        }
      } else {
        // 普通字符
        this[`input${this.activeInput}`] = this[`input${this.activeInput}`].concat(key)
      }
    }
  }
}
</script>

