<template>
  <div class="main-screen">
    <div class="header d-flex">
      <div class="header-left d-flex" @click="$dialog.open('Patient', [0, 0])">
        <div class="name">{{ name }}</div>
        <div class="info d-flex">
          <div class="info-bed">BED-01</div>
          <div class="info-age">{{ type }}</div>
        </div>
      </div>
      <div class="header-right d-flex">
        <div class="date">
          <span class="mr-40" style="color: #666;">MCDOC 133 [B] CO - 3570</span>
          <span>2021-4-19 Mon.</span>
        </div>
        <div class="time-wrapper">
          <div class="tof-message c-black" v-if="currentMain === 'main-6'">ECG Check Electrodes</div>
          <div class="time">15:10</div>
        </div>
      </div>
    </div>
    <div class="main">
      <component :is="currentMainComponent"></component>
      <div class="time-list" :class="layout === 'row' ? 'row' : 'vertical'" v-show="timeSwitch">
        <div class="time-item" v-for="(item, idx) of timeList" :key="idx" ref="hammer">
          <div class="time-item-label">Time{{ item.id }}</div>
          <div class="time-item-info">
            <img src="./images/icon_music-mute.png" alt="">
            <div class="button" @click="handleTime(item)">
              <div :class="item.play ? 'pause' : 'triangle-right'"></div>
            </div>
            <div class="time">{{ formatTime(item.time) }}</div>
          </div>
          <div class="layout" v-if="item.layout">
            <div class="layout-button" @click="handleLayoutClick('row', item)">Row<br>Layout</div>
            <div class="layout-button" @click="handleLayoutClick('vertical', item)">Vertical<br>Layout</div>
          </div>
          <div class="control" v-if="item.control">
            <div class="control-button" @click="handleReset(item)">Reset</div>
            <div class="control-button c-red"  @click="handleDelete(item, idx)">Delete</div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer d-flex">
      <div
        v-for="(item, idx) of buttonList"
        :key="idx"
        class="button d-flex flex-center"
        :class="[item.borderColor, item.bgColor, item.disabled ? 'disabled' : '']"
        v-html="item.text"
        @click="handleButtonClick(item.tab, idx)">
      </div>
    </div>
    <div class="standby" @click="handleStandby" v-if="standbySwicth">
      <div class="standby-info1">Standby</div>
      <div class="standby-info2">Touch the screen or press any key to resume monitoring.</div>
    </div>
    <div class="transportdata" v-if="transportdataSwitch">
      <div class="transportdata-pannel">
        <div class="transportdata-pannel-header d-flex flex-center">WLAN Transport</div>
        <div class="transportdata-pannel-container">
          <div class="transportdata-pannel-container-1">
            <div class="info1">G9</div>
            <div class="info2">G9</div>
            <div class="info3">
              <div class="info3-left">
                <div class="info3-left-1 c-green">HR</div>
                <div class="info3-left-2 c-green">60</div>
                <div class="info3-left-3 c-atrovirens">VPC</div>
                <div class="info3-left-4 c-atrovirens">ST-Ⅱ</div>
                <div class="info3-left-5 c-atrovirens">0</div>
                <div class="info3-left-6 c-atrovirens">0.00</div>
              </div>
              <div class="info3-right">
                <ecg-heart-beat
                  width="100%"
                  height="100%"
                  :dataFunc="genData1"/>
                <div class="info3-right-info">
                  <div class="info3-right-info-1">Ⅱ</div>
                  <div class="info3-right-info-2">x1</div>
                  <div class="info3-right-info-3">1mV</div>
                  <div class="info3-right-info-4 gong"></div>
                </div>
              </div>
            </div>
            <div class="info4">
              <div class="info4-left">
                <div class="info4-left-1 c-red">ART</div>
                <div class="info4-left-2 c-red">119 / 80</div>
                <div class="info4-left-3 c-red">( 94 )</div>
              </div>
              <div class="info4-right">
                <ecg-heart-beat
                  width="100%"
                  height="100%"
                  :dataFunc="genData2"
                  :lines="[
                    { color: '#8F2828', width: 1, value: 0 },
                    { color: '#8F2828', width: 1, value: 94 },
                    { color: '#8F2828', width: 1, value: -94 }
                  ]"/>
                <div class="info4-right-info c-red">
                  <div class="info4-right-info-1">ART</div>
                  <div class="info4-right-info-2">200</div>
                  <div class="info4-right-info-3">0</div>
                </div>
              </div>
            </div>
          </div>
          <div class="transportdata-pannel-container-2">
            <div class="info1 c-white">RR-lmp</div>
            <div class="info2 c-red">ART</div>
            <div class="info3 c-white">30</div>
            <div class="info4 c-red">119 / 80</div>
            <div class="info5 c-red">( 94 )</div>
            <div class="info6 c-yellow">PAP</div>
            <div class="info7 c-orange">T1</div>
            <div class="info8 c-yellow">50 / 30</div>
            <div class="info9 c-yellow">( 38 )</div>
            <div class="info10 c-orange">98.6</div>
            <div class="info11 c-purple">CVP</div>
            <div class="info12 c-purple">( 10 )</div>
            <div class="info13 d-flex flex-center" @click="handleTransportData">End</div>
            <div class="info14 d-flex flex-center">Silence<br>Alarm</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bpm-overlay" v-if="bpmSwitch" @click.self="SET_BPMSWITCH(false)">
      <div class="bpm">
        <div class="bpm-header">
          <div class="bpm-header-title">HR/PR</div>
          <div class="bpm-header-close" @click="SET_BPMSWITCH(false)"></div>
        </div>
        <div class="bpm-main">
          <div class="scale">
            <div class="scale-max">300</div>
            <div class="scale-min">0</div>
          </div>
          <ecg-slider-double
            :minValue="30"
            :maxValue="60"
            height="200px"/>
          <div class="ml-20">
            <ecg-counter border-color="#FFFF00" :value="bpmValue1" @input="handleBpmValue1"/>
            <ecg-button class="my-20" width="100px" height="60px">Auto</ecg-button>
            <ecg-counter border-color="#FF4848" :value="bpmValue2" @input="handleBpmValue2"/>
          </div>
        </div>
      </div>
    </div>
    <div class="sensitivity-overlay" v-if="sensitivitySwitch" @click.self="SET_SENSITIVITYSWITCH(false)">
      <div class="sensitivity">
        <div class="sensitivity-header">
          <div class="sensitivity-header-name">Trace 1<br>Ⅱ</div>
          <div class="sensitivity-header-title">Sensitivity</div>
          <div class="sensitivity-header-close" @click="SET_SENSITIVITYSWITCH(false)"></div>
        </div>
        <div class="sensitivity-main">
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x1/2'" @click="sensitivity = 'x1/2'">x1/2</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x1'" @click="sensitivity = 'x1'">x1</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x1/4'" @click="sensitivity = 'x1/4'">x1/4</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x2'" @click="sensitivity = 'x2'">x2</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x1/8'" @click="sensitivity = 'x1/8'">x1/8</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'x4'" @click="sensitivity = 'x4'">x4</ecg-button>
          <ecg-button width="100px" height="50px" :active="sensitivity === 'Auto'" @click="sensitivity = 'Auto'">Auto</ecg-button>
        </div>
      </div>
    </div>
    <div class="lead-overlay" v-if="leadSwitch" @click.self="SET_LEADSWITCH(false)">
      <div class="lead">
        <div class="lead-header">
          <div class="lead-header-name">Trace 1</div>
          <div class="lead-header-title">Lead</div>
          <div class="lead-header-close" @click="SET_LEADSWITCH(false)"></div>
        </div>
        <div class="lead-main">
          <ecg-button width="60px" height="50px" :active="lead === 'Ⅰ'" @click="lead = 'Ⅰ'">Ⅰ</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'Ⅱ'" @click="lead = 'Ⅱ'">Ⅱ</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'Ⅲ'" @click="lead = 'Ⅲ'">Ⅲ</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'aVR'" @click="lead = 'aVR'">aVR</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'aVL'" @click="lead = 'aVL'">aVL</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'aVF'" @click="lead = 'aVF'">aVF</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V1'" @click="lead = 'V1'">V1</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V2'" @click="lead = 'V2'">V2</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V3'" @click="lead = 'V3'">V3</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V4'" @click="lead = 'V4'">V4</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V5'" @click="lead = 'V5'">V5</ecg-button>
          <ecg-button width="60px" height="50px" :active="lead === 'V6'" @click="lead = 'V6'">V6</ecg-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Hammer from 'hammerjs'
import { mapState, mapMutations, mapActions } from 'vuex'
import { find as _find, random as _random, map as _map } from 'lodash'

export default {
  data () {
    return {
      bpmValue1: 200,
      bpmValue2: 100,
      lead: 'Ⅱ',
      sensitivity: 'x1',
      layout: 'row',
      timeList: [
        { id: '1', layout: false, control: false, time: 0, play: false, timer: null },
        { id: '2', layout: false, control: false, time: 0, play: false, timer: null },
        { id: '3', layout: false, control: false, time: 0, play: false, timer: null }
      ],
      MainList: [
        ['main-1', require('./main/main-1').default],
        ['main-2', require('./main/main-2').default],
        ['main-3', require('./main/main-3').default],
        ['main-4', require('./main/main-4').default],
        ['main-5', require('./main/main-5').default],
        ['main-6', require('./main/main-6').default]
      ],
      buttonList: [
        { text: 'Menu<br>Home', borderColor: 'bd-purple', bgColor: 'bg-grey', disabled: false, tab: 'MenuHome' },
        { text: 'Admit<br>Discharge', borderColor: 'bd-pink', bgColor: 'bg-pink', disabled: false, tab: 'AdmitDischarge' },
        { text: 'Vital Sign<br>List', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'VitalSignList' },
        { text: 'Silence<br>Alarm', borderColor: 'bd-orange', bgColor: 'bg-grey', disabled: false, tab: 'SilenceAlarm' },
        { text: 'Screen<br>Capture', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'ScreenCapture' },
        { text: 'Intvl<br>Man', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'IntvlMan' },
        { text: 'NIBP<br>Strt/Stp', borderColor: 'bd-blue', bgColor: 'bg-grey', disabled: false, tab: 'NIBPStrtStp' },
        { text: 'Interbed', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'Interbed' },
        { text: 'Zero All', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'ZeroAll' },
        { text: 'Print', borderColor: '', bgColor: '', disabled: true, tab: 'Print' },
        { text: 'Transport<br>Data', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'TransportData' },
        { text: 'L SIDE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LSIDE' },
        { text: 'L BOTTOM', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LBOTTOM' },
        { text: 'SWAN<br>INSERT', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'SWANINSERT' }
      ]
    }
  },
  mounted () {
    this.hammer = this.$refs.hammer.map((item, idx) => {
      return new Hammer(item)
    })
    for (let i = 0; i < this.hammer.length; i++) {
      this.hammer[i].on('swipe', (e) => {
        this.handlePanend(e, this.timeList[i], i)
      })
    }
  },
  beforeDestroy () {
    for (let i = 0; i < this.hammer.length; i++) {
      this.hammer[i].destroy()
    }
    for (let i = 0; i < this.timeList.length; i++) {
      clearInterval(this.timeList[i].timer)
      this.timeList[i].timer = null
    }
  },
  computed: {
    ...mapState('home', ['timeSwitch', 'standbySwicth', 'transportdataSwitch', 'currentMain', 'bpmSwitch', 'sensitivitySwitch', 'leadSwitch']),
    ...mapState('patient', ['name', 'type']),
    ...mapState('app', ['hiqviewMode']),
    currentMainComponent () {
      return _find(this.MainList, item => item[0] === this.currentMain)[1]
    }
  },
  methods: {
    ...mapMutations('home', ['SET_STANDBY_SWITCH', 'SET_CURRENT_MAIN', 'SET_TRANSPORTDATA_SWITCH', 'SET_BPMSWITCH', 'SET_SENSITIVITYSWITCH', 'SET_LEADSWITCH']),
    ...mapActions('tof', ['resetTOF']),
    changeButton (type) {
      switch (type) {
        case 1:
          this.$set(this.buttonList, 11, { text: 'L SIDE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LSIDE' })
          this.$set(this.buttonList, 12, { text: 'L BUTTON', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LBOTTOM' })
          break
        case 2:
          this.$set(this.buttonList, 11, { text: 'L NONE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LNONE' })
          this.$set(this.buttonList, 12, { text: 'L BUTTON', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LBOTTOM' })
          break
        case 3:
          this.$set(this.buttonList, 11, { text: 'L SIDE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LSIDE' })
          this.$set(this.buttonList, 12, { text: 'L NONE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LNONE' })
          break
        default:
          break
      }
    },
    handleButtonClick (tab, idx) {
      switch (tab) {
        case 'MenuHome':
          if (this.currentMain === 'main-6') {
            this.SET_CURRENT_MAIN('main-1')
            this.resetTOF()
          }
          this.$dialog.openState('MenuHome', [0])
          break
        case 'AdmitDischarge':
          this.$dialog.open('Patient', [0, 0])
          break
        case 'LNONE':
          this.SET_CURRENT_MAIN('main-1')
          if (idx === 12) {
            this.$set(this.buttonList, idx, { text: 'L BOTTOM', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LBOTTOM' })
          } else {
            this.$set(this.buttonList, idx, { text: 'L SIDE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LSIDE' })
          }
          break
        case 'LBOTTOM':
          this.SET_CURRENT_MAIN('main-2')
          this.$set(this.buttonList, idx, { text: 'L NONE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LNONE' })
          this.$set(this.buttonList, idx - 1, { text: 'L SIDE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LSIDE' })
          break
        case 'LSIDE':
          this.SET_CURRENT_MAIN('main-3')
          this.$set(this.buttonList, idx, { text: 'L NONE', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LNONE' })
          this.$set(this.buttonList, idx + 1, { text: 'L BOTTOM', borderColor: 'bd-grey', bgColor: 'bg-grey', disabled: false, tab: 'LBOTTOM' })
          break
        case 'SWANINSERT':
          this.SET_CURRENT_MAIN('main-4')
          break
        case 'SilenceAlarm':
          this.$alarm.silence('Silence Alarms')
          break
        case 'VitalSignList':
          this.$dialog.open('Review', [1, 0])
          break
        case 'Interbed':
          this.$dialog.open('Interbed', [0, 0])
          break
        case 'TransportData':
          this.$dialog.open('Patient', this.hiqviewMode ? [0, 2, 0] : [0, 2])
          break
        default:
          break
      }
    },
    handleStandby () {
      this.SET_STANDBY_SWITCH(false)
    },
    handleTransportData () {
      this.SET_TRANSPORTDATA_SWITCH(false)
    },
    handleLayoutClick (layout, item) {
      this.layout = layout
      item.layout = false
    },
    handlePanend (e, item) {
      if (e.direction === Hammer.DIRECTION_RIGHT) {
        item.layout = true
      } else if (e.direction === Hammer.DIRECTION_LEFT) {
        item.control = true
      }
    },
    formatTime (time) {
      let s = time % 60
      if (s < 10) { s = '0' + s }
      let m = Math.floor(time / 60) % 60
      if (m < 10) { m = '0' + m }
      let h = Math.floor(time / 3600) % 60
      if (h < 10) { h = '0' + h }
      return `${h} : ${m} : ${s}`
    },
    handleTime (item) {
      if (!item.play) {
        item.timer = setInterval(() => { item.time += 1 }, 1000)
      } else {
        clearInterval(item.timer)
      }
      item.play = !item.play
    },
    handleReset (item) {
      item.control = false
      item.time = 0
      clearInterval(item.timer)
    },
    handleDelete (item, idx) {
      this.$delete(this.timeList, idx)
    },
    genData1 () {
      const data = _map(new Array(7 * 25), (item, idx) => {
        if (idx % 25 === 7) {
          return _random(88, 90)
        } else {
          return _random(0, 10)
        }
      })
      return [
        {
          color: '#00FF00',
          width: 2,
          data: data
        }
      ]
    },
    genData2 () {
      const data = _map(new Array(7 * 7), (item, idx) => {
        switch (idx % 7) {
          case 0:
            return _random(-10, 0)
          case 1:
            return _random(-15, -10)
          case 2:
            return -15
          case 3:
            return 25
          case 4:
            return _random(10, 15)
          case 5:
            return _random(5, 10)
          case 6:
            return _random(0, 5)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FF4848',
          width: 2,
          data: data
        }
      ]
    },
    handleBpmValue1 (value) {
      this.bpmValue1 = value
    },

    handleBpmValue2 (value) {
      this.bpmValue2 = value
    }
  }
}
</script>

<style lang="scss" scoped>
.bd-purple { border: 5px outset #7798E3; }
.bd-pink { border: 5px outset #AD7B85; }
.bd-grey { border: 5px outset #72727B; }
.bd-blue { border: 5px outset #57D7BE; }
.bd-orange { border: 5px outset #E99D56; }
.bg-grey { background-image: linear-gradient(to bottom right, #72727B 20%, lighten(#72727B, 10) 50%, #72727B 80%); }
.bg-pink { background-image: linear-gradient(to bottom right, #AD7B85 20%, lighten(#AD7B85, 10) 50%, #AD7B85 80%); }
.main-screen{
  height: 100%;
  position: relative;
  .header{
    height: 70px;
    justify-content: space-between;
    &-left{
      flex-direction: column;
      justify-content: space-between;
      .name{
        font-size: 36px;
      }
      .info{
        font-size: 24px;
        &-bed{
          margin-right: 60px;
        }
      }
    }
    &-right{
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;
      .date{
        font-size: 16px;
      }
      .time-wrapper{
        display: flex;
        align-items: flex-end;
        .tof-message{
          padding: 5px 40px;
          background-color: #D7D7D7;
          font-size: 24px;
          margin-right: 40px;
        }
        .time{
          font-size: 42px;
        }
      }
    }
  }
  .main{
    height: calc(100% - 140px);
    position: relative;
    .time-list{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      &.row{
        justify-content: flex-end;
      }
      &.vertical{
        flex-direction: column;
        align-items: flex-end;
      }
      .time-item{
        background-color: rgba($color: #000000, $alpha: .5);
        box-sizing: border-box;
        padding: 5px;
        border: 2px solid #BABABA;
        width: 360px;
        height: 70px;
        position: relative;
        .layout{
          position: absolute;
          top: 0;
          left: 0;
          width: 180px;
          height: 100%;
          display: flex;
          align-items: center;
          height: 100%;
          &-button{
            box-sizing: border-box;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            width: 50%;
            height: 100%;
            background-image: linear-gradient(to bottom right, #72727B 20%, #8c8c94 50%, #72727B 80%);
            border: 5px solid #fff;
          }
        }
        .control{
          position: absolute;
          top: 0;
          right: 0;
          width: 180px;
          height: 100%;
          display: flex;
          align-items: center;
          height: 100%;
          &-button{
            box-sizing: border-box;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            width: 50%;
            height: 100%;
            background-image: linear-gradient(to bottom right, #72727B 20%, #8c8c94 50%, #72727B 80%);
            border: 5px solid #fff;
          }
        }
        &-label{
          font-size: 16px;
          margin-bottom: 10px;
        }
        &-info{
          display: flex;
          align-items: center;
          >img{
            width: 30px;
          }
          .button{
            width: 72px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #fff;
            border-radius: 3px;
            .pause{
              &::before{
                content: '';
                display: inline-block;
                width: 5px;
                height: 20px;
                background-color: #fff;
                margin-right: 10px;
              }
              &::after{
                content: '';
                display: inline-block;
                width: 5px;
                height: 20px;
                background-color: #fff;
              }
            }
          }
          .time{
            font-size: 30px;
            flex: 1;
            text-align: center;
          }
        }
      }
    }
  }
  .footer{
    height: 70px;
    display: flex;
    .button{
