<template>
  <div class="part4 d-flex">
    <div class="info">
      <div class="info-1 c-atrovirens">TOF rat</div>
      <div class="info-2 c-darken-atrovirens" :style="{ opacity: transparent ? 0.5 : 1 }">{{ tofRat ? tofRatValue : '---' }}</div>
      <div class="info-3 c-darken-atrovirens" v-if="tofRat">13:01:03</div>
      <div class="info-4">
        <img :src="displayMode === 'bar' || displayMode === 'wave' ? imgTof : imgTofNone" alt="">
      </div>
      <div class="drag-info" v-if="drag">RR-lmp</div>
    </div>
    <div class="chart" @click="handleThumbnail">
      <div class="thumbnail" v-if="thumbnail">
        <div v-for="n of 12" :style="{ left: `${(n - 1) * 60 + 20}px` }" :class="['thumbnail-line', n % 2 === 0 ? 'solid' : 'dashed']" :key="n"></div>
        <div class="thumbnail-info1 c-atrovirens">TOF rat</div>
        <div class="thumbnail-info2 c-yellow">TOF cnt</div>
        <div class="thumbnail-info3 c-atrovirens">100</div>
        <div class="thumbnail-info4 c-atrovirens">0</div>
        <div class="thumbnail-info5 c-yellow">4</div>
        <div class="thumbnail-info6 c-yellow">0</div>
        <div class="thumbnail-info7">12:40</div>
        <div class="thumbnail-info8">12:45</div>
        <div class="thumbnail-info9">12:50</div>
        <div class="thumbnail-info10">12:55</div>
        <div class="thumbnail-info11">13:00</div>
        <div class="thumbnail-info12">13:05</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import imgTof from './images/img_tof.jpg'
import imgTofNone from './images/img_tof_none.jpg'
export default {
  data () {
    return {
      drag: false,
      imgTof: imgTof,
      imgTofNone: imgTofNone
    }
  },
  computed: {
    ...mapState('app', ['thumbnail']),
    ...mapState('tof', ['displayMode', 'tofRat', 'tofRatValue', 'transparent'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    handleThumbnail () {
      this.SET_THUMBNAIL()
    }
  }
}
</script>

<style lang="scss" scoped>
.part4{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    >div{
      position: absolute;
      text-align: right;
      display: flex;
      align-items: flex-start;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #68E8A8;
      background-color: #333;
      color: #68E8A8;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: 5px; top: 5px; }
    &-2{ font-size: 64px; left: 50px; top: 30px; }
    &-3{ font-size: 14px; left: 5px; bottom: 0px; }
    &-4{
      left: 50%;
      top: 0px;
      >img{ width: 200px; }
    }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 40px; top: 20px }
      &-2{ right: 10px; top: 20px }
    }
    .thumbnail{
      width: 50%;
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      background-color: #000;
      border-top: 1px solid #68E8A8;
      border-bottom: 1px solid #68E8A8;
      >div{ position: absolute; }
      &-line{
        top: 0;
        height: 100%;
        &.dashed{ border-left: 1px dashed #68E8A8; }
        &.solid{ border-left: 1px solid #68E8A8; }
      }
      &-info1{ left: 10px; top: 0px; }
      &-info2{ left: 10px; bottom: 0px; }
      &-info3{ right: 5px; top: 0px; }
      &-info4{ right: 5px; bottom: 50%; }
      &-info5{ right: 5px; top: 50%; }
      &-info6{ right: 5px; bottom: 0; }
      &-info7{ left: calc(60px + 20px); bottom: 0; transform: translateX(-50%); }
      &-info8{ left: calc(60px * 3 + 20px); bottom: 0; transform: translateX(-50%); }
      &-info9{ left: calc(60px * 5 + 20px); bottom: 0; transform: translateX(-50%); }
      &-info10{ left: calc(60px * 7 + 20px); bottom: 0; transform: translateX(-50%); }
      &-info11{ left: calc(60px * 9 + 20px); bottom: 0; transform: translateX(-50%); }
      &-info12{ left: calc(60px * 11 + 20px); bottom: 0; transform: translateX(-50%); }
    }
  }
}
</style>
