<template>
<div class="BasicSettings">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box>
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData" :lines="[
            { color: '#ccc', width: 1, value: 90 },
            { color: '#ccc', width: 1, value: -90 }
          ]"/>
          <span class="t-1 c-orange">EEG1-BIS</span>
          <span class="t-2">50</span>
          <div class="gong"></div>
        </div>

        <ecg-label>Sens</ecg-label>
        <ecg-button-list itemWidth="40px" itemHeight="40px" :list="[
          ['10', '20', '30', '50', '70', '100'],
          ['150', '200']
        ]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="h-100">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['', '16:00', '18:00']" :y="[0, 25, 50, 75, 100]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10 c-orange" content="98" text1="BIS" text2=""/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box class="c-alarm-box" title="BIS" content="98" text1="Off" text2="40" active/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { waveData } from '@/libs/helper'

export default {
  methods: {
    leftData () {
      return [
        {
          color: '#FF9028',
          width: 2,
          data: waveData(10, 10, [0, 10, 20, 30, 10, 20, 0, 20, 10, 0])
        }
      ]
    },

    rightData () {
      return [
        {
          color: '#FF9028',
          area: 60,
          data: [90]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.BasicSettings {
  .left-chart {
    height: 330px;
    position: relative;
    span {
      position: absolute;
      font-weight: bold; font-size: 16px;
      &.t-1 { left: 5px; top: 15px; }
      &.t-2 { right: 30px; top: 100px; }
    }
    .gong { position: absolute; right: 20px; top: 110px; }
  }
  .c-alarm-box /deep/ .ecg-alarm-box-content { color: #FF9028; }
}
</style>
