<template>
<ecg-embeded-dialog class="pacemaker-dlg" ref="dlg" title="Pacemaker" width="300px" height="150px">
  <div class="wrapper">
    <ecg-button :active="pacemaker" @click="set(true)">On</ecg-button>
    <ecg-button :active="!pacemaker" @click="set(false)">Off</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('patient', ['pacemaker'])
  },

  methods: {
    ...mapMutations('patient', ['SET_PACEMAKER']),

    open () {
      this.$refs.dlg.open()
    },

    set (pacemaker) {
      this.SET_PACEMAKER(pacemaker)
      this.$refs.dlg.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.pacemaker-dlg {
  .wrapper {
    height: 100%;
    padding: 0 50px;
    display: flex; justify-content: space-between; align-items: center;
  }
}
</style>
