<template>
  <div class="DataEntry">
    <div class="DataEntry-left">
      <div class="input" v-for="(item, idx) of inputs" :key="idx">
        <div v-if="item.type === 'label'" v-html="item.text" class="input-label"></div>
        <ecg-button width="160px" height="40px" v-else :active="item.id === currentInput" v-html="item.text" @click="changeInput(item.id)"></ecg-button>
        <div class="input-box">{{ item.value ? item.value : '---' }} {{ item.unit }}</div>
      </div>
    </div>
    <div class="DataEntry-right">
      <component :is="currentComponent"></component>
    </div>
  </div>
</template>

<script>
import { find as _find } from 'lodash'
export default {
  data () {
    return {
      currentInput: 'Height',
      inputs: [
        { id: 'Height', text: 'Height', type: 'button', value: '', component: require('./DataEntry/Height').default, unit: '[cm]' },
        { id: 'Weight', text: 'Weight', type: 'button', value: '', component: null, unit: '[mg]' },
        { id: 'CO', text: 'CO', type: 'button', value: '', component: null, unit: '[L/min]' },
        { id: 'O2', text: 'O<sub>2</sub>', type: 'button', value: '', component: null, unit: '[%]' },
        { id: 'Pbar', text: 'Pbar', type: 'button', value: '', component: null, unit: '[mmHg]' },
        { id: 'PaCO2', text: 'PaCO<sub>2</sub>', type: 'button', value: '', component: null, unit: '[mmHg]' },
        { id: 'Hb', text: 'Hb', type: 'button', value: '', component: null, unit: '[g/dl]' },
        { id: 'PaO2', text: 'PaO<sub>2</sub>', type: 'button', value: '', component: null, unit: '[mmHg]' },
        { id: 'SaO2', text: 'SaO<sub>2</sub>', type: 'button', value: '', component: null, unit: '[%]' },
        { id: 'PvO2', text: 'PvO<sub>2</sub>', type: 'button', value: '', component: null, unit: '[mmHg]' },
        { id: 'SvO2', text: 'SvO<sub>2</sub>', type: 'button', value: '', component: null, unit: '[%]' }

      ]
    }
  },
  computed: {
    currentComponent () {
      return _find(this.inputs, o => o.id === this.currentInput).component
    }
  },
  methods: {
    changeInput (id) {
      this.currentInput = id
    }
  }
}
</script>

<style lang="scss" scoped>
.DataEntry{
  display: flex;
  align-items: center;
  padding: 40px 0 0 10px;
  &-left{
    width: 50%;
    .input{
      display: flex;
      align-items: center;
      font-weight: bold;
      margin-bottom: 16px;
      &-label{
        width: 160px;
        text-align: center;
      }
      &-box{
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #333;
        width: 240px;
        height: 40px;
        background: #fff;
        color: #000;
      }
    }
  }
  &-right{
    width: 50%;
  }
}
</style>
