<script>
import ZoomIn from './ZoomIn'

const GRAPHIMAGES_PATH = require.context('../GraphImages/', false, /(\.png)$/)
const GRAPHIMAGES = {}
GRAPHIMAGES_PATH.keys().forEach(key => {
  const keyname = key.split('/').slice(1)[0].split('.')[0]
  GRAPHIMAGES[keyname] = GRAPHIMAGES_PATH(key)
})
export default {
  components: { ZoomIn },

  data () {
    return {
      images: GRAPHIMAGES,
      zoomInVisible: false
    }
  }
}
</script>

<style lang="scss" scoped>
.Recall{
  position: relative;
  &-container{
    background-color: #404040;
    padding: 5px;
    height: 550px;
    .table{
      width: 100%;
      >tr{
        &:not(:first-of-type){
          background-color: #000;
        }
        &:not(:last-of-type){
          >td{
            border-bottom: 1px solid #2A2A2A;
          }
        }
        >td{
          height: 40px;
          font-size: 16px;
          position: relative;
          &:nth-of-type(1){
            width: calc(100% * 3 / 10);
          }
          &:nth-of-type(2){
