              display: block;
              position: absolute;
              width: 1px;
              height: 80%;
              background-color: #fff;
              right: 0;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          .time{
            padding-right: 20px;
            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
          }
          .priority{
