            height: 20px;
            background-color: #FFFF00;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
          }
          .type{
            padding-left: 20px;
          }
          .desc{
            padding-left: 20px;
          }
        }
      }
    }
  }
