import dialog from './dialog'
import alarm from './alarm'
import { random as _random, remove as _remove } from 'lodash'
const ECG_COMPONENTS = require.context('../packages', false, /\.vue/)

const randomArr = []
let randomTimer = null
function randomUpdate () {
  randomArr.forEach(item => {
    const min = Math.floor(item.value * 0.8)
    const max = Math.floor(item.value * 1.2)
    item.el.innerHTML = _random(min, max)
  })
}

export default {
  install (Vue, _opts) {
    Vue.directive('random', {
      bind (el, binding) {
        randomArr.push({ el, value: binding.value })
        if (!randomTimer) {
          randomTimer = setInterval(randomUpdate, 1000)
        }
      },
      inserted (el, binding) {
        el.innerHTML = binding.value
      },
      unbind (el, binding) {
        _remove(randomArr, item => item.el === el && item.value === binding.value)
        if (!randomArr.length) {
          clearInterval(randomTimer)
          randomTimer = null
        }
      }
    })

    Vue.prototype.$dialog = dialog
    Vue.prototype.$alarm = alarm

    ECG_COMPONENTS
      .keys()
      .map(c => (['ecg-' + c.slice(2, -4), ECG_COMPONENTS(c).default]))
      .forEach(c => Vue.component(c[0], c[1]))
  }
}
