<template>
  <div class="Display2" @click.stop>
    <ecg-button
      width="340px"
      height="30px"
      v-for="(item, idx) of btns"
      :key="idx"
      :active="active === item.text"
      @click="handleClick(item.text)">
      {{ item.text }}
    </ecg-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      active: '',
      btns: [
        { text: 'L NONE' },
        { text: 'Screen 11' },
        { text: 'R NONE' },
        { text: 'Screen 12' },
        { text: 'L SIDE' },
        { text: 'Screen 13' },
        { text: 'R SIDE' },
        { text: 'Screen 14' },
        { text: 'L BOTTOM' },
        { text: 'Screen 15' },
        { text: 'R BOTTOM' },
        { text: 'Screen 16' },
        { text: 'SWAN INSERT' },
        { text: 'Screen 17' },
        { text: 'Screen 8' },
        { text: 'Screen 18' },
        { text: 'Screen 9' },
        { text: 'Screen 19' },
        { text: 'Screen 10' },
        { text: 'Screen 20' }
      ]
    }
  },
  mounted () {
    this.active = this.$parent.input2
  },
  methods: {
    handleClick (text) {
      this.active = text
      this.$parent.input2 = text
      this.$parent.open = false
    }
  }
}
</script>

<style lang="scss" scoped>
.Display2{
  display: flex;
  flex-wrap: wrap;
  .ecg-button{
    margin-bottom: 2px;
    &:nth-of-type(2n-1){
      margin-right: 10px;
    }
  }
}
</style>
