<template>
<div class="ecg-embeded-dialog" v-if="visible">
  <div class="ecg-embeded-dialog-wrapper" :style="{ width, height }">
    <div class="ecg-embeded-dialog-header">
      <div class="title">{{ title }}</div>
      <ecg-button class="close" width="40px" v-if="closeable" @click="close"></ecg-button>
    </div>

    <div class="ecg-embeded-dialog-content">
      <slot/>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    width: { type: String, default: '100%' },
    height: { type: String, default: '100%' },
    title: { type: String, default: '' },
    closeable: { type: Boolean, default: true }
  },

  data () {
    return {
      visible: false
    }
  },

  methods: {
    open () {
      this.visible = true
    },

    close () {
      this.visible = false
    }
  }
}
</script>

