        <div class="chart-info-3">FiltMon</div>
        <div class="chart-info-4">1mV</div>
        <div class="chart-info-5">Ⅱ</div>
        <div class="chart-info-6 gong"></div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <div v-for="n of 12" :style="{ left: `${(n - 1) * 60 + 20}px` }" :class="['thumbnail-line', n % 2 === 0 ? 'solid' : 'dashed']" :key="n"></div>
        <div class="thumbnail-info1 c-green">HR</div>
        <div class="thumbnail-info2 c-atrovirens">ST-Ⅱ</div>
        <div class="thumbnail-info3 c-green">200</div>
