</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['thumbnail'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    genData () {
      return [
        {
          color: '#00FF00',
          width: 2,
          data: [90]
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    }
  }
}
</script>

<style lang="scss" scoped>
.part1{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    >div{
      position: absolute;
      text-align: right;
      display: flex;
      align-items: flex-start;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #00FF00;
      background-color: #333;
      color: #00FF00;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: 5px; top: 5px; }
    &-2{ font-size: 14px; right: 0px; top: 20px; }
    &-3{ font-size: 120px; right: 80px; top: 0px; }
  }
  .chart{
    height: 100%;
