<template>
<div class="PatientSettings">
  <div class="d-flex mb-40">
    <div class="hw-box mr-40" :class="{ active: activeInput === 1 }" @click="activeInput = 1">
      <span class="text">{{ input1 }}</span>
      <div class="t-1">Height</div>
      <div class="t-2">(0'2 ~ 9'11.9)</div>
      <div class="t-3">[ft]</div>
    </div>
    <div class="hw-box" :class="{ active: activeInput === 2 }" @click="activeInput = 2">
      <span class="text">{{ input2 }}</span>
      <div class="t-1"></div>
      <div class="t-2"></div>
      <div class="t-3">[in]</div>
    </div>
  </div>
  <div class="d-flex mb-40">
    <div class="hw-box" :class="{ active: activeInput === 3 }" @click="activeInput = 3">
      <span class="text">{{ input3 }}</span>
      <div class="t-1">Weight</div>
      <div class="t-2">(0.1 ~ 654.9)</div>
      <div class="t-3">[Ib]</div>
    </div>
    <div class="hw-box mt-40 ml-auto" style="height: 40px" :class="{ active: activeInput === 4 }" @click="activeInput = 4">
      <span class="text">{{ input4 }}</span>
      <div class="t-1">BSA</div>
      <div class="t-3">[m²]</div>
    </div>
  </div>
  <div class="d-flex">
    <ecg-box>
      <ecg-number-keyboard @press="handlePress"/>
    </ecg-box>
  </div>
  <div class="d-flex">
    <ecg-button class="ml-auto">Set</ecg-button>
  </div>
</div>
</template>

<script>
import { startsWith as _startsWith } from 'lodash'

export default {
  data () {
    return {
      activeInput: 1,
      input1: '',
      input2: '',
      input3: '',
      input4: ''
    }
  },

  methods: {
    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this[`input${this.activeInput}`] = ''
        } else if (key === 'CTRL:BS') {
          this[`input${this.activeInput}`] = this[`input${this.activeInput}`].slice(0, this[`input${this.activeInput}`].length - 1)
        }
      } else {
        // 普通字符
        this[`input${this.activeInput}`] = this[`input${this.activeInput}`].concat(key)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.PatientSettings {
  padding: 50px 290px 20px;
  .hw-box {
    width: 100px; height: 80px;
    box-sizing: border-box;
    background: #fff;
    border: 4px inset #999;
    color: #000;
    font-weight: bold;
    position: relative;
    display: flex; justify-content: center; align-items: center;
    &.active { border-color: #009DDC; }
    .text { font-size: 18px; }
    .t-1 { position: absolute; right: calc(100% + 10px); top: 50%; transform: translateY(-50%); }
    .t-2 { position: absolute; left: 0; top: calc(100% + 5px); }
    .t-3 { position: absolute; left: calc(100% + 5px); bottom: 0; }
  }
}
</style>
