<template>
<div class="LungFunc-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        {
          type: 'group',
          text: 'Lung<br>Func',
          children: [
            { type: 'item', text: 'Data<br>Entry', component: require('@/dialogs/LungFunc/DataEntry').default },
            { type: 'item', text: 'Result', component: require('@/dialogs/LungFunc/Result').default }
          ]
        }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
