<template>
  <div class="part2 d-flex">
    <div class="info">
      <div class="info-part1">
        <div class="info-part1-1 c-yellow">PAP</div>
        <div class="info-part1-2 c-darken-yellow">Off<br>Off</div>
        <div class="info-part1-3 c-darken-yellow">Off<br>Off</div>
        <div class="info-part1-4 c-yellow">16/ -4</div>
        <div class="info-part1-5 c-yellow">( 3)</div>
        <div class="info-part1-6 c-darken-yellow">Off<br>Off</div>
      </div>
      <div class="info-part2">
        <div class="info-part2-1 c-white">CO<sub>2</sub>-E</div>
        <div class="info-part2-2 c-darken-white">[mmHg]</div>
        <div class="info-part2-3 c-white">RR</div>
        <div class="info-part2-4 c-white">20</div>
        <div class="info-part2-5 c-darken-white">Off<br>Off</div>
        <div class="info-part2-6 c-white">42</div>
        <div class="info-part2-7 c-darken-white">Off<br>Off</div>
      </div>
      <div class="info-part3">
        <div class="info-part3-1 c-white">RR-lmp</div>
        <div class="info-part3-2 c-white">20</div>
        <div class="info-part3-3 c-white">Off<br>Off</div>
      </div>
      <div class="info-part4">
        <div class="info-part4-1 c-white">CO</div>
        <div class="info-part4-2 c-white">---</div>
        <div class="info-part4-3 c-white">CI</div>
        <div class="info-part4-4 c-white">---</div>
        <div class="info-part4-5 c-white">---</div>
        <div class="info-part4-6 c-white">PCWP</div>
        <div class="info-part4-7 c-white">---</div>
        <div class="info-part4-8 c-orange">Tb</div>
        <div class="info-part4-9 c-orange">37.1</div>
        <div class="info-part4-10 c-darken-orange">38.0<br>Off</div>
        <div class="info-part4-11 c-white">---</div>
      </div>
      <div class="drag-info" v-if="drag">ECG</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#999900', width: 1, value: 98 },
          { color: '#999900', width: 1, value: -20 },
          { color: '#999900', width: 1, value: 40},
          { color: '#999900', width: 1, value: 70},
          { color: '#999900', width: 1, value: 10},
          { color: '#9A9A9A', width: 1, value: -24},
          { color: '#9A9A9A', width: 1, value: -52},
          { color: '#9A9A9A', width: 1, value: -38}
        ]"
      />
      <div class="chart-info">
        <div class="chart-info-1 c-yellow">PAP</div>
        <div class="chart-info-2 c-yellow">40</div>
        <div class="chart-info-3 c-yellow">0</div>
        <div class="chart-info-4 c-white">40</div>
        <div class="chart-info-5 c-white">0</div>
        <div class="chart-info-6 c-white">Resp-lmp</div>
        <div class="chart-info-7 c-white">x1/2</div>
      </div>
