  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'

export default {
  data () {
    return {
      drag: false
    }
  },
  methods: {
    genData () {
      const data1 = _map(new Array(15 * 8), (item, idx) => {
        switch (idx % 8) {
          case 0:
            return _random(30, 35)
          case 1:
            return _random(0, 5)
          case 2:
            return _random(0, 5)
          case 3:
            return _random(-20, -10)
          case 4:
            return _random(30, 40)
          case 5:
            return _random(-25, -20)
          case 6:
            return _random(-25, -20)
          case 7:
            return _random(-40, -35)
          default:
            return 0
        }
      })
      const data2 = _map(new Array(9 * 12), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return -52
          case 1:
            return -52
          case 2:
            return _random(-30, -27)
          case 3:
            return _random(-27, -24)
          case 4:
            return -24
          case 5:
            return _random(-27, -24)
          case 6:
            return _random(-30, -27)
          case 7:
            return -52
          case 8:
            return -52
          case 9:
            return -52
          case 10:
            return -52
          case 11:
            return -52
          default:
            return -52
        }
      })
      const data3 = _map(new Array(9 * 7), (item, idx) => {
        switch (idx % 7) {
          case 0:
            return -60
          case 1:
            return -58
          case 2:
            return -56
          case 3:
            return _random(-56, -54)
          case 4:
            return _random(-56, -54)
          case 5:
            return _random(-56, -54)
          case 6:
            return _random(-40, -38)
        }
      })
      return [
        { color: '#FFFF00', width: 2, data: data1 },
        { color: '#FFFFFF', width: 2, data: data2, area: -52 },
        { color: '#FFFFFF', width: 2, data: data3 }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part2{
  .info{
    height: 100%;
    width: 25%;
    border: 1px solid #808080;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FFFF00;
      background-color: #333;
      color: #FFFF00;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    >div{
      box-sizing: border-box;
      position: relative;
      >div{
        position: absolute;
        text-align: right;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
    }
    &-part1{
      height: calc(100% * 8 / 13);
      border-bottom: 1px solid #808080;
      &-1{ font-size: 16px; left: 5px; top: 25%; }
      &-2{ font-size: 14px; left: 15px; top: calc(25% + 25px); }
      &-3{ font-size: 14px; right: 5px; top: calc(25% + 25px); }
      &-4{ font-size: 80px; right: 100px; top: calc(25% + 30px); }
      &-5{ font-size: 60px; right: 150px; top: calc(25% + 120px); }
      &-6{ font-size: 14px; right: 120px; top: calc(25% + 120px); }
    }
    &-part2{
      height: calc(100% * 2 / 13);
      border-bottom: 1px solid #808080;
      &-1{ font-size: 16px; left: 50%; top: 5px; }
      &-2{ font-size: 14px; right: 5px; top: 5px; }
      &-3{ font-size: 16px; left: 5px; top: 30px; }
      &-4{ font-size: 60px; left: 25%; top: 30px; }
      &-5{ font-size: 14px; left: calc(25% + 80px); top: 30px; }
      &-6{ font-size: 80px; right: 60px; top: 10px; }
      &-7{ font-size: 14px; right: 5px; top: 30px; }
    }
    &-part3{
      height: calc(100% * 1 / 13);
      border-bottom: 1px solid #808080;
      &-1{ font-size: 16px; left: 50%; top: 5px; }
      &-2{ font-size: 40px; right: 50px; top: 5px; }
      &-3{ font-size: 14px; right: 5px; top: 5px; }
    }
    &-part4{
      height: calc(100% * 2 / 13);
      border-bottom: 1px solid #808080;
      &-1{ font-size: 16px; left: 5px; top: 5px; }
