      &-8{ font-size: 16px; left: 50%; top: calc(50% + 5px); }
      &-9{ font-size: 40px; left: 75%; top: 50%; }
      &-10{ font-size: 14px; right: 5px; top: 50%; }
      &-11{ font-size: 16px; left: 5px; bottom: 0px; }
    }
  }
  .chart{
    height: 100%;
    width: 75%;
