        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
      &-2{ right: 10px; top: 10px; }
      &-3{ right: 10px; top: 400px; }
      &-4{ right: 10px; top: 450px; }
      &-5{ right: 10px; top: 525px; }
      &-6{ right: 60px; top: 550px; }
