import 'normalize.css'
import './styles/app.scss'

import Vue from 'vue'
import TWEEN from '@tweenjs/tween.js'
import App from './app'
import ECGPlugin from './libs/ecg-plugin'
import store from './store'
import { imgPreloader } from '@/libs/helper'
Vue.config.productionTip = false
Vue.use(ECGPlugin)

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')

// Tween 动画循环
function tweenAnimate (time) {
  window.requestAnimationFrame(tweenAnimate)
  TWEEN.update(time)
}

tweenAnimate()

// 调用图片预加载
const preLoadImage = [
  require('@/dialogs/Review/GraphImages/compressed1.jpg'),
  require('@/dialogs/Review/GraphImages/compressed2.jpg'),
  require('@/dialogs/Review/GraphImages/compressed3.jpg')
]
preLoadImage.forEach(item => {
  imgPreloader(item)
})
