<template>
<div class="Interbed-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        {
          type: 'group',
          text: 'Interbed',
          children: [
            { type: 'item', text: 'Group1', component: require('@/dialogs/Interbed/Group1').default },
            { type: 'item', text: 'Group2', component: require('@/dialogs/Interbed/Group2').default },
            { type: 'item', text: 'Beds', component: require('@/dialogs/Interbed/Beds').default },
            { type: 'item', text: 'Detailed<br>Settings', component: require('@/dialogs/Interbed/DetailedSettings').default }
          ]
        }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
