<template>
<canvas
  class="ecg-heart-beat"
  :style="{ width, height }"/>
</template>

<script>
import { clamp as _clamp, cloneDeep as _cloneDeep } from 'lodash'
import TWEEN from '@tweenjs/tween.js'

export default {
  props: {
    width: { type: String, default: '100%' },
    height: { type: String, default: '200px' },
    duration: { type: Number, default: 15000 },
    lines: { type: Array, default () { return [] } },
    dataFunc: { type: Function, required: true }
  },

  mounted () {
    this.initCanvas()
    this.animate()
  },

  methods: {
    initCanvas () {
      this._canvas = this.$el
      this._ctx = this._canvas.getContext('2d')

      this._canvas.width = this._canvas.clientWidth
      this._canvas.height = this._canvas.clientHeight

      this._ctx.lineJoin = 'round'
    },

    transformY (dataY) {
      const halfHeight = this._canvas.height / 2

      let y = _clamp(dataY, -100, 100)
      y = (y / 100) * halfHeight
      y = -y + halfHeight

      return y
    },

    animate () {
      const data = this.dataFunc()

      const firstData = data.map(item => this.transformY(item.data[0]))
        .reduce((o, item, idx) => {
          o[`x_${idx}`] = 0
          o[`y_${idx}`] = item
          return o
        }, {})

      const restData = data.map(item => item.data.slice(1).map(this.transformY))
        .reduce((o, item, idx) => {
          o[`x_${idx}`] = this._canvas.width
          o[`y_${idx}`] = item
          return o
        }, {})

      this._prevPos = _cloneDeep(firstData)

      new TWEEN.Tween(firstData)
        .to(restData, this.duration)
        .onUpdate(o => {
          // 画横线
          this.lines.forEach(item => {
            const y = this.transformY(item.value)

            this._ctx.strokeStyle = item.color
            this._ctx.lineWidth = item.width
            this._ctx.beginPath()
            this._ctx.moveTo(0, y)
            this._ctx.lineTo(this._canvas.width, y)
            this._ctx.stroke()
          })

          // 擦除
          this._ctx.clearRect(o.x_0, 0, Math.min(10, this._canvas.width / 20), this._canvas.height)

          // 画波
          for (let idx = 0; idx < data.length; idx++) {
            const item = data[idx]
            const xKey = `x_${idx}`
            const yKey = `y_${idx}`

            if (item.area === undefined) {
              // 线条
              this._ctx.strokeStyle = item.color
              this._ctx.lineWidth = item.width
              this._ctx.beginPath()
              this._ctx.moveTo(this._prevPos[xKey], this._prevPos[yKey])
              this._ctx.lineTo(o[xKey], o[yKey])
              this._ctx.stroke()
              this._ctx.closePath()
            } else {
              // 填充
              const areaValue = this.transformY(item.area)
              const areaPadding = 1

              this._ctx.strokeStyle = item.color
              this._ctx.fillStyle = item.color
              this._ctx.lineWidth = 1
              this._ctx.beginPath()
              this._ctx.moveTo(this._prevPos[xKey] - areaPadding, this._prevPos[yKey])
              this._ctx.lineTo(o[xKey] + areaPadding, o[yKey])
              this._ctx.lineTo(o[xKey] + areaPadding, areaValue)
              this._ctx.lineTo(this._prevPos[xKey] - areaPadding, areaValue)
              this._ctx.lineTo(this._prevPos[xKey] - areaPadding, this._prevPos[yKey])
              this._ctx.fill()
              this._ctx.closePath()
            }

            this._prevPos[xKey] = o[xKey]
            this._prevPos[yKey] = o[yKey]
          }
        })
        .onComplete(this.animate)
        .start()
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-heart-beat {
  background: #000;
}
</style>
