import EventEmiter from 'events'

class Dialog extends EventEmiter {
  /**
   * 打开弹框
   * @param {string} name 弹框名称
   * @param  {...any} opts 弹框参数
   */
  open (name, ...opts) {
    this.emit('open', name, ...opts)
  }

  /**
   * 关闭弹框
   */
  close () {
    this.emit('close')
  }

  /**
   * 判断弹窗是否打开
   * @param {string} name 弹框名称
   * @param  {...any} opts 弹框参数
   */
  openState (name, ...opts) {
    this.emit('openState', name, ...opts)
  }

  /**
   * 设置弹框背景颜色
   * @param {string} color 背景颜色
   */
  setBgColor (color) {
    this.emit('setBgColor', color)
  }
}

export default new Dialog()
