<template>
<div class="Review-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        {
          type: 'group',
          text: 'Screen<br>Layout',
          children: [
            { type: 'item', text: 'Select', component: require('@/dialogs/Setup/ScreenLayout/Select').default }
          ]
        },
        {
          type: 'group',
          text: 'Sound/<br>Display',
          children: [
            { type: 'item', text: 'Volume', component: require('@/dialogs/Setup/SoundDisplay/Volume').default },
            { type: 'item', text: 'Wave', component: require('@/dialogs/Setup/SoundDisplay/Wave').default }
          ]
        },
        {
          type: 'group',
          text: 'Record',
          children: [
            { type: 'item', text: 'Record', component: require('@/dialogs/Setup/Record/Record').default },
            { type: 'item', text: 'Printer', component: require('@/dialogs/Setup/Record/Printer').default }
          ]
        },
        {
          type: 'group',
          text: 'Remote<br>Controller',
          children: [
            { type: 'item', text: 'Keys', component: require('@/dialogs/Setup/RemoteController/Keys').default }
          ]
        }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
