<template>
<div class="TransportDataRemove">
  <div class="text d-flex" v-html="text"></div>
  <div class="flex-center" v-if="switchButton">
    <ecg-button style="margin-right: 120px" @click="handleTransportdata">Yes</ecg-button>
    <ecg-button @click="$dialog.close()">No</ecg-button>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      switchButton: true,
      text: `
        <div class="c-red py-40">Note: HiQ-View ends when the input unit is removed.</div>
        <div class="c-black py-40">Data in input unit will be saved and input unit will be disconnected. Continue?</div>
      `
    }
  },
  methods: {
    handleTransportdata () {
      this.switchButton = false
      this.text = `
        <div class="c-black py-40">Please wait</div>
      `
      setTimeout(() => {
        this.text = `
          <div class="c-black py-40">Remove the input unit</div>
        `
        setTimeout(() => {
          this.$dialog.close()
          this.$dialog.open('StandbyTransportData')
          this.$dialog.setBgColor('#000')
        }, 3000)
      }, 3000)
    }
  }
}
</script>

<style lang="scss" scoped>
.TransportDataRemove {
  .text{
    margin: 0 auto;
    width: 800px;
    height: 600px;
    font-size: 24px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
</style>
