<template>
<div class="CarSeatChallenge">
  <div class="box">
    <div class="box-header">Car Seat Challenge</div>
    <div class="box-content">
      <div class="time">{{ timeText }}</div>
      <ecg-button class="mx-auto" @click="stop">Stop</ecg-button>
    </div>
  </div>
</div>
</template>

<script>
import { padStart as _padStart } from 'lodash'
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      time: 0
    }
  },

  mounted () {
    this.time = this.timeInterval

    this._timer = setInterval(() => {
      this.time -= 1

      if (this.time < 0) {
        this.stop()
      }
    }, 1000)
  },

  beforeDestroy () {
    clearInterval(this._timer)
    this.SET_HAS_RESULT(true)
    this.$dialog.open('CarSeatChallengeMenu', [1])
  },

  computed: {
    ...mapState('carseat', ['timeInterval']),

    timeText () {
      const h = Math.floor(this.time / 3600)
      const m = Math.floor((this.time - 3600 * h) / 60)
      const s = this.time - 3600 * h - 60 * m

      return `${_padStart(h, 2, 0)} : ${_padStart(m, 2, 0)} : ${_padStart(s, 2, 0)}`
    }
  },

  methods: {
    ...mapMutations('carseat', ['SET_HAS_RESULT']),

    stop () {
      clearInterval(this._timer)
      this.$dialog.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.CarSeatChallenge {
  height: 600px;

  .box {
    width: 320px;
    margin: 0 auto;

    &-header {
      background: #62626D;
      padding: 10px 0;
      font-weight: bold; font-size: 18px;
      text-align: center;
    }
    &-content {
      background: #C0C0CE;
      padding: 20px;
      .time {
        color: #000;
        font-size: 48px;
        text-align: center;
        margin-bottom: 30px;
      }
    }
  }
}
</style>
