<template>
<div class="ecg-button-list">
  <div class="ecg-button-list-row"
      v-for="(row, ridx) of list"
      :key="ridx">
    <ecg-button
        v-for="(item, cidx) of row"
        :key="`${ridx}-${cidx}`"
        :width="itemWidth"
        :height="itemHeight"
        :active="`${ridx}-${cidx}` === current"
        :disabled="getDisabled(item)"
        @click="handleClick(ridx, cidx)"
        v-html="getText(item)">
    </ecg-button>
  </div>
</div>
</template>

<script>
export default {
  props: {
    itemWidth: { type: String, default: '80px' },
    itemHeight: { type: String, default: '40px' },
    list: { type: Array, required: true },
    defaultActive: { type: String, default: '0-0' }
  },

  data () {
    return {
      current: this.defaultActive
    }
  },

  methods: {
    getText (item) {
      if (typeof item === 'string') {
        return item
      } else {
        return item.text
      }
    },

    getDisabled (item) {
      if (typeof item === 'string') {
        return false
      } else {
        return !!item.disabled
      }
    },

    handleClick (ridx, cidx) {
      this.current = `${ridx}-${cidx}`
      this.$emit('change', this.current)
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-button-list {
  &-row { display: flex; }
}
</style>
