  methods: {
    genData () {
      const data1 = _map(new Array(12 * 8), (item, idx) => {
        switch (idx % 8) {
          case 0:
            return _random(80, 82)
          case 1:
            return _random(75, 77)
          case 2:
            return _random(80, 82)
          case 3:
            return _random(60, 62)
          case 4:
            return _random(80, 82)
          case 5:
            return _random(75, 77)
          case 6:
            return _random(77, 80)
          case 7:
            return _random(90, 95)
          default:
            return 0
        }
      })
      const data2 = _map(new Array(100), (item, idx) => {
        return _random(-90, -70)
      })
      return [
        {
          color: '#FFFF00',
          width: 2,
          data: data1
        },
        {
          color: '#FFFF00',
          width: 2,
          data: data2
        }
