<template>
  <div class="part2 d-flex">
    <div class="info">
      <div class="info-1 c-atrovirens">ST-Ⅱ</div>
      <div class="info-2 c-atrovirens"> --- </div>
      <div class="info-3 c-darken-atrovirens">[mm]</div>
      <div class="info-4 c-atrovirens">ST-V4</div>
      <div class="info-5 c-atrovirens"> --- </div>
      <div class="info-6 c-darken-atrovirens">[mm]</div>
      <div class="info-7 c-atrovirens">VPC</div>
      <div class="info-8 c-atrovirens"> --- </div>
      <div class="drag-info" v-if="drag">ST-Ⅱ</div>
    </div>
    <div class="chart" @click="handleThumbnail">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"/>
      <div class="chart-info c-white">
        <div class="chart-info-1">x1</div>
        <div class="chart-info-2">1mV</div>
        <div class="chart-info-3">Ⅱ</div>
        <div class="chart-info-4 gong"></div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <div v-for="n of 12" :style="{ left: `${(n - 1) * 60 + 20}px` }" :class="['thumbnail-line', n % 2 === 0 ? 'solid' : 'dashed']" :key="n"></div>
        <div class="thumbnail-info1 c-atrovirens">ST-V4</div>
        <div class="thumbnail-info2 c-atrovirens">5.0</div>
        <div class="thumbnail-info3 c-atrovirens">-5.0</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['thumbnail'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    genData () {
      return [
        {
          color: '#00FF00',
          width: 2,
          data: [90]
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    }
  }
}
</script>

<style lang="scss" scoped>
.part2{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    >div{
      position: absolute;
      text-align: right;
      display: flex;
      align-items: flex-start;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #68E8A8;
      background-color: #333;
      color: #68E8A8;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: 5px; top: 50%; }
    &-2{ font-size: 20px; right: calc(50% + 50px); top: 50%; }
    &-3{ font-size: 14px; right: calc(50% + 5px); top: 50%; }
    &-4{ font-size: 16px; left: calc(50% + 5px); top: 50%; }
    &-5{ font-size: 20px; right: 50px; top: 50%; }
    &-6{ font-size: 14px; right: 5px; top: 50%; }
    &-7{ font-size: 16px; left: calc(50% + 5px); top: 75%; }
    &-8{ font-size: 20px; right: 50px; top: 75%; }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ left: 40px; top: 20px }
      &-2{ right: 40px; top: 20px }
      &-3{ right: 40px; top: 60px }
      &-4{ right: 20px; top: 30px }
    }
    .thumbnail{
      width: 50%;
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      background-color: #000;
      border-top: 1px solid #68E8A8;
      border-bottom: 1px solid #68E8A8;
      >div{ position: absolute; }
      &-line{
        top: 0;
        height: 100%;
        &.dashed{ border-left: 1px dashed #68E8A8; }
        &.solid{ border-left: 1px solid #68E8A8; }
      }
      &-info1{ left: 10px; top: 0px; }
      &-info2{ right: 5px; top: 0px; }
      &-info3{ right: 5px; bottom: 0px; }
    }
  }
}
</style>
