<template>
  <div class="DoseUnit">
    <ecg-button
      width="200px"
      height="40px"
      v-for="(item, idx) of btns"
      :key="idx"
      v-html="item.text"
      :disabled="item.disabled"
      :active="item.text === current"
      @click="handleClick(item)">
    </ecg-button>
  </div>
</template>

<script>
import {
  find as _find
} from 'lodash'
export default {
  data () {
    return {
      current: '',
      btns: [
        { text: 'mg/min', disabled: false },
        { text: 'mg/h', disabled: false },
        { text: 'mg/kg/min', disabled: false },
        { text: 'mg/kg/h', disabled: false },
        { text: 'mcg/min', disabled: false },
        { text: 'mcg/h', disabled: false },
        { text: 'mcg/kg/min', disabled: false },
        { text: 'mcg/kg/h', disabled: false },
        { text: 'units/h', disabled: true },
        { text: 'units/kg/h', disabled: true },
        { text: 'IU/h', disabled: true },
        { text: 'IU/kg/h', disabled: true }
      ]
    }
  },
  mounted () {
    const parent = this.$parent
    this.current = _find(parent.inputs, o => parent.currentInput === o.id).value
  },
  methods: {
    handleClick (item) {
      this.current = item.text
      this.changeParentInput()
    },
    changeParentInput () {
      const parent = this.$parent
      parent.$set(_find(parent.inputs, o => parent.currentInput === o.id), 'value', this.current)
    }
  }
}
</script>

<style lang="scss" scoped>
.DoseUnit{
  width: 410px;
  display: flex;
  flex-wrap: wrap;
  .ecg-button{
    margin-bottom: 10px;
    &:nth-of-type(2n-1){
      margin-right: 10px;
    }
  }
}
</style>
