export default {
  namespaced: true,

  state () {
    return {
      displayMode: 'wave-none', // bar | wave | bar-none | wave-none
      chartMode: 'left', // left | right
      calActive: false,
      tofActive: false,
      timeActive: false,
      tofRat: false,
      tofRatValue: Math.round(Math.random() * 10 + 95),
      stimulationCurrent: 50,
      sensitivity: 50,
      stimulationDuration: 50,
      EMGWaveform: 50,
      twoMin: true,
      transparent: false
    }
  },

  mutations: {
    SET_DISPLAY_MODE (state, mode) {
      state.displayMode = mode
    },

    SET_CHART_MODE (state, mode) {
      state.chartMode = mode
    },

    SET_CAL_ACTIVE (state, value) {
      state.calActive = value
    },

    SET_TOF_ACTIVE (state, value) {
      state.tofActive = value
    },

    SET_TIME_ACTIVE (state, value) {
      state.timeActive = value
    },

    SET_TOF_RAT (state, value) {
      state.tofRat = value
    },

    CHANGE_TOF_RAT_VALUE (state) {
      state.tofRatValue = Math.round(Math.random() * 10 + 95)
    },

    CHANGE_STIMULATION_CURRENT (state, value) {
      state.stimulationCurrent = value
    },

    CHANGE_SENSITIVITY (state, value) {
      state.sensitivity = value
    },

    CHANGE_STIMULATION_DURATION (state, value) {
      state.stimulationDuration = value
    },

    CHANGE_EMG_WAVEFORM (state, value) {
      state.EMGWaveform = value
    },

    SET_TWOMIN (state, value) {
      state.twoMin = value
    },

    SET_TRANSPARENT (state, value) {
      state.transparent = value
    }
  },
  actions: {
    resetTOF (context) {
      context.commit('SET_DISPLAY_MODE', 'wave-none')
      context.commit('SET_CHART_MODE', 'left')
      context.commit('SET_CAL_ACTIVE', false)
      context.commit('SET_TOF_ACTIVE', false)
      context.commit('SET_TIME_ACTIVE', false)
      context.commit('SET_TOF_RAT', false)
    }
  }
}
