<template>
<div class="ecg-counter">
  <ecg-button width="100px" height="100px" @click="enable = !enable">
    <div class="wrapper">
      <div class="t-1">
        {{ enable ? count : 'Off' }}
      </div>

      <div class="t-2">
        {{ enable ? '' : count }}
      </div>

      <div class="b" v-if="borderColor" :style="{ background: borderColor }"></div>
    </div>
  </ecg-button>
  <div v-if="showArrow">
    <ecg-button width="50px" height="50px" :disabled="!upEnabled" @click="changeCount(step)">
      <div class="arrow up" :class="{ disabled: !upEnabled }"></div>
    </ecg-button>
    <ecg-button width="50px" height="50px" :disabled="!downEnabled" @click="changeCount(-step)">
      <div class="arrow down" :class="{ disabled: !downEnabled }"></div>
    </ecg-button>
  </div>
</div>
</template>

<script>
export default {
  props: {
    borderColor: { type: String },
    showArrow: { type: Boolean, default: true },
    value: { type: Number },
    max: { type: Number, default: 1000 },
    min: { type: Number, default: 0 },
    step: { type: Number, default: 1 }
  },

  data () {
    return {
      enable: true,
      innerCount: 20
    }
  },

  computed: {
    count () {
      if (this.step < 1) {
        return this.value === undefined ? this.innerCount : this.value.toFixed(2)
      }
      return this.value === undefined ? this.innerCount : this.value
    },

    upEnabled () {
      return this.enable && this.count < this.max
    },

    downEnabled () {
      return this.enable && this.count > this.min
    }
  },

  methods: {
    changeCount (n) {
      if (!this.enable) { return }

      if (this.value !== undefined) {
        this.innerCount = this.value + n
        if (this.innerCount >= this.max || this.innerCount <= this.min) { return }
      } else {
        this.innerCount += n
      }

      this.$emit('input', this.innerCount)
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-counter {
  display: flex;

  .wrapper {
    background: #fff;
    width: 85px; height: 85px;
    position: relative;
  }

  .t-1 {
    color: #000;
    font-size: 24px;
    position: absolute; left: 50%; top: 50%;
    transform: translate(-50%, -50%);
  }

  .t-2 {
    color: gray;
    font-size: 16px;
    position: absolute; right: 10px; bottom: 10px;
  }

  .b {
    width: 4px; height: 100%;
    position: absolute; right: 0; top: 0;
    border-left: 2px solid lighten(#72727B, 10);
  }

  .arrow {
    width: 0; height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    &.up {
      border-bottom: 20px solid #fff;
      &.disabled { border-bottom-color: #95959D; }
    }
    &.down {
      border-top: 20px solid #fff;
      &.disabled { border-top-color: #95959D; }
    }
  }
}
</style>
