<template>
<div class="alarm-info" v-if="alarmVisiable">
  <div class="white-box" v-if="silence">
    <img class="mr-10" :src="silenceIcon">
    {{ silenceMsg }}
    {{ formatSilenceCount }}
  </div>
  <div class="d-flex" :style="{ height: silence ? '30px' : '50px' }">
    <div class="color-box red-box" :class="{ active: red }">Bradycardia</div>
    <div class="color-box yellow-box" :class="{ active: yellow }">Tachycardia</div>
    <div class="color-box blue-box" :class="{ active: blue }"></div>
  </div>
</div>
</template>

<script>
import { padStart as _padStart } from 'lodash'
import { Howl } from 'howler'
import alarmRedSoundSrc from '@/sfx/alarm-red.wav'
import alarmYellowSoundSrc from '@/sfx/alarm-yellow.mp3'

const SILENCE_ICONS = {
  icon1: require('@/images/alarm-icon/icon1.png'),
  icon2: require('@/images/alarm-icon/icon2.png')
}

const SILENCE_COUNT = 120

export default {
  data () {
    return {
      silence: false,
      red: false,
      yellow: false,
      blue: false,
      silenceCount: 0,
      silenceMsg: '',
      silenceIcon: ''
    }
  },

  created () {
    this._silenceTimer = null
    this._redAlarmSound = new Howl({
      src: [alarmRedSoundSrc],
      loop: true
    })
    this._yellowAlarmSound = new Howl({
      src: [alarmYellowSoundSrc],
      loop: true
    })

    this.$alarm.on('open:red', this.handleOpenRed)
    this.$alarm.on('open:yellow', this.handleOpenYellow)
    this.$alarm.on('open:blue', this.handleOpenBlue)

    this.$alarm.on('close:red', this.handleCloseRed)
    this.$alarm.on('close:yellow', this.handleCloseYellow)
    this.$alarm.on('close:blue', this.handleCloseBlue)

    this.$alarm.on('silence', this.handleSilence)
  },

  beforeDestroy () {
    this.$alarm.off('open:red', this.handleOpenRed)
    this.$alarm.off('open:yellow', this.handleOpenYellow)
    this.$alarm.off('open:blue', this.handleOpenBlue)

    this.$alarm.off('close:red', this.handleCloseRed)
    this.$alarm.off('close:yellow', this.handleCloseYellow)
    this.$alarm.off('close:blue', this.handleCloseBlue)

    this.$alarm.off('silence', this.handleSilence)
  },

  computed: {
    alarmVisiable () {
      return this.silence || this.red || this.yellow || this.blue
    },

    formatSilenceCount () {
      const m = Math.floor(this.silenceCount / 60)
      const s = this.silenceCount % 60

      return `00:${_padStart(m, 2, 0)}:${_padStart(s, 2, 0)}`
    }
  },

  methods: {
    handleOpenRed () {
      if (this.red) { return }
      this.red = true
      this.playAlarmSound()
    },

    handleCloseRed () {
      if (!this.red) { return }
      this.red = false
      this.stopAlarmSound()
    },

    handleOpenYellow () {
      if (this.yellow) { return }
      this.yellow = true
      this.playAlarmSound()
    },

    handleCloseYellow () {
      if (!this.yellow) { return }
      this.yellow = false
      this.stopAlarmSound()
    },

    handleOpenBlue () {},
    handleCloseBlue () {},

    handleSilence (msg, icon) {
      if (this.silence) {
        this.stopSilence()
      } else {
        this.startSilence(msg, icon)
      }
    },

    startSilence (msg, icon) {
      if (this._silenceTimer) { return }

      this.silence = true
      this.silenceCount = SILENCE_COUNT
      this.silenceMsg = msg
      this.silenceIcon = SILENCE_ICONS[icon] || SILENCE_ICONS.icon1
      this.stopAlarmSound()

      this._silenceTimer = setInterval(() => {
        this.silenceCount -= 1

        if (this.silenceCount < 0) { this.stopSilence() }
      }, 1000)
    },

    stopSilence () {
      this.silence = false
      clearInterval(this._silenceTimer)
      this._silenceTimer = null
      this.silenceCount = 0
      this.silenceMsg = ''
      this.playAlarmSound()
    },

    playAlarmSound () {
      if (this.silence) {
        this.stopAlarmSound()
        return
      }

      if (this.red) {
        this._redAlarmSound.play()
        this._yellowAlarmSound.stop()
        return
      }

      if (this.yellow) {
        this._redAlarmSound.stop()
        this._yellowAlarmSound.play()
      }
    },

    stopAlarmSound () {
      this._redAlarmSound.stop()
      this._yellowAlarmSound.stop()
    }
  }
}
</script>

<style lang="scss" scoped>
.alarm-info {
  position: absolute; top: 0; left: 50%;
  transform: translateX(-50%);
  width: 1000px;
  font-size: 20px;

  .white-box {
    background: #fff; color: #000;
    display: flex; justify-content: center; align-items: center;
    width: 60%; height: 40px;
    margin: 0 auto;
  }

  .color-box {
    flex: 0 0 auto; width: calc(100% / 3);
    color: #000;
    display: flex; justify-content: center; align-items: center;
    opacity: 0;
    &.active { opacity: 1; }
  }

  .red-box { background: #FF3300; }
  .yellow-box { background: #FFFF00; }
  .blue-box { background: #33CCFF; }
}
</style>
