<template>
  <div class="ecg-bar-chart" :style="{ width, height }">
    <div class="bar-list">
      <div
        class="bar-item"
        v-for="(item, idx) of barList"
        :key="idx"
        :style="{
          width: item + '%',
          height: barWidth,
          background: barColor
        }">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '150px'
    },
    height: {
      type: String,
      default: '300px'
    },
    barColor: {
      type: String,
      default: '#fff'
    },
    barWidth: {
      type: String,
      default: '5px'
    },
    barList: {
      type: Array,
      default: function () {
        return []
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-bar-chart{
  position: relative;
  .bar-list{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
</style>
