<template>
  <div class="Setup">
    <ecg-label-box class="Operator d-flex mb-40" label="Operator">
      <div class="d-flex">
        <ecg-button width="120px" height="40px" @click="$dialog.open('Setup', [0, 0])">Screen Select</ecg-button>
        <ecg-button width="120px" height="40px" @click="$dialog.open('Setup', [1, 0])">Sound/<br>Display</ecg-button>
        <ecg-button width="120px" height="40px" @click="$dialog.open('Setup', [2, 0])">Record</ecg-button>
        <ecg-button width="120px" height="40px" @click="$dialog.open('Setup', [3, 0])">Remote Cntl</ecg-button>
      </div>
      <ecg-button width="120px" height="40px">Remove USB</ecg-button>
    </ecg-label-box>
    <ecg-label-box class="Administrator d-flex" label="Administrator">
      <div class="Administrator-left">
        <ecg-box class="mb-">
          <ecg-label>Master</ecg-label>
          <ecg-button class="mb-20" width="120px" height="40px" disabled>Patient Type</ecg-button>
          <div class="d-flex mb-20">
            <ecg-button width="120px" height="40px" disabled>Adult</ecg-button>
            <ecg-button width="120px" height="40px" disabled>Child</ecg-button>
            <ecg-button width="120px" height="40px" disabled>Neonate</ecg-button>
          </div>
          <ecg-button width="120px" height="40px" disabled>Screen Layout<br>Select</ecg-button>
        </ecg-box>
        <div class="d-flex ml-10">
          <ecg-button width="120px" height="40px" disabled>Settings</ecg-button>
          <ecg-button width="120px" height="40px" disabled>Maintenance</ecg-button>
        </div>
      </div>
      <div class="Administrator-right d-flex">
        <div class="tip mb-20">The system setting screen opens when<br>you enter the password.</div>
        <div class="inputs d-flex mb-20">
          <div class="input d-flex flex-center" :class="{ active: activeInput === 1 }" @click="activeInput = 1">{{ input1 }}</div>
          <div class="input d-flex flex-center" :class="{ active: activeInput === 2 }" @click="activeInput = 2">{{ input2 }}</div>
          <div class="input d-flex flex-center" :class="{ active: activeInput === 3 }" @click="activeInput = 3">{{ input3 }}</div>
          <div class="input d-flex flex-center" :class="{ active: activeInput === 4 }" @click="activeInput = 4">{{ input4 }}</div>
        </div>
        <ecg-box>
          <ecg-number-keyboard @press="handlePress"/>
        </ecg-box>
      </div>
    </ecg-label-box>
  </div>
</template>

<script>
import { startsWith as _startsWith } from 'lodash'

export default {
  data () {
    return {
      activeInput: 1,
      input1: '',
      input2: '',
      input3: '',
      input4: ''
    }
  },
  methods: {
    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this[`input${this.activeInput}`] = ''
        } else if (key === 'CTRL:BS') {
          this[`input${this.activeInput}`] = this[`input${this.activeInput}`].slice(0, this[`input${this.activeInput}`].length - 1)
        }
      } else {
        // 普通字符
        this[`input${this.activeInput}`] = key
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.Setup{
  padding-top: 20px;
  .Operator{
    padding: 20px 40px 20px 10px;
    justify-content: space-between;
  }
  .Administrator{
    &-left{

    }
    &-right{
      width: 100%;
      flex-direction: column;
      align-items: center;
      .inputs{
        .input{
          width: 50px;
          height: 50px;
          margin: 0 5px;
          background-color: #000;
          box-sizing: border-box;
          &.active { border: 1px solid #009DDC; }
        }
      }
    }
  }
}
</style>
