<template>
<ecg-embeded-dialog class="notes-dlg" ref="dlg" title="Notes">
  <div class="input-box">{{ notesCache }}</div>
  <ecg-keyboard class="mx-auto" @press="handlePress"/>
  <ecg-button class="set" @click="set">Set</ecg-button>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { startsWith as _startsWith } from 'lodash'

export default {
  data () {
    return {
      notesCache: ''
    }
  },

  computed: {
    ...mapState('patient', ['notes'])
  },

  methods: {
    ...mapMutations('patient', ['SET_NOTES']),

    open () {
      this.$refs.dlg.open()
      this.notesCache = this.notes
    },

    set () {
      this.SET_NOTES(this.notesCache)
      this.$refs.dlg.close()
    },

    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:BS') {
          this.notesCache = this.notesCache.slice(0, this.notesCache.length - 1)
        } else if (key === 'CTRL:DEL') {
          this.notesCache = ''
        }
      } else {
        // 普通字符
        this.notesCache = this.notesCache.concat(key)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.notes-dlg {
  .input-box {
    box-sizing: border-box;
    padding: 10px;
    border: 2px inset #999;
    background: #fff; color: #000;
    height: 160px;
    margin: 0 150px 20px;
  }
  .set {
    margin: 20px 90px 0 auto;
  }
}
</style>
