<template>
<div class="ecg-col" :style="{ width }">
  <slot/>
</div>
</template>

<script>
export default {
  props: {
    span: { type: Number, required: true }
  },

  computed: {
    width () {
      return `calc(${this.span / 12 * 100}% - 10px)`
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-col {
  margin: 0 5px;
  flex: 0 0 auto;
}
</style>
