<template>
  <div class="Weight">
    <div class="Weight-header">
      <div class="input">
        <div class="input-box">{{ input }}</div>
        <div class="unit">[kg]</div>
      </div>
      <div class="range">(0.1~449.9)</div>
    </div>
    <ecg-box>
      <ecg-number-keyboard @press="handlePress"/>
    </ecg-box>
  </div>
</template>

<script>
import {
  startsWith as _startsWith,
  find as _find
} from 'lodash'
export default {
  data () {
    return {
      input: ''
    }
  },
  mounted () {
    const parent = this.$parent
    this.input = _find(parent.inputs, o => parent.currentInput === o.id).value
  },
  methods: {
    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this.input = ''
        } else if (key === 'CTRL:BS') {
          this.input = this.input.slice(0, this.input.length - 1)
        }
      } else {
        // 普通字符
        this.input = this.input.concat(key)
      }
      this.changeParentInput()
    },
    changeParentInput () {
      const parent = this.$parent
      parent.$set(_find(parent.inputs, o => parent.currentInput === o.id), 'value', this.input)
    }
  }
}
</script>

<style lang="scss" scoped>
.Weight{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 60px;
  &-header{
    margin-left: 30px;
    margin-bottom: 5px;
    .input{
      display: flex;
      align-items: flex-end;
      &-box{
        height: 40px;
        width: 250px;
        background-color: #fff;
        border: 2px inset #999;
        color: #000;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 24px;
      }
      .unit{
        color: #000;
        font-weight: bold;
      }
    }
    .range{
      width: 260px;
      text-align: center;
      color: #000;
      font-weight: bold;
    }
  }
}
</style>
