<template>
<div class="Discharge">
  <ecg-row>
    <ecg-col :span="10">
      <div class="d-flex mb-10">
        <div class="form-label">ID</div>
        <div class="input-box input-box-id">{{ id }}</div>
      </div>
      <div class="d-flex mb-10">
        <div class="form-label">Name</div>
        <div class="input-box input-box-name">{{ name }}</div>
      </div>
      <div class="d-flex mb-10">
        <div class="form-label">Birth Date</div>
        <div class="input-box input-box-birth-date">
          <span>{{ birthDateText }}</span>
          <span class="age">Age</span>
          <span class="yr">{{ age[0] }} Yr</span>
          <span class="mo">{{ age[1] }} Mo</span>
          <span class="d">{{ age[2] }} D</span>
        </div>
      </div>
      <div class="d-flex mb-10">
        <div class="form-label">Patient Type</div>
        <div class="input-box input-box-patient-type">{{ type }}</div>
      </div>
      <div class="d-flex mb-10">
        <div class="form-label">Height / Weight</div>
        <div class="input-box input-box-height-weight">
          <span class="cm">{{ height }} [cm]</span>
          <span class="kg">{{ weight }} [kg]</span>
          <span class="bsa">BSA</span>
          <span class="m2">{{ bsa }} [m²]</span>
        </div>
      </div>
      <div class="d-flex mb-10">
        <div class="d-flex">
          <div class="form-label">Gender</div>
          <div class="input-box input-box-gender">{{ gender }}</div>
        </div>
        <div class="d-flex ml-auto">
          <div class="form-label">Blood</div>
          <div class="input-box input-box-blood">{{ blood }}</div>
        </div>
      </div>
      <div class="d-flex mb-10">
        <div class="form-label">Pacemaker</div>
        <div class="input-box input-box-pacemaker">{{ pacemakerText }}</div>
      </div>
      <div class="d-flex">
        <div class="form-label">Notes</div>
        <div class="input-box input-box-notes">{{ notes }}</div>
      </div>
    </ecg-col>
    <ecg-col :span="2">
      <ecg-button class="ml-auto" @click="openDlg('discharge-dlg')">Discharge</ecg-button>
    </ecg-col>
  </ecg-row>

  <!-- 表单弹框 -->
  <discharge-dlg ref="discharge-dlg"/>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import DischargeDlg from './dialogs/discharge-dlg'

export default {
  components: { DischargeDlg },

  computed: {
    ...mapState('patient', ['id', 'name', 'age', 'type', 'height', 'weight', 'bsa', 'gender', 'blood', 'pacemaker', 'notes']),
    ...mapGetters('patient', ['birthDateText', 'pacemakerText'])
  },

  methods: {
    openDlg (name) {
      this.$refs[name].open()
    }
  }
}
</script>

<style lang="scss" scoped>
.Discharge {
  position: relative;
  .form-label {
    flex: 0 0 auto;
    width: 120px; height: 40px;
    text-align: center;
    font-weight: bold;
    color: #000;
    display: flex; justify-content: center; align-items: center;
  }
  .input-box {
    box-sizing: border-box;
    border: 1px solid #666;
    background: #fff; color: #000;
    font-size: 18px;
    height: 40px; line-height: 40px;
    padding: 0 10px;

    &-id { width: 50%; }
    &-name { flex: 1 1 auto; }
    &-birth-date {
      flex: 1 1 auto;
      position: relative;
      span {
        position: absolute; top: 50%;
        transform: translateY(-50%);
        font-size: 14px; font-weight: bold;
      }
      .age { left: 40%; }
      .yr { left: 50%; }
      .mo { left: 65%; }
      .d { left: 80%; }
    }
    &-patient-type { width: 120px; text-align: center; }
    &-height-weight {
      flex: 1 1 auto;
      position: relative;
      span {
        position: absolute; top: 50%;
        transform: translateY(-50%);
        font-size: 14px; font-weight: bold;
      }
      .cm { left: 20%; }
      .kg { left: 40%; }
      .bsa { left: 70%; }
      .m2 { left: 80%; }
    }
    &-gender { width: 120px; text-align: center; }
    &-blood { width: 120px; text-align: center; }
    &-pacemaker { width: 120px; text-align: center; }
    &-notes { flex: 1 1 auto; height: 240px; }
  }
}
</style>
