<template>
<div class="CarSeatChallengeMenu">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState('carseat', ['hasResult']),

    tabTree () {
      return [
        { type: 'item', text: 'Test', component: require('@/dialogs/CarSeatChallenge/Test').default },
        { type: 'item', text: 'Result', disabled: !this.hasResult, component: require('@/dialogs/CarSeatChallenge/Result').default }
      ]
    }
  },

  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
