<template>
  <div class="ecg-scale">
    <div class="scale" :style="{ backgroundColor: scaleColor }">
      <div
        class="number-stick"
        v-for="(item, idx) of scale"
        :key="idx"
        :style="{ bottom: `calc(${position[idx]} - 7px)` }">
        <div class="number" :style="{ color: scaleColor }">
          {{ isFloat ? item.toFixed(1) : item }}
        </div>
        <div
          class="stick"
          :style="{ backgroundColor: scaleColor }">
        </div>
      </div>
    </div>
    <div class="label-list">
      <div class="label-item" v-for="(item, idx) of labels" :key="idx" :style="{ color: item.color }" v-html="item.text"></div>
    </div>
  </div>
</template>

<script>
import { map as _map } from 'lodash'
export default {
  props: {
    isFloat: {
      type: Boolean,
      default: false
    },
    labels: {
      type: Array,
      default: function () {
        return []
      }
    },
    scale: {
      type: Array,
      default: function () {
        return []
      }
    },
    scaleColor: {
      type: String,
      default: '#fff'
    }
  },
  computed: {
    position () {
      const length = this.scale[this.scale.length - 1] - this.scale[0]
      return _map(this.scale, (item, idx) => {
        return (((item - this.scale[0]) / length) * 100) + '%'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-scale{
  width: 80px;
  height: 100%;
  position: relative;
  .scale{
    width: 2px;
    height: 92%;
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    .number-stick{
      display: flex;
      align-items: center;
      height: 14px;
      position: absolute;
      right: 0px;
      &:first-of-type{
        >.number{
          position: relative;
          bottom: 5px;
        }
      }
      &:last-of-type{
        >.number{
          position: relative;
          top: 5px;
        }
      }
      .stick{
        width: 5px;
        height: 2px;
        margin-left: 2px;
      }
      .number{
        font-size: 12px;
      }
    }
  }
  .label-list{
    position: absolute;
    left: 0;
    top: 20%;
    .label-item{
      font-weight: bold;
      &:not(:last-of-type){
        margin-bottom: 20px;
      }
    }
  }
}
</style>
