<template>
  <div class="Display1" @click.stop>
    <ecg-button
      width="340px"
      height="30px"
      v-for="(item, idx) of btns"
      :key="idx"
      :active="active === item.text"
      @click="handleClick(item)">
      {{ item.text }}
    </ecg-button>
  </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
export default {
  data () {
    return {
      active: '',
      btns: [
        { text: 'L NONE', tag: 'lnone' },
        { text: 'Screen 11', tag: '' },
        { text: 'R NONE', tag: '' },
        { text: 'Screen 12', tag: '' },
        { text: 'L SIDE', tag: 'lside' },
        { text: 'Screen 13', tag: '' },
        { text: 'R SIDE', tag: '' },
        { text: 'Screen 14', tag: '' },
        { text: 'L BOTTOM', tag: 'lbutton' },
        { text: 'Screen 15', tag: '' },
        { text: 'R BOTTOM', tag: '' },
        { text: 'Screen 16', tag: '' },
        { text: 'SWAN INSERT', tag: 'swaninsert' },
        { text: 'Screen 17', tag: '' },
        { text: 'Advanced Interbed', tag: 'interbed' },
        { text: 'Screen 18', tag: '' },
        { text: 'Screen 9', tag: '' },
        { text: 'Screen 19', tag: '' },
        { text: 'Screen 10', tag: '' },
        { text: 'Screen 20', tag: '' }
      ]
    }
  },
  mounted () {
    this.active = this.$parent.input1
  },
  methods: {
    ...mapMutations('home', ['SET_CURRENT_MAIN']),
    ...mapActions('app', ['flashBlackScreen']),
    handleClick (item) {
      this.active = item.text
      this.$parent.input1 = item.text
      this.$parent.open = false
      if (item.tag === 'interbed') {
        this.SET_CURRENT_MAIN('main-5')
        this.$dialog.close()
      } else if (item.tag === 'lnone') {
        this.SET_CURRENT_MAIN('main-1')
        this.$root.$children[0].$children[0].changeButton(1)
        this.$dialog.close()
      } else if (item.tag === 'lside') {
        this.SET_CURRENT_MAIN('main-3')
        this.$root.$children[0].$children[0].changeButton(2)
        this.$dialog.close()
      } else if (item.tag === 'lbutton') {
        this.SET_CURRENT_MAIN('main-2')
        this.$root.$children[0].$children[0].changeButton(3)
        this.$dialog.close()
      } else if (item.tag === 'swaninsert') {
        this.SET_CURRENT_MAIN('main-4')
        this.$dialog.close()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.Display1{
  display: flex;
  flex-wrap: wrap;
  .ecg-button{
    margin-bottom: 2px;
    &:nth-of-type(2n-1){
      margin-right: 10px;
    }
  }
}
</style>
