<template>
<ecg-embeded-dialog class="height-weight-dlg" ref="dlg" title="Height / Weight">
  <div class="wrapper">
    <div class="hw-box height" :class="{ active: activeInput === 1 }" @click="activeInput = 1">
      <span class="text">{{ input1 }}</span>
      <div class="t-1">Height</div>
      <div class="t-2">(5.0 ~ 300.0)</div>
      <div class="t-3">[cm]</div>
    </div>
    <div class="hw-box weight" :class="{ active: activeInput === 2 }" @click="activeInput = 2">
      <span class="text">{{ input2 }}</span>
      <div class="t-1">Weight</div>
      <div class="t-2">(0.1 ~ 449.9)</div>
      <div class="t-3">[kg]</div>
    </div>
    <div class="hw-box bsa" :class="{ active: activeInput === 3 }" @click="activeInput = 3">
      <span class="text">{{ input3 }}</span>
      <div class="t-1">BSA</div>
      <div class="t-3">[m²]</div>
    </div>
    <ecg-box class="nk">
      <ecg-number-keyboard @press="handlePress"/>
    </ecg-box>
    <ecg-button class="set" @click="set">Set</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { startsWith as _startsWith } from 'lodash'
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      activeInput: 1,
      input1: '',
      input2: '',
      input3: ''
    }
  },

  computed: {
    ...mapState('patient', ['height', 'weight', 'bsa'])
  },

  methods: {
    ...mapMutations('patient', ['SET_HEIGHT', 'SET_WEIGHT', 'SET_BSA']),

    open () {
      this.$refs.dlg.open()
      this.activeInput = 1
      this.input1 = this.height
      this.input2 = this.weight
      this.input3 = this.bsa
    },

    set () {
      this.SET_HEIGHT(this.input1)
      this.SET_WEIGHT(this.input2)
      this.SET_BSA(this.input3)
      this.$refs.dlg.close()
    },

    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this[`input${this.activeInput}`] = ''
        } else if (key === 'CTRL:BS') {
          this[`input${this.activeInput}`] = this[`input${this.activeInput}`].slice(0, this[`input${this.activeInput}`].length - 1)
        }
      } else {
        // 普通字符
        this[`input${this.activeInput}`] = this[`input${this.activeInput}`].concat(key)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.height-weight-dlg {
  .wrapper {
    position: relative;
    height: 100%;
  }
  .hw-box {
    width: 100px; height: 80px;
    box-sizing: border-box;
    background: #fff;
    border: 4px inset #999;
    color: #000;
    font-weight: bold;
    position: relative;
    display: flex; justify-content: center; align-items: center;
    &.active { border-color: #009DDC; }
    .text { font-size: 18px; }
    .t-1 { position: absolute; right: calc(100% + 10px); top: 50%; transform: translateY(-50%); }
    .t-2 { position: absolute; left: 0; top: calc(100% + 5px); }
    .t-3 { position: absolute; left: calc(100% + 5px); bottom: 0; }
  }
  .height { position: absolute; left: 25%; top: 20px; }
  .weight { position: absolute; left: 25%; top: 140px; }
  .bsa {
    height: 40px;
    position: absolute; right: 25%; top: 160px;
  }
  .nk { position: absolute; left: 25%; bottom: 20px; }
  .set { position: absolute; right: 25%; bottom: 10px; }
}
</style>
