<template>
  <div class="ecg-scale4" :style="{ height }">
    <div class="scale">
      <div class="text-stick"
          v-for="(item, idx) of positionScale"
          :key="idx"
          :style="{ bottom: `calc(${item.position} - 7px)` }">
        <div class="text">{{ item.text }}</div>
        <div class="stick"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    height: {
      type: String,
      default: '300px'
    },
    scale: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  computed: {
    positionScale () {
      return this.scale.map((item, idx) => {
        return {
          text: item,
          position: this.position(idx)
        }
      })
    }
  },
  methods: {
    position (idx) {
      const length = this.scale.length
      return (100 / (length - 1)) * idx + '%'
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-scale4{
  position: relative;
  width: 100px;
  .scale {
    border: 2px solid #fff;
    width: 10px; height: 100%;
    position: absolute; left: 60%; top: 50%;
    transform: translateX(-50%) translateY(-50%);
    .text-stick {
      display: flex; align-items: center;
      height: 14px;
      position: absolute; right: 0px;
      .stick {
        width: 10px; height: 2px;
        margin-left: 10px;
        background-color: #fff;
      }
      .text { font-weight: bold; width: 30px; text-align: right;}
    }
  }
  .label-list {
    position: absolute; left: 0; top: 20%;
    .label-item { font-weight: bold; }
  }
}
</style>
