<template>
  <div class="ecg-scale2" :style="{ height }">
    <div class="scale">
      <div class="number-stick-left"
          v-for="(item, idx) of leftScale"
          :key="idx + 'left'"
          :style="{ bottom: `calc(${item.position} - 7px)` }">
        <div class="number">{{ item.number }}</div>
        <div class="stick" :style="{background: showStick ? '#fff' : 'transparent' }"></div>
      </div>
      <div class="number-stick-right"
          v-for="(item, idx) of rightScale"
          :key="idx + 'right'"
          :style="{ bottom: `calc(${item.position} - 7px)` }">
        <div class="stick" :style="{background: showStick ? '#fff' : 'transparent' }"></div>
        <div class="number">{{ item.number }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    showStick: {
      type: Boolean,
      default: true
    },
    height: {
      type: String,
      default: '300px'
    },
    doubleLine: {
      type: Boolean,
      default: false
    },
    scale: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  computed: {
    leftScale () {
      if (this.doubleLine) {
        return this.scale.filter((item, idx) => {
          return idx % 2 === 0
        }).map((item, idx) => {
          return {
            number: item,
            position: this.position(item)
          }
        })
      } else {
        return this.scale.map((item, idx) => {
          return {
            number: item,
            position: this.position(item)
          }
        })
      }
    },
    rightScale () {
      if (this.doubleLine) {
        return this.scale.filter((item, idx) => {
          return idx % 2 !== 0
        }).map((item, idx) => {
          return {
            number: item,
            position: this.position(item)
          }
        })
      } else {
        return []
      }
    }
  },
  methods: {
    position (value) {
      const length = this.scale[this.scale.length - 1] - this.scale[0]
      return (((value - this.scale[0]) / length) * 100) + '%'
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-scale2 {
  position: relative;
  width: 100px;
  .scale {
    border: 2px solid #fff;
    width: 10px; height: 100%;
    position: absolute; left: 50%; top: 50%;
    transform: translateX(-50%) translateY(-50%);
    .number-stick-left {
      display: flex; align-items: center;
      height: 14px;
      position: absolute; right: 0px;
      .stick {
        width: 10px; height: 2px;
        margin-left: 10px;
        background-color: #fff;
      }
      .number { font-weight: bold; }
    }
    .number-stick-right {
      display: flex; align-items: center;
      height: 14px;
      position: absolute; left: 0px;
      .stick {
        width: 10px; height: 2px;
        margin-right: 10px;
        background-color: #fff;
      }
      .number { font-weight: bold; }
    }
  }
  .label-list {
    position: absolute; left: 0; top: 20%;
    .label-item { font-weight: bold; }
  }
}
</style>
