<template>
  <div class="main-3">
    <draggable @choose="handleChoose" @unchoose="handleUnchoose" @change="handleChange" :list="draggablePart" class="part-wrapper" :delay="1000">
      <component v-for="c of draggablePart" :key="c[0]" :is="c[1]" ref="part"></component>
    </draggable>
    <div class="hammer" ref="hammer">
      <component :is="hammerList[currentHammer]"></component>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import Hammer from 'hammerjs'
export default {
  components: { draggable },
  data () {
    return {
      draggablePart: [
        [1, require('./main-3/part1').default],
        [2, require('./main-3/part2').default],
        [3, require('./main-3/part3').default],
        [4, require('./main-3/part4').default],
        [5, require('./main-3/part5').default]
      ],
      currentHammer: 0,
      hammerList: [
        require('./main-3/hammer/hammer1').default,
        require('./main-3/hammer/hammer2').default,
        require('./main-3/hammer/hammer3').default
      ]
    }
  },
  mounted () {
    this.hammer = new Hammer(this.$refs.hammer)
    this.hammer.get('swipe').set({
      direction: Hammer.DIRECTION_ALL
    })
    this.hammer.on('swipe', this.handleSwipe)
  },
  beforeDestroy () {
    this.hammer.destroy()
  },
  methods: {
    handleChoose (e) {
      this.$refs.part[e.oldIndex].drag = true
    },
    handleUnchoose (e) {
      this.$refs.part[e.oldIndex].drag = false
    },
    handleChange (e) {
      const moveEl = this.$refs.part.splice(e.moved.oldIndex, 1)[0]
      this.$refs.part.splice(e.moved.newIndex, 0, moveEl)
    },
    handleSwipe (e) {
      if (e.direction === Hammer.DIRECTION_RIGHT || e.direction === Hammer.DIRECTION_DOWN) {
        if (this.currentHammer > 0) {
          this.currentHammer -= 1
        } else {
          this.currentHammer = 2
        }
      } else if (e.direction === Hammer.DIRECTION_LEFT || e.direction === Hammer.DIRECTION_UP) {
        if (this.currentHammer < 2) {
          this.currentHammer += 1
        } else {
          this.currentHammer = 0
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.main-3{
  height: 100%;
  display: flex;
  .part-wrapper{
    height: 100%;
    width: 75%;
    .part1{ height: calc(100% * 3 / 17); }
    .part2{ height: calc(100% * 5 / 17); }
    .part3{ height: calc(100% * 3 / 17); }
    .part4{ height: calc(100% * 3 / 17); }
    .part5{ height: calc(100% * 3 / 17); }
  }
  .hammer{
    height: 100%;
    width: 25%;
    &-1{}
    &-2{}
    &-3{}
  }
}
</style>
