<template>
  <div class="part3 d-flex">
    <div class="info d-flex">
      <div class="info-part1">
        <div class="info-part1-1 c-white">RR</div>
        <div class="info-part1-2 c-white">CO<sub>2</sub>-E</div>
        <div class="info-part1-3 c-white">12</div>
        <div class="info-part1-4 c-white">4.8</div>
      </div>
      <div class="info-part2">
        <div class="info-part2-1 c-yellow">PAP</div>
        <div class="info-part2-2 c-yellow">5.6/ 3.1</div>
        <div class="info-part2-3 c-yellow">( 4.3)</div>
      </div>
      <div class="drag-info" v-if="drag">RR</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#B4B45A', width: 1, value: 0 },
          { color: '#B4B45A', width: 1, value: 100 },
          { color: '#B4B45A', width: 1, value: -100 }
        ]"/>
      <div class="chart-info">
        <div class="chart-info-1 c-purple">CVP<br>5.3</div>
        <div class="chart-info-2 c-yellow">PAP<br>5.3</div>
      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'

export default {
  data () {
    return {
      drag: false
    }
  },
  methods: {
    genData () {
      const data1 = _map(new Array(19 * 5), (item, idx) => {
        switch (idx % 5) {
          case 0:
            return _random(-52, -48)
          case 1:
            return _random(-77, -73)
          case 2:
            return -95
          case 3:
            return _random(23, 27)
          case 4:
            return _random(-27, -23)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FFFF00',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part3{
  .info{
    height: 100%;
    width: 25%;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FFFFFF;
      background-color: #333;
      color: #FFFFFF;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    >div{
      box-sizing: border-box;
      position: relative;
      border-bottom: 1px solid #808080;
      border-right: 1px solid #808080;
      >div{
        position: absolute;
        text-align: right;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
    }
    &-part1{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 16px; left: 50%; top: 0px; }
      &-3{ font-size: 36px; right: calc(50% + 5px); top: 10px; }
      &-4{ font-size: 36px; right: 5px; top: 10px; }
    }
    &-part2{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 36px; right: 40%; top: 10px; }
      &-3{ font-size: 36px; right: 5px; top: 10px; }
    }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
      &-2{ right: 10px; top: 10px; }
      &-3{ right: 10px; bottom: 10px; }
    }
  }
}
</style>
