<template>
<div class="Threshold">
  <ecg-box class="box-1">
    <ecg-label>EMG Waveform Detection Threshold [mVp-p]</ecg-label>
    <div class="box-2">
      <div class="bg-black py-40 pl-20">
        <ecg-scale2 :scale="['0.30', '1.00', '1.50', '2.00', '2.50', '3.00']"/>
      </div>
      <ecg-slider-single
        @update:value="changeValue($event)"
        :value="EMGWaveform"
        height="380px"
        :dotOptions="{ min: 10, max: 90 }"
        :dotType="2"/>
      <ecg-counter class="ml-40" :max="3" :step="0.1" :value="valuedata" @input="changeValue2($event)"/>
    </div>
  </ecg-box>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('tof', [
      'EMGWaveform'
    ]),
    valuedata () {
      return parseFloat((3 - 0.3) * (this.EMGWaveform - 10) / 80)
    }
  },
  methods: {
    ...mapMutations('tof', [
      'CHANGE_EMG_WAVEFORM'
    ]),
    changeValue (value) {
      this.CHANGE_EMG_WAVEFORM(value)
    },
    changeValue2 (value) {
      const n = parseFloat(value * (90 - 10) / (3 - 0.3) + 10)
      if (n < 90 && n > 10) {
        this.CHANGE_EMG_WAVEFORM(n)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.Threshold {
  .box-1 { padding: 40px 80px; }
  .box-2 { display: flex; align-items: center; }
}
</style>
