<template>
<div class="ICP">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box>
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData" :lines="[
            { color: '#990', width: 1, value: 80 },
            { color: '#990', width: 1, value: 40 },
            { color: '#990', width: 1, value: 0 },
            { color: '#990', width: 1, value: -40 },
            { color: '#990', width: 1, value: -80 }
          ]"/>
          <span class="t-1 c-yellow">40</span>
          <span class="t-2 c-yellow">0</span>
        </div>

        <ecg-label>Zero Cal</ecg-label>
        <ecg-button class="mb-20">Zero</ecg-button>
        <ecg-label>
          <span class="mr-40">-- - -- - ---- --:--</span>
          Not Zero
        </ecg-label>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="h-100">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['16:00', '18:00']" :y="[0, 50, 100, 150]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10 c-yellow" content="37" text1="Mean" text2="[mmHg]"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box-list class="c-btn-list" :list="[
          [
            { title: 'Mean', content: '37', text1: 'Off', text2: 'Off' }
          ]
        ]"/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
export default {
  methods: {
    leftData () {
      return [
        {
          color: '#FF0',
          width: 2,
          data: [85]
        }
      ]
    },

    rightData () {
      return [
        {
          color: 'rgba(200, 200, 0, 0.3)',
          area: -50,
          data: [-30]
        },
        {
          color: '#ff0',
          width: 2,
          data: [-30]
        },
        {
          color: '#ff0',
          width: 2,
          data: [-40]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.ICP {
  .left-chart {
    height: 330px;
    position: relative;
    span {
      font-size: 16px; font-weight: bold;
      position: absolute;
      &.t-1 { left: 10px; top: 30px; }
      &.t-2 { left: 10px; bottom: 30px; }
    }
  }
  .c-btn-list /deep/ .ecg-alarm-box-content { color: #FF0; }
}
</style>
