<template>
<div class="SlowTOF">
  <ecg-box class="box-1">
    <ecg-label>Stimulation Duration [min]</ecg-label>
    <div class="box-2">
      <div class="bg-black py-40">
        <ecg-scale2 :scale="[1, 10, 20, 30, 40, 50, 60]"/>
      </div>
      <ecg-slider-single
        @update:value="changeValue($event)"
        :value="stimulationDuration"
        height="380px"
        :dotOptions="{ min: 10, max: 90 }"
        :dotType="2"/>
      <ecg-counter class="ml-40" :max="60" :value="valuedata" @input="changeValue2($event)"/>
    </div>
  </ecg-box>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('tof', [
      'stimulationDuration'
    ]),
    valuedata () {
      return Math.round(60 * (this.stimulationDuration - 10) / (90 - 10))
    }
  },
  methods: {
    ...mapMutations('tof', [
      'CHANGE_STIMULATION_DURATION'
    ]),
    changeValue (value) {
      this.CHANGE_STIMULATION_DURATION(value)
    },
    changeValue2 (value) {
      const n = Math.round(value / 60 * (90 - 10) + 10)
      this.CHANGE_STIMULATION_DURATION(n)
    }
  }
}
</script>

<style lang="scss" scoped>
.SlowTOF {
  .box-1 { padding: 40px 80px; }
  .box-2 { display: flex; align-items: center; }
}
</style>
