    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#FFFFFF', width: 1, value: 40 },
          { color: '#FFFFFF', width: 1, value: 98 },
          { color: '#FFFFFF', width: 1, value: -18 },
