<script>
import { random as _random, map as _map } from 'lodash'
export default {
  data () {
    return {
      drag: false
    }
  },
  methods: {
    genData () {
      const data1 = _map(new Array(7 * 10), (item, idx) => {
        switch (idx % 10) {
          case 0:
            return -18
          case 1:
            return -18
          case 2:
            return -18
          case 3:
            return -18
          case 4:
            return -18
          case 5:
            return _random(80, 85)
          case 6:
            return _random(85, 90)
          case 7:
            return _random(90, 95)
          case 8:
            return _random(95, 100)
          case 9:
            return _random(85, 90)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FFFFFF',
          width: 2,
          data: data1,
          area: -18
        }
      ]
    }
  }
}
</script>

