<template>
<div class="ECGAnalysis-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        { type: 'item', text: 'ECG<br>Analysis', component: require('@/dialogs/ECGAnalysis/ECGAnalysis').default }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
