<template>
<div class="StimulationSensitivity">
  <ecg-box>
    <ecg-row>
      <ecg-col :span="6">
        <div class="box-1">
          <ecg-label>Stimulation Current [mA]</ecg-label>
          <div class="box-2">
            <div class="bg-black py-40 pl-20">
              <ecg-scale2 :scale="[1, 10, 20, 30, 40, 50, 60]"/>
            </div>
            <ecg-slider-single
              height="380px"
              @update:value="changeValue2('value1', $event)"
              :value="stimulationCurrent"
              :dotOptions="{ min: 10, max: 90 }"
              :dotType="2"/>
            <ecg-counter class="ml-40" :max="60" :value="value1data" @input="changeValue('value1', $event)"/>
          </div>
        </div>
      </ecg-col>
      <ecg-col :span="6">
        <div class="box-1">
          <ecg-label>Sensitivity [mVp-p]</ecg-label>
          <div class="box-2">
            <div class="bg-black py-40 pl-20">
              <ecg-scale2 :scale="['0.30', '10.00', '20.00', '30.00', '40.00', '50.00']"/>
            </div>
            <ecg-slider-single
              height="380px"
              @update:value="changeValue2('value2', $event)"
              :value="sensitivity"
              :dotOptions="{ min: 10, max: 90 }"
              :dotType="2"/>
            <ecg-counter class="ml-40" :max="50" :value="value2data" @input="changeValue('value2', $event)"/>
          </div>
        </div>
      </ecg-col>
    </ecg-row>

    <div style="padding-left: 70px">
      <ecg-label>Pulse width</ecg-label>
      <ecg-button-list :list="[['200 μs', '300 μs']]" @change="handleChange"/>
    </div>
  </ecg-box>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
    }
  },

  computed: {
    ...mapState('tof', [
      'stimulationCurrent',
      'sensitivity'
    ]),
    value1data () {
      return Math.round(60 * (this.stimulationCurrent - 10) / (90 - 10))
    },

    value2data () {
      return Math.round(50 * (this.sensitivity - 10) / (90 - 10))
    }
  },

  methods: {
    ...mapMutations('tof', [
      'CHANGE_STIMULATION_CURRENT',
      'CHANGE_SENSITIVITY',
      'SET_TIME_ACTIVE'
    ]),
    changeValue (field, value) {
      let n
      if (field === 'value1') {
        n = value / 60 * (90 - 10) + 10
        this.CHANGE_STIMULATION_CURRENT(Math.round(n))
      } else {
        n = value / 50 * (90 - 10) + 10
        this.CHANGE_SENSITIVITY(Math.round(n))
      }
      this.SET_TIME_ACTIVE(false)
    },

    changeValue2 (field, value) {
      if (field === 'value1') {
        this.CHANGE_STIMULATION_CURRENT(value)
      } else {
        this.CHANGE_SENSITIVITY(value)
      }
      this.SET_TIME_ACTIVE(false)
    },
    handleChange () {
      console.log(1)
      this.SET_TIME_ACTIVE(false)
    }
  }
}
</script>

<style lang="scss" scoped>
.StimulationSensitivity {
  .box-1 { padding: 40px 0 40px 70px; }
  .box-2 { display: flex; align-items: center; }
}
</style>
