<template>
<ecg-embeded-dialog class="name-dlg" ref="dlg" title="Name">
  <div class="wrapper">
    <div class="input-box">{{ nameCache }}</div>
    <ecg-keyboard class="mx-auto" @press="handlePress"/>
    <ecg-button class="set" width="100px" @click="set">Set</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { startsWith as _startsWith } from 'lodash'

export default {
  data () {
    return {
      nameCache: ''
    }
  },

  computed: {
    ...mapState('patient', ['name'])
  },

  methods: {
    ...mapMutations('patient', ['SET_NAME']),

    open () {
      this.$refs.dlg.open()
      this.nameCache = this.name
    },

    set () {
      this.SET_NAME(this.nameCache)
      this.$refs.dlg.close()
    },

    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:BS') {
          this.nameCache = this.nameCache.slice(0, this.nameCache.length - 1)
        } else if (key === 'CTRL:DEL') {
          this.nameCache = ''
        }
      } else {
        // 普通字符
        this.nameCache = this.nameCache.concat(key)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.name-dlg {
  .wrapper {
    height: 100%;
    position: relative;
    box-sizing: border-box;
    padding-top: 50px;
  }

  .input-box {
    background: #fff; color: #000;
    width: 500px; height: 40px;
    box-sizing: border-box;
    padding: 0 20px;
    margin: 0 auto 50px;
    border: 4px inset #999;
    display: flex; align-items: center;
  }

  .set {
    position: absolute; right: 86.5px; bottom: 40px;
  }
}
</style>
