<template>
<ecg-embeded-dialog class="patient-type-dlg" ref="dlg" title="Patient Type">
  <div class="row-1">
    <ecg-button class="mx-40" :active="typeCache === 'Adult'" @click="typeCache = 'Adult'">Adult</ecg-button>
    <ecg-button class="mx-40" :active="typeCache === 'Child'" @click="typeCache = 'Child'">Child</ecg-button>
    <ecg-button class="mx-40" :active="typeCache === 'Neonate'" @click="typeCache = 'Neonate'">Neonate</ecg-button>
  </div>
  <div class="row-2">
    <div>&lt;CAUTION&gt;</div>
    <div>Settings will be changed according to patient type.</div>
  </div>

  <template v-if="typeCache !== type">
    <div class="row-3">
      Alarm Master: Alarm Master 1
      <br>
      Change patient type to {{ typeCache }}?
    </div>
    <div class="row-4">
      <ecg-button @click="submit">Yes</ecg-button>
      <ecg-button @click="close">No</ecg-button>
    </div>
  </template>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      typeCache: null
    }
  },

  computed: {
    ...mapState('patient', ['type'])
  },

  methods: {
    ...mapMutations('patient', ['SET_TYPE']),

    open () {
      this.typeCache = this.type
      this.$refs.dlg.open()
    },

    submit () {
      this.SET_TYPE(this.typeCache)
      this.close()
    },

    close () {
      this.$refs.dlg.close()
      this.typeCache = null
    }
  }
}
</script>

