<template>
  <div class="ecg-tree-tab">
    <div
      v-for="(path, idx_row) of current"
      :key="idx_row"
      class="row">
      <div
        v-for="(item, idx_col) of getRow(idx_row)"
        :key="idx_col"
        class="col"
        :class="{ active: idx_col === path, disabled: item.disabled }"
        v-html="item.text"
        @click="changePath(item, idx_row, idx_col)">
      </div>
    </div>
    <div class="tab-page">
      <component v-if="currentComponent" :is="currentComponent"></component>
    </div>
  </div>
</template>

<script>
import { get as _get } from 'lodash'
export default {
  props: {
    tabTree: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      current: []
    }
  },
  computed: {
    currentComponent () {
      if (!this.current.length) { return null }
      let items = this.tabTree
      for (let i = 0; i < this.current.length - 1; i++) {
        items = _get(items, [this.current[i], 'children'])
      }
      return items[this.current[this.current.length - 1]].component
    }
  },
  mounted () {
    this.initCurrent()
  },
  methods: {
    initCurrent () {
      this.findPath(this.tabTree)
    },
    changeCurrent (path) {
      this.current = path
    },
    getRow (index) {
      const path = this.current.slice(0, index)
      if (path.length) {
        let items = this.tabTree
        for (let i = 0; i < path.length; i++) {
          items = _get(items, [path[i], 'children'])
        }
        return items
      } else {
        return this.tabTree
      }
    },
    changePath (item, row, col) {
      if (item.disabled) { return }

      if (item.type === 'dialog') {
        this.$dialog.open(item.name, item.path)
      } else {
        this.findIndex(item, row, col)
      }
    },
    findIndex (item, row, col) {
      this.current.splice(row + 1)
      this.$set(this.current, this.current.length - 1, col)
      if (item.type === 'group') {
        this.findPath(item.children)
      }
    },
    findPath (arr) {
      if (!arr[0]) {
        return
      } else {
        this.current.push(0)
      }
      if (arr[0].type === 'group') {
        this.findPath(arr[0].children)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-tree-tab{
  .row{
    display: flex;
    border-bottom: 3px solid #009DDC;
    &:not(:first-of-type){
      padding-top: 5px;
      background-color: #C6C6D1;
    }
    &:nth-of-type(3){
      .col{
        height: 30px;
      }
    }
    .col{
      background-image: linear-gradient(to bottom, #9E9EA2 10%, #72727B 50%);
      width: 100px;
      height: 50px;
      border-top-right-radius: 10px;
      border: 2px solid #9E9EA2;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 14px;
      box-sizing: border-box;
      &.active{
        background-image: linear-gradient(to bottom, #009DDC 10%, #0078A8 50%);
        border-style: inset;
        border: 2px solid #009DDC;
        -webkit-tap-highlight-color: transparent;
      }
      &:active{
        border-style: inset;
        -webkit-tap-highlight-color: transparent;
      }
      &.disabled {
        background: #363638;
        border: none;
        color: #95959D;
      }
    }
  }
  .tab-page{
    background-color: #C6C6D1;
    padding: 20px 10px;
    min-height: 700px;
  }
}
</style>
