<template>
  <div class="part8 d-flex">
    <div class="info d-flex">
      <div class="info-part1">
        <div class="info-part1-1 c-white">CO</div>
        <div class="info-part1-2 c-white">---</div>
        <div class="info-part1-3 c-white">PCWP</div>
        <div class="info-part1-4 c-white">---</div>
        <div class="info-part1-5 c-white">---</div>
        <div class="info-part1-6 c-white">---</div>
      </div>
      <div class="info-part2">
        <div class="info-part2-1 c-white">MV</div>
        <div class="info-part2-2 c-white">TVe</div>
        <div class="info-part2-3 c-white">4.20</div>
        <div class="info-part2-4 c-white">3700</div>
      </div>
      <div class="drag-info" v-if="drag">CO</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#969696', width: 1, value: 0 },
          { color: '#969696', width: 1, value: 98 },
          { color: '#969696', width: 1, value: -98 },
          { color: '#969696', width: 1, value: 49 },
          { color: '#969696', width: 1, value: -49 },
        ]"/>
      <div class="chart-info">
        <div class="chart-info-1 c-white">Flow</div>
        <div class="chart-info-2 c-white">1.0</div>
        <div class="chart-info-3 c-white">Paw</div>
        <div class="chart-info-4 c-white">50</div>
      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
export default {
  data () {
    return {
      drag: false
    }
  },
  methods: {
    genData () {
      const data1 = _map(new Array(6 * 9), (item, idx) => {
        switch (idx % 9) {
          case 0:
            return _random(70, 80)
          case 1:
            return _random(60, 70)
          case 2:
            return _random(70, 80)
          case 3:
            return _random(70, 80)
          case 4:
            return _random(70, 80)
          case 5:
            return _random(70, 80)
          case 6:
            return _random(70, 80)
          case 7:
            return _random(70, 80)
          case 8:
            return _random(80, 90)
          default:
            return 0
        }
      })
      const data2 = _map(new Array(6 * 9), (item, idx) => {
        switch (idx % 9) {
          case 0:
            return _random(-80, -70)
          case 1:
            return _random(-70, -60)
          case 2:
            return _random(-80, -70)
          case 3:
            return _random(-80, -70)
          case 4:
            return _random(-80, -70)
          case 5:
            return _random(-80, -70)
          case 6:
            return _random(-80, -70)
          case 7:
            return _random(-80, -70)
          case 8:
            return _random(-90, -80)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FFFFFF',
          width: 2,
          data: data1
        },
        {
          color: '#FFFFFF',
          width: 2,
          data: data2
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part8{
  .info{
    height: 100%;
    width: 25%;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FFFFFF;
      background-color: #333;
      color: #FFFFFF;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    >div{
      box-sizing: border-box;
      position: relative;
      border-bottom: 1px solid #808080;
      border-right: 1px solid #808080;
      >div{
        position: absolute;
        text-align: right;
        display: flex;
        align-items: flex-start;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
    }
    &-part1{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 16px; left: 25%; top: 0px; }
      &-3{ font-size: 16px; left: 50%; top: 0px; }
      &-4{ font-size: 16px; left: 75%; top: 0px; }
      &-5{ font-size: 36px; right: 50%; top: 10px; }
      &-6{ font-size: 36px; right: 0; top: 10px; }
    }
    &-part2{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 16px; left: 50%; top: 0px; }
      &-3{ font-size: 30px; left: 40px; top: 10px; }
      &-4{ font-size: 30px; left: calc(50% + 40px); top: 10px; }
    }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 5px; }
      &-2{ right: 10px; top: 5px; }
      &-3{ right: 90px; top: 30px; }
      &-4{ right: 10px; top: 30px; }
    }
  }
}
</style>
