<template>
<div class="Admit">
  <ecg-row>
    <ecg-col :span="10">
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('id-dlg')">ID</ecg-button>
        <div class="input-box input-box-id">{{ id }}</div>
      </div>
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('name-dlg')">Name</ecg-button>
        <div class="input-box input-box-name">{{ name }}</div>
      </div>
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('birth-date-dlg')">Birth Date</ecg-button>
        <div class="input-box input-box-birth-date">
          <span>{{ birthDateText }}</span>
          <span class="age">Age</span>
          <span class="yr">{{ age[0] }} Yr</span>
          <span class="mo">{{ age[1] }} Mo</span>
          <span class="d">{{ age[2] }} D</span>
        </div>
      </div>
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('patient-type-dlg')">Patient Type</ecg-button>
        <div class="input-box input-box-patient-type">{{ type }}</div>
      </div>
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('height-weight-dlg')">Height / Weight</ecg-button>
        <div class="input-box input-box-height-weight">
          <span class="cm">{{ height }} [cm]</span>
          <span class="kg">{{ weight }} [kg]</span>
          <span class="bsa">BSA</span>
          <span class="m2">{{ bsa }} [m²]</span>
        </div>
      </div>
      <div class="d-flex mb-10">
        <div class="d-flex">
          <ecg-button class="form-btn" width="120px" @click="openDlg('gender-dlg')">Gender</ecg-button>
          <div class="input-box input-box-gender">{{ gender }}</div>
        </div>
        <div class="d-flex ml-auto">
          <ecg-button class="form-btn" width="120px" @click="openDlg('blood-dlg')">Blood</ecg-button>
          <div class="input-box input-box-blood">{{ blood }}</div>
        </div>
      </div>
      <div class="d-flex mb-10">
        <ecg-button class="form-btn" width="120px" @click="openDlg('pacemaker-dlg')">Pacemaker</ecg-button>
        <div class="input-box input-box-pacemaker">{{ pacemakerText }}</div>
      </div>
      <div class="d-flex">
        <ecg-button class="form-btn" width="120px" @click="openDlg('notes-dlg')">Notes</ecg-button>
        <div class="input-box input-box-notes">{{ notes }}</div>
      </div>
    </ecg-col>
    <ecg-col :span="2">
      <ecg-button class="ml-auto" @click="openDlg('admit-dlg')">Admit</ecg-button>
      <ecg-button class="ml-auto" style="margin-top: 510px" @click="$dialog.open('ECGAnalysis', [0])">ECG<br>Analysis>></ecg-button>
    </ecg-col>
  </ecg-row>

  <!-- 表单弹框 -->
  <id-dlg ref="id-dlg"/>
  <name-dlg ref="name-dlg"/>
  <birth-date-dlg ref="birth-date-dlg"/>
  <patient-type-dlg ref="patient-type-dlg"/>
  <height-weight-dlg ref="height-weight-dlg"/>
  <gender-dlg ref="gender-dlg"/>
  <blood-dlg ref="blood-dlg"/>
  <pacemaker-dlg ref="pacemaker-dlg"/>
  <notes-dlg ref="notes-dlg"/>
  <admit-dlg ref="admit-dlg"/>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import IdDlg from './dialogs/id-dlg'
import NameDlg from './dialogs/name-dlg'
import BirthDateDlg from './dialogs/birth-date-dlg'
import PatientTypeDlg from './dialogs/patient-type-dlg'
import HeightWeightDlg from './dialogs/height-weight-dlg'
import GenderDlg from './dialogs/gender-dlg'
import BloodDlg from './dialogs/blood-dlg'
import PacemakerDlg from './dialogs/pacemaker-dlg'
import NotesDlg from './dialogs/notes-dlg'
import AdmitDlg from './dialogs/admit-dlg'

export default {
  components: {
    IdDlg,
    NameDlg,
    BirthDateDlg,
    PatientTypeDlg,
    HeightWeightDlg,
    GenderDlg,
    BloodDlg,
    PacemakerDlg,
    NotesDlg,
    AdmitDlg
  },

  computed: {
    ...mapState('patient', ['id', 'name', 'age', 'type', 'height', 'weight', 'bsa', 'gender', 'blood', 'pacemaker', 'notes']),
    ...mapGetters('patient', ['birthDateText', 'pacemakerText'])
  },

  methods: {
    openDlg (name) {
      this.$refs[name].open()
    }
  }
}
</script>

<style lang="scss" scoped>
.Admit {
  position: relative;
  .form-btn { flex: 0 0 auto; }
  .input-box {
    box-sizing: border-box;
    border: 1px solid #666;
    background: #fff; color: #000;
    font-size: 18px;
    height: 40px; line-height: 40px;
    padding: 0 10px;

    &-id { width: 50%; }
    &-name { flex: 1 1 auto; }
    &-birth-date {
      flex: 1 1 auto;
      position: relative;
      span {
        position: absolute; top: 50%;
        transform: translateY(-50%);
        font-size: 14px; font-weight: bold;
      }
      .age { left: 40%; }
      .yr { left: 50%; }
      .mo { left: 65%; }
      .d { left: 80%; }
    }
    &-patient-type { width: 120px; text-align: center; }
    &-height-weight {
      flex: 1 1 auto;
      position: relative;
      span {
        position: absolute; top: 50%;
        transform: translateY(-50%);
        font-size: 14px; font-weight: bold;
      }
      .cm { left: 20%; }
      .kg { left: 40%; }
      .bsa { left: 70%; }
      .m2 { left: 80%; }
    }
    &-gender { width: 120px; text-align: center; }
    &-blood { width: 120px; text-align: center; }
    &-pacemaker { width: 120px; text-align: center; }
    &-notes { flex: 1 1 auto; height: 240px; }
  }
}
</style>
