        <ecg-button width="100px" height="50px" class="mr-40">Use as<br>Reference</ecg-button>
        <div class="time bg-black d-flex flex-center mr-10">8 h</div>
      </div>
      <div class="STReview-control-right d-flex">
        <ecg-slider-step class="mr-10"/>
