<template>
<div class="dialog-manager" v-if="visible" @click.self="handleClose" :style="{ background: bgcolor ? bgcolor : 'none'  }">
  <div class="dialog-manager-wrapper">
    <component v-if="currentDialog" :is="currentDialog" ref="dialog"/>
  </div>
</div>
</template>

<script>
const DIALOGS = require.context('./dialogs', false, /\.vue$/)
const DIALOGS_COMPONENTS = DIALOGS.keys()
  .map(p => ({
    name: p.slice(2, -4),
    component: DIALOGS(p).default
  }))
  .reduce((o, d) => {
    o[d.name] = d.component
    return o
  }, {})

export default {
  data () {
    return {
      bgcolor: null,
      visible: false,
      current: null
    }
  },

  created () {
    this.$dialog.on('open', this.handleOpen)
    this.$dialog.on('close', this.handleClose)
    this.$dialog.on('setBgColor', this.handleSetBgColor)
    this.$dialog.on('openState', this.handleOpenState)
  },

  beforeDestroy () {
    this.$dialog.off('open', this.handleOpen)
    this.$dialog.off('close', this.handleClose)
    this.$dialog.off('setBgColor', this.handleSetBgColor)
    this.$dialog.off('openState', this.handleOpenState)
  },

  computed: {
    currentDialog () {
      return this.current ? DIALOGS_COMPONENTS[this.current] : null
    }
  },

  methods: {
    handleOpen (name, ...opts) {
      this.current = name
      this.visible = true
      this.$nextTick(() => {
        if (this.$refs.dialog.onOpen) {
          this.$refs.dialog.onOpen(...opts)
        }
      })
    },

    handleClose () {
      this.visible = false
      this.current = null
      this.bgcolor = null
    },

    handleSetBgColor (color) {
      this.bgcolor = color
    },

    handleOpenState (name, ...opts) {
      if (this.current) {
        this.handleClose()
      } else {
        this.handleOpen(name, ...opts)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-manager {
  position: absolute; top: 0; bottom: 70px; left: 0; right: 0;
  z-index: 9999;

  &-wrapper {
    width: 960px;
    position: absolute; left: 25%; bottom: 0;
  }
}
</style>
