<template>
  <div class="DrugRegistration">
    <div class="mb-40">
      <ecg-label>Drug Select</ecg-label>
      <div class="d-flex">
        <ecg-button width="200px" height="40px"
          v-for="(item, idx) of drugList"
          :key="idx"
          v-html="item.text"
          :active="currentDrug === item.text"
          @click="handleClick(item)">
        </ecg-button>
      </div>
    </div>
    <div class="keyboard-wrapper mb-10">
      <div class="input">{{ keyboardInput }}</div>
      <ecg-keyboard class="mb-20" @press="handlePress"></ecg-keyboard>
      <div class="unit">
        <div class="unit-label">Unit</div>
        <ecg-button-list
          itemWidth="100px"
          itemHeight="50px"
          :list="[['mg', 'units', 'IU']]"/>
      </div>
    </div>
    <ecg-button class="set" width="100px" height="50px" disabled>Set</ecg-button>
  </div>
</template>

<script>
import {
  startsWith as _startsWith
} from 'lodash'
export default {
  data () {
    return {
      currentDrug: 'Drug A',
      keyboardInput: '',
      drugList: [
        { text: 'Drug A' },
        { text: 'Drug B' },
        { text: 'Drug C' },
        { text: 'Drug D' }
      ]
    }
  },
  mounted () {
    this.keyboardInput = this.currentDrug
  },
  methods: {
    handleClick (item) {
      this.currentDrug = item.text
      this.keyboardInput = item.text
    },
    handlePress (key) {
      if (_startsWith(key, 'CTRL:')) {
        // 控制字符
        if (key === 'CTRL:CLR') {
          this.keyboardInput = ''
        } else if (key === 'CTRL:BS') {
          this.keyboardInput = this.keyboardInput.slice(0, this.keyboardInput.length - 1)
        }
      } else {
        // 普通字符
        this.keyboardInput = this.keyboardInput.concat(key)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.DrugRegistration{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 40px 85px 0px;
  .keyboard-wrapper{
    padding: 10px;
    border: 1px solid #333;
    .input{
      width: 400px;
      height: 40px;
      border: 2px inset #ccc;
      background-color: #fff;
      color: #000;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }
    .unit{
      &-label{
        color: #000;
        font-weight: bold;
      }
    }
  }
  .set{
    align-self: flex-end;
  }
}
</style>
