import { random as _random } from 'lodash'

/**
 * 生成波形数据
 * @param {int} count 周期数
 * @param {int} offset 随机偏移量
 * @param {Array} list 每个周期数据
 */
export function waveData (count, offset, list) {
  let all = []

  for (let i = 0; i < count; i++) {
    const points = list.map(n => _random(n - offset, n + offset))

    all = all.concat(points)
  }

  return all
}

export function imgPreloader (url) {
  return new Promise((resolve, reject) => {
    const image = new Image()
    const src = url

    image.onload = () => {
      resolve(url)
    }

    image.onerror = (err) => {
      // eslint-disable-next-line
      console.error(`[IMAGE ERROR] ${url}`)
      reject(err)
    }

    image.src = src
  })
}
