<template>
<div class="SpO2">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box class="h-100">
        <div class="left-chart mb-10">
          <ecg-heart-beat height="100%" :dataFunc="leftData"/>
          <div class="l-1"></div>
          <div class="l-2"></div>
          <span class="t-1 c-blue">SpO<sub>2</sub></span>
          <span class="t-2">x1<br>Auto</span>
          <span class="t-3 c-green">I</span>
          <span class="t-4">x1<br>Auto</span>
          <span class="t-5">1 mV</span>
          <div class="gong"></div>
        </div>

        <ecg-label>Sens</ecg-label>
        <ecg-button-list itemWidth="40px" itemHeight="40px" :list="[
          ['Auto', 'x1/8', 'x1/4', 'x1/2', 'x1', 'x2'],
          ['x4', 'x8']
        ]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="mb-10">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['16:00', '18:00']" :y="[0, 25, 50, 75, 100]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-single
            :value="50"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"
            :dotType="0"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10" content="99" text1="SpO<sub>2</sub>" style="color: #00F8F8"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box-list :list="[
          [
            { title: 'SpO<sub>2</sub>', content: '99', text1: 'Off', text2: '89' },
            { title: 'PR', content: '', text1: 'Off', text2: 'Off' }
          ]
        ]"/>
      </ecg-box>
      <ecg-box>
        <ecg-info-box-2 title="PI"/>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { waveData } from '@/libs/helper'

export default {
  methods: {
    leftData () {
      return [
        {
          color: '#00F8F8',
          width: 2,
          data: waveData(4, 2, [50, 60, 70, 70, 60, 50, 50])
        },
        {
          color: '#0f0',
          width: 2,
          data: waveData(4, 2, [-50, -50, -20, -50, -50, -50, -50, -45, -50, -50, -50, -50])
        }
      ]
    },

    rightData () {
      return [
        {
          color: '#00F8F8',
          width: 2,
          data: [90]
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.SpO2 {
  .left-chart {
    background: #000;
    height: 330px;
    box-sizing: border-box;
    padding: 20px 0;
    position: relative;
    .l-1 { position: absolute; left: 0; right: 0; top: 20px; height: 1px; background: #fff; }
    .l-2 { position: absolute; left: 0; right: 0; bottom: 20px; height: 1px; background: #fff; }
    span {
      font-weight: bold; font-size: 16px;
      position: absolute;
      &.t-1 { left: 10px; top: 20px; }
      &.t-2 { right: 10px; top: 20px; text-align: right; }
      &.t-3 { left: 10px; bottom: 160px; }
      &.t-4 { right: 80px; bottom: 100px; text-align: right; }
      &.t-5 { right: 20px; bottom: 120px; }
    }
    .gong {
      position: absolute; right: 10px; bottom: 80px;
    }
  }
}
</style>
