<template>
<div class="Readings bg-black">
  <div class="container1">
    <div class="box box1">
      <div class="box-info info1">Ppeak</div>
      <div class="box-info info2 c-lighten-gray f-14">[cmH₂O]</div>
      <div class="box-info info3">Pplat</div>
      <div class="box-info info4 c-lighten-gray f-14">[cmH₂O]</div>
      <div class="box-info info5">40</div>
      <div class="box-info info6">5</div>
      <div class="box-info info7">Pmean</div>
      <div class="box-info info8 c-lighten-gray f-14">[cmH₂O]</div>
      <div class="box-info info9">38</div>
    </div>
    <div class="box box2">
      <div class="box-info info1">MV</div>
      <div class="box-info info2 c-lighten-gray f-14">[L/min]</div>
      <div class="box-info info3">0.50</div>
      <div class="box-info info4">TVe</div>
      <div class="box-info info5 c-lighten-gray f-14">[mL]</div>
      <div class="box-info info6">380</div>
    </div>
    <div class="box box3">
      <div class="box-info info1">RR</div>
      <div class="box-info info2 c-lighten-gray f-14">[/min]</div>
      <div class="box-info info3">RRspo</div>
      <div class="box-info info4 c-lighten-gray f-14">[/min]</div>
      <div class="box-info info5">15</div>
      <div class="box-info info6">2</div>
    </div>
    <div class="box box4"></div>
    <div class="box box5"></div>
    <div class="box box6"></div>
  </div>
  <div class="container2">
    <div class="box box7">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :lines="[
          { color: '#808080', width: 1, value: 0 },
          { color: '#808080', width: 1, value: 96 },
          { color: '#808080', width: 1, value: -96 },
          { color: '#808080', width: 1, value: 48 },
          { color: '#808080', width: 1, value: -48 }
        ]"
        :dataFunc="genData"/>
      <div class="box-info info1">Paw</div>
      <div class="box-info info2">50</div>
      <div class="box-info info3">0</div>
      <div class="box-info info4">Flow</div>
      <div class="box-info info5">1.0</div>
      <div class="box-info info6">-1.0</div>
    </div>
    <div class="container2-1">
      <div class="box box8">
        <div class="box-info info1">R</div>
        <div class="box-info info2 c-lighten-gray f-14">[cmH₂O/L/s]</div>
        <div class="box-info info3">C</div>
        <div class="box-info info4 c-lighten-gray f-14">[mL/cmH₂O]</div>
        <div class="box-info info5">20.0</div>
        <div class="box-info info6">12.0</div>
      </div>
      <div class="container2-1-1">
        <div class="container2-1-1-1">
          <div class="box box9"></div>
          <div class="box box10"></div>
        </div>
        <div class="container2-1-1-2">
          <div class="box box11"></div>
          <div class="box box12"></div>
        </div>
        <div class="box box13">
          <div class="box-info info1">Vent Mode:VCV</div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
export default {
  methods: {
    genData () {
      const data1 = _map(new Array(12 * 2), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return _random(5, 8)
          case 1:
            return _random(15, 18)
          case 2:
            return _random(25, 28)
          case 3:
            return _random(35, 38)
          case 4:
            return 45
          case 5:
            return _random(13, 15)
          case 6:
            return _random(11, 13)
          case 7:
            return _random(9, 11)
          case 8:
            return _random(8, 9)
          case 9:
            return _random(7, 8)
          case 10:
            return _random(6, 7)
          case 11:
            return _random(5, 6)
        }
      })
      const data2 = _map(new Array(12 * 2), (item, idx) => {
        switch (idx % 12) {
          case 0:
            return _random(-45, -47)
          case 1:
            return -30
          case 2:
            return -31
          case 3:
            return -32
          case 4:
            return -33
          case 5:
            return -100
          case 6:
            return -55
          case 7:
            return -51
          case 8:
            return -49
          case 9:
            return -47
          case 10:
            return -47
          case 11:
            return -47
        }
      })
      return [
        {
          color: '#FFF',
          width: 2,
          data: data1
        },
        {
          color: '#FFF',
          width: 2,
          data: data2
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
