        }
        &-2{
          height: calc(100% / 3);
          border-bottom: 1px solid #808080;
          box-sizing: border-box;
          display: flex;
          align-items: stretch;
          .box11{
            width: 50%;
            border-right: 1px solid #808080;
            box-sizing: border-box;
          }
