<template>
<div class="BasicSettings">
  <ecg-row>
    <ecg-col :span="4">
      <ecg-box class="h-100">
        <div class="bg-black p-10 mb-10">
          <div class="row-1">
            <div>
              <span class="c-pink mr-5">PR</span>
              <span class="c-darken-pink">[/min]</span>
            </div>
            <div>
              <span class="c-darken-pink">[mmHg]</span>
            </div>
          </div>
          <div class="row-2 c-pink">
            <div>65</div>
            <div>106 / 67</div>
            <div>(78)</div>
          </div>
          <div class="row-3 c-pink mb-10">
            <div>18:31</div>
            <div>Man</div>
          </div>
          <div class="d-flex">
            <ecg-scale2 :scale="scaleList" height="400px" doubleLine/>
            <div class="line-chart">
              <ecg-bar-chart width="100%" height="100%" barColor="#FF9090" barWidth="4px" :barList="barList"/>
            </div>
          </div>
        </div>
        <ecg-label>Mode</ecg-label>
        <ecg-button-list :list="[['Adult 180', 'Child 140', 'Neonate 100']]"/>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="8">
      <ecg-box class="mb-10">
        <div class="d-flex mb-10">
          <ecg-axis-box width="400px" height="330px" :x="['16:00', '18:00']" :y="[0, 100, 200, 300]" :gridRow="10" :gridCol="10">
            <ecg-heart-beat width="100%" height="100%" :dataFunc="rightData"/>
          </ecg-axis-box>
          <ecg-slider-double
            :minValue="20"
            :maxValue="80"
            height="330px"
            :dotOptions="{ min: 10, max: 90 }"/>
          <div class="ml-auto">
            <ecg-info-box-1 class="mb-10 c-pink" content="106" text1="Sys" text2="[mmHg]"/>
            <ecg-counter class="mb-10" borderColor="yellow"/>
            <ecg-button class="mb-10" width="100px">Auto</ecg-button>
            <ecg-counter borderColor="yellow"/>
          </div>
        </div>
        <ecg-label>Alarm</ecg-label>
        <ecg-alarm-box-list class="c-alarm-list" :list="[
          [
            { title: 'Sys', content: '106', text1: '180', text2: '80' },
            { title: 'Dia', content: '67', text1: 'Off', text2: 'Off' },
            { title: 'MAP', content: '78', text1: 'Off', text2: 'Off' }
          ]
        ]"/>
      </ecg-box>
      <ecg-box>
        <ecg-row>
          <ecg-col :span="4">
            <ecg-label>Measurement</ecg-label>
            <div class="d-flex">
              <ecg-button
                class="green-button"
                :disabled="measurement.stat"
                :active="measurement.manual"
                @click="changeManual">Manual {{ measurement.manual ? 'Stop' : 'Start' }}</ecg-button>
              <ecg-button
                class="green-button"
                :active="measurement.stat"
                @click="changeStat">STAT {{ measurement.stat ? 'Stop' : 'Start' }}</ecg-button>
            </div>
            <ecg-button
              class="green-button"
              :active="measurement.interval"
              @click="changeInterval">Interval {{ measurement.interval ? 'Stop' : 'Start' }}</ecg-button>
          </ecg-col>
          <ecg-col :span="2" class="bottom-fix">
            <ecg-button class="green-button" @click="stopAll">Stop All</ecg-button>
          </ecg-col>
          <ecg-col :span="6">
            <ecg-label>Interval</ecg-label>
            <ecg-button-list itemWidth="40px" :list="[
              ['1 min', '2 min', '2.5 min', '5 min', '10 min'],
              ['15 min', '30 min', '1 h', '2 h', '4 h']
            ]"/>
          </ecg-col>
        </ecg-row>
      </ecg-box>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { range as _range, random as _random } from 'lodash'

export default {
  data () {
    return {
      scaleList: _range(0, 31).map(n => n * 10),
      barList: this.genBarList(),

      measurement: {
        manual: false,
        stat: false,
        interval: false
      }
    }
  },

  methods: {
    genBarList () {
      const list = []

      for (let i = 0; i < 50; i++) {
        list.push(_random(1, 80))
      }

      return list
    },

    rightData () {
      return [
        {
          color: '#FF9090',
          width: 2,
          data: [-20]
        }
      ]
    },

    changeManual () {
      if (this.measurement.stat) { return }
      this.measurement.manual = !this.measurement.manual
    },

    changeStat () {
      this.measurement.stat = !this.measurement.stat
    },

    changeInterval () {
      this.measurement.interval = !this.measurement.interval
    },

    stopAll () {
      this.measurement.manual = false
      this.measurement.stat = false
      this.measurement.interval = false
    }
  }
}
</script>

<style lang="scss" scoped>
.BasicSettings {
  .row-1 { display: flex; justify-content: space-between; margin-bottom: 5px; }
  .row-2 { display: flex; justify-content: space-around; font-size: 20px; margin-bottom: 10px; }
  .row-3 {
    display: flex; justify-content: space-between;
    box-sizing: border-box;
    padding-left: 100px;
  }
  .line-chart {
    flex: 1 1 auto;
    box-sizing: border-box;
    border: 1px solid #fff;
  }
  .bottom-fix {
    display: flex; flex-direction: column; justify-content: flex-end;
  }
  .green-button {
    background: #46A493;
    &.disabled { background:#9bb9b4; }
  }
  .c-alarm-list /deep/ .ecg-alarm-box-content { color: #FF9090; }
}
</style>
