<template>
<div class="Measurement">
  <ecg-row>
    <ecg-col :span="3">
      <ecg-box>
        <div class="bg-black p-10 mb-5 bb-1">
          <div class="mb-5">Time</div>
          <div class="pl-10 mb-10">{{ tofRat ? '2020-9-2 12:13:36' : '----·--·-- --:--:--' }}</div>
          <div class="flex-between mb-10">
            <div class="c-atrovirens">TOF rat</div>
            <div class="c-darken-atrovirens">[%]</div>
          </div>
          <div class="d-flex">
            <div class="circle"></div>
            <div class="big-number c-atrovirens">{{ tofRat ? tofRatValue : '---' }}</div>
          </div>
        </div>
        <div class="bg-black p-10 bb-2">
          <div class="flex-between mb-10">
            <strong>Stimulation Current</strong>
            <strong class="c-gray">[mA]</strong>
          </div>
          <div class="number">{{ calActive ? '---' : stimulationCurrentFormat }}</div>

          <div class="flex-between mb-10">
            <strong>Pulse width</strong>
            <strong class="c-gray">[μs]</strong>
          </div>
          <div class="number">300</div>

          <div class="flex-between mb-10">
            <strong>Sensitivity</strong>
            <strong class="c-gray">[mVp-p]</strong>
          </div>
          <div class="number">{{ calActive ? '---' : sensitivityFormat }}</div>

          <div class="d-flex mt-40">
            <ecg-button
              width="80px" height="45px"
              :disabled="calActive"
              @click="handleClickCAL">CAL<br>(hold 2s)</ecg-button>
            <strong class="pt-10 ml-10" v-html="timeActive ?  '2021-3-4<br>13:01:03' : '----·--·--<br>--:--:--'"></strong>
          </div>
        </div>
      </ecg-box>
    </ecg-col>
    <ecg-col :span="9">
      <ecg-box class="mb-10">
        <div class="bg-black p-10 bb-3" :class="chartMode">
          <ecg-button width="40px" height="120px" @click="handleClickArrow">
            <div :class="chartMode === 'left' ? 'triangle-right' : 'triangle-left'"></div>
          </ecg-button>
          <img :src="chartImage">
        </div>
      </ecg-box>
      <ecg-row>
        <ecg-col :span="8">
          <ecg-box style="height: 240px">
            <ecg-label>Stimulation Mode</ecg-label>
            <div class="flex-between mb-20">
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive" @click="handleClickTOF">TOF</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">PTC</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">1 Hz</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px">Stop</ecg-button>
            </div>
            <div class="flex-between mb-10">
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">TOFs<br>{{ TOFs }}min</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">TET<br>50</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">0.1 Hz</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">DBS<br>3.2</ecg-button>
            </div>
            <div class="flex-between">
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">Single<br>TOF</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">TET<br>100</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">Single<br>Twitch</ecg-button>
              <ecg-button class="btn-green" width="100px" height="50px" :disabled="calActive || tofActive">DBS<br>3.3</ecg-button>
            </div>
          </ecg-box>
        </ecg-col>
        <ecg-col :span="4" class="py-10">
          <ecg-label>TOF cnt Alarm</ecg-label>
          <ecg-counter border-color="#60D2F8"/>
          <div class="d-flex mt-20">
            <ecg-button width="100px" height="50px" class="mr-10" @click="$dialog.open('Review', [0, 7])">TOF<br>Trend >></ecg-button>
            <ecg-button width="100px" height="50px" @click="$dialog.open('Review', [1, 9])">TOF<br>List >></ecg-button>
          </div>
        </ecg-col>
      </ecg-row>
    </ecg-col>
  </ecg-row>
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

const CHART_IMAGES = {
  bar: { left: require('./bar-left.jpg'), right: require('./bar-right.jpg') },
  wave: { left: require('./wave-left.jpg'), right: require('./wave-right.jpg') },
  'bar-none': { left: require('./bar-none.png'), right: require('./bar-none.png') },
  'wave-none': { left: require('./wave-none.jpg'), right: require('./wave-none.jpg') }
}

export default {
  computed: {
    ...mapState('tof', [
      'displayMode',
      'chartMode',
      'calActive',
      'tofActive',
      'timeActive',
      'tofRat',
      'tofRatValue',
      'stimulationDuration',
      'stimulationCurrent',
      'sensitivity',
      'twoMin'
    ]),
    TOFs () {
      return Math.round(60 * (this.stimulationDuration - 10) / (90 - 10))
    },
    chartImage () {
      return CHART_IMAGES[this.displayMode][this.chartMode]
    },
    stimulationCurrentFormat () {
      return Math.round(60 * (this.stimulationCurrent - 10) / (90 - 10))
    },
    sensitivityFormat () {
      return Math.round(50 * (this.sensitivity - 10) / (90 - 10))
    }
  },

  methods: {
    ...mapMutations('tof', [
      'SET_DISPLAY_MODE',
      'SET_CHART_MODE',
      'SET_CAL_ACTIVE',
      'SET_TOF_ACTIVE',
      'SET_TOF_RAT',
      'CHANGE_TOF_RAT_VALUE',
      'SET_TIME_ACTIVE',
      'SET_TRANSPARENT'
    ]),

    handleClickCAL () {
      this.SET_CAL_ACTIVE(true)
      setTimeout(() => {
        this.SET_CAL_ACTIVE(false)
        this.SET_TIME_ACTIVE(true)
      }, 3000)
    },

    handleClickArrow () {
      if (this.chartMode === 'left') {
        this.SET_CHART_MODE('right')
      } else {
        this.SET_CHART_MODE('left')
      }
    },

    handleClickTOF () {
      this.SET_TOF_ACTIVE(true)
      setTimeout(() => {
        this.CHANGE_TOF_RAT_VALUE()
        this.SET_TOF_ACTIVE(false)
        this.SET_TOF_RAT(true)
        if (this.displayMode === 'wave-none') {
          this.SET_DISPLAY_MODE('wave')
        } else if (this.displayMode === 'bar-none') {
          this.SET_DISPLAY_MODE('bar')
        }
        if (this.twoMin) {
          this.SET_TRANSPARENT(false)
          setTimeout(() => {
            this.SET_TRANSPARENT(true)
          }, 2 * 60 * 1000)
        }
      }, 2000)
    }
  }
}
</script>

