<template>
<div class="ecg-alarm-box"
    :class="{ active, disabled }"
    @click="handleClick">
  <div class="ecg-alarm-box-title" v-html="title"></div>
  <div class="ecg-alarm-box-wrapper">
    <div class="ecg-alarm-box-content">{{ content }}</div>
    <div class="ecg-alarm-box-text">
      <div class="text">
        <template v-if="text1">
          {{ text1 }} <div class="b"></div>
        </template>
      </div>
      <div class="text">
        <template v-if="text2">
          {{ text2 }} <div class="b"></div>
        </template>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    active: { type: Boolean, default: false },
    disabled: { type: Boolean, default: false },
    title: { type: String, default: '' },
    content: { type: String, default: '' },
    text1: { type: String, default: '' },
    text2: { type: String, default: '' }
  },

  methods: {
    handleClick () {
      if (this.disabled) { return }

      this.$emit('click')
    }
  }
}
</script>

