  display: flex; flex-direction: column; justify-content: space-between;
  &:active { border-style: inset; -webkit-tap-highlight-color: transparent; }
  &.active {
    border-style: inset;
    background: #02A6D7;
    .ecg-alarm-box-content { color: #00D8D8; }
  }
  &.disabled {
    background: #A4A4B1; color: darken(#A4A4B1, 20);
    cursor: default;
    &:active { border-style: outset; -webkit-tap-highlight-color: transparent; }
  }

  &-title {
    font-size: 18px;
    text-align: center;
