<template>
  <div class="main-6">
    <draggable @choose="handleChoose" @unchoose="handleUnchoose" @change="handleChange" :list="heartBeatList" class="part-wrapper" :delay="1000">
      <component v-for="c of heartBeatList" :key="c[0]" :is="c[1]" ref="part"></component>
    </draggable>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
export default {
  components: { draggable },
  data () {
    return {
      heartBeatList: [
        [1, require('./main-6/part1').default],
        [2, require('./main-6/part2').default],
        [3, require('./main-6/part3').default],
        [4, require('./main-6/part4').default]
      ]
    }
  },
  methods: {
    handleChoose (e) {
      this.$refs.part[e.oldIndex].drag = true
    },
    handleUnchoose (e) {
      this.$refs.part[e.oldIndex].drag = false
    },
    handleChange (e) {
      const moveEl = this.$refs.part.splice(e.moved.oldIndex, 1)[0]
      this.$refs.part.splice(e.moved.newIndex, 0, moveEl)
    }
  }
}
</script>

<style lang="scss" scoped>
.main-6{
  height: 100%;
  .part-wrapper{
    height: 100%;
    .part1{ height: calc(50% / 4); }
    .part2{ height: calc(50% / 4); }
    .part3{ height: calc(50% / 4); }
    .part4{ height: calc(50% / 4); }
  }
}
</style>
