<template>
  <div class="part3 d-flex">
    <div class="info">
      <div class="info-part" @click="$dialog.open('BasicParameter', [4, 0])">
        <div class="info-part-1 c-pink">NIBP</div>
        <div class="info-part-2 c-pink">Adult</div>
        <div class="info-part-3 c-pink">15min</div>
        <div class="info-part-4 c-pink">00:04</div>
        <div class="info-part-5"><img :src="images['img_mmhg']"></div>
        <div class="info-part-6 c-darken-pink">[mmHg]</div>
        <div class="info-part-7 c-darken-pink">180<br>80</div>
        <div class="info-part-8 c-pink">107/ 54</div>
        <div class="info-part-9 c-darken-pink">Off<br>Off</div>
        <div class="info-part-10 c-pink">( 71)</div>
        <div class="info-part-11 c-darken-pink">Off<br>Off</div>
        <div class="info-part-12 c-pink">15:09</div>
        <div class="info-part-13 c-pink">PR</div>
        <div class="info-part-14 c-pink">74</div>
        <div class="info-part-15 c-pink">250</div>
        <div class="info-part-16"><img :src="images['img_nibp']"></div>
        <div class="info-part-17 c-pink">0</div>
      </div>
      <div class="info-part" @click="$dialog.open('BasicParameter', [5, 0, 0])">
        <template v-if="!hiqviewMode">
          <div class="info-part-18 c-red" >ART</div>
          <div class="info-part-19 c-darken-red">Off<br>80</div>
          <div class="info-part-20 c-red">{{ hiqviewMode ? '---/ ---' : '102/ 60' }}</div>
          <div class="info-part-21 c-darken-red">Off<br>Off</div>
          <div class="info-part-22 c-red">{{ hiqviewMode ? '( ---)' : '( 75)' }}</div>
          <div class="info-part-23 c-darken-red">Off<br>60</div>
        </template>
      </div>
      <div class="drag-info" v-if="drag">NIBP</div>
    </div>
    <div class="chart" @click="handleThumbnail">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#FF4848', width: 1, value: 0 },
          { color: '#FF4848', width: 1, value: 94 },
          { color: '#FF4848', width: 1, value: -94 },
          { color: '#FF4848', width: 1, value: 47 },
          { color: '#FF4848', width: 1, value: -47 }
        ]"/>
      <div class="chart-info">
        <div class="chart-info-1 c-red">ART</div>
        <div class="chart-info-2 c-red">200</div>
        <div class="chart-info-3 c-red">0</div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <ecg-heart-beat-static
          width="100%"
          height="100%"
          :dataFunc="thumbnailGenData1"/>
        <div class="thumbnail-line1"></div>
        <div class="thumbnail-line2"></div>
        <div class="thumbnail-line3"></div>
        <div class="thumbnail-info1 c-pink">NIBP</div>
        <div class="thumbnail-info2 c-pink">10.0</div>
        <div class="thumbnail-info3 c-pink">0.0</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { random as _random, map as _map } from 'lodash'
const IMAGES_PATH = require.context('./images/', false, /(\.png)$/)
const IMAGES = {}
IMAGES_PATH.keys().forEach(key => {
  const keyname = key.split('/').slice(1)[0].split('.')[0]
  IMAGES[keyname] = IMAGES_PATH(key)
})

export default {
  data () {
    return {
      images: IMAGES,
      drag: false
    }
  },
  computed: {
    ...mapState('app', ['hiqviewMode', 'thumbnail'])
  },
  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    genData () {
      const data1 = _map(new Array(19 * 5), (item, idx) => {
        switch (idx % 5) {
          case 0:
            return _random(-5, 5)
          case 1:
            return _random(-20, -10)
          case 2:
            return _random(-30, -20)
          case 3:
            return _random(-40, -30)
          case 4:
            return _random(-50, -40)
          default:
            return 0
        }
      })
      const data2 = []
      return [
        {
          color: '#FF4848',
          width: 2,
          data: this.hiqviewMode ? data2 : data1
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    },
    thumbnailGenData1 () {
      return [
        {
          color: '#FF4848',
          width: 2,
          data: [
            -30, -30, -30, -30, -30, -30, -30, -30, -30, -30,
            -10, -10,
            -50, -50, -50, -50, -50, -50, -50
          ],
          area: 0
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part3{
  .info{
    height: 100%;
    width: 25%;
    border: 1px solid #808080;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FF9090;
      background-color: #333;
      color: #FF9090;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-part{
      box-sizing: border-box;
      position: relative;
      &:first-of-type{ height: 60%; border-bottom: 1px solid #808080; }
      &:last-of-type{ height: 40%; }
      >div{
        position: absolute;
        text-align: right;
        display: flex;
        align-items: flex-start;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
      &-1{ font-size: 16px; left: 5px; top: 5px; }
      &-2{ font-size: 14px; left: 70px; top: 5px; }
      &-3{ font-size: 14px; right: 50%; top: 5px; }
      &-4{ font-size: 14px; left: calc(50% + 40px); top: 0px;}
      &-5{ left: calc(50% + 80px); top: 2px; width: 95px; >img{ width: 100%; height: 100%; } }
      &-6{ font-size: 14px; right: 5px; top: 0px; }
      &-7{ font-size: 14px; left: 20px; top: 40px; }
      &-8{ font-size: 60px; left: 50px; top: 30px; }
      &-9{ font-size: 14px; left: 280px; top: 40px; }
      &-10{ font-size: 48px; left: 100px; top: 90px; }
      &-11{ font-size: 14px; left: 220px; top: 90px; }
      &-12{ font-size: 16px; left: 5px; bottom: 5px; }
      &-13{ font-size: 16px; left: 230px; bottom: 10px; }
      &-14{ font-size: 24px; left: 280px; bottom: 0px; }
      &-15{ font-size: 14px; left: 360px; top: 20px; }
      &-16{ left: 390px; top: 20px; height: 140px; >img{ width: 100%; height: 100%; }}
      &-17{ font-size: 14px; left: 370px; bottom: 5px; }
      &-18{ font-size: 16px; left: 5px; top: 5px; }
      &-19{ font-size: 14px; left: 20px; top: 30px; }
      &-20{ font-size: 60px; left: 50px; top: 30px; }
      &-21{ font-size: 14px; left: 280px; top: 30px; }
      &-22{ font-size: 48px; right: 40px; top: 50px; }
      &-23{ font-size: 14px; right: 5px; top: 50px; }

    }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
      &-2{ right: 10px; top: 10px; }
      &-3{ right: 10px; bottom: 10px; }
    }
    .thumbnail{
      width: calc(100% / 6);
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      border-top: 2px solid #862626;
      border-right: 2px solid #862626;
      border-bottom: 2px solid #862626;
      box-sizing: border-box;
      &-line1{ position: absolute; height: 100%; border-left: 2px solid #862626; left: 45%; top: 0px;}
      &-line2{ position: absolute; height: 100%; border-left: 2px dashed #862626; left: calc(45% / 2); top: 0px;}
      &-line3{ position: absolute; height: 100%; border-left: 2px dashed #862626; left: calc(45% + (55% / 2)); top: 0px;}
      &-info1{ position: absolute; font-size: 16px; left: 5px; top: 5px; }
      &-info2{ position: absolute; font-size: 16px; right: 5px; top: 5px; }
      &-info3{ position: absolute; font-size: 16px; right: 5px; bottom: 5px; }
    }
  }
}
</style>
