<script>
export default {
  data () {
    return {
      caps: false
    }
  },

  methods: {
    press (key) {
      this.$emit('press', key)
    },

    alphaPress (key) {
      this.$emit('press', this.caps ? key.toUpperCase() : key.toLowerCase())
    },

    alphaText (key) {
      return this.caps ? key.toUpperCase() : key.toLowerCase()
    }
  }
}
</script>

