<template>
<div class="HiQ-View-Enabled">
  <div class="end-text">
    End HiQ-View?
  </div>
  <div class="flex-center">
    <ecg-button style="margin-right: 100px" @click="yes">Yes</ecg-button>
    <ecg-button @click="no">No</ecg-button>
  </div>
</div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  methods: {
    ...mapMutations('app', ['SET_HIQVIEW_MODE']),

    yes () {
      this.SET_HIQVIEW_MODE(false)
      this.$dialog.close()
    },

    no () {
      this.$dialog.close()
    }
  }
}
</script>

