<template>
  <div class="part4 d-flex">
    <div class="info">
      <div class="info-part1">
        <div class="info-part1-1 c-red">P1</div>
        <div class="info-part1-2 c-darken-red">Off<br>Off</div>
        <div class="info-part1-3 c-red">2.0/ -0.8</div>
        <div class="info-part1-4 c-darken-red">Off<br>Off</div>
        <div class="info-part1-5 c-red">( 0.1)</div>
        <div class="info-part1-6 c-darken-red">Off<br>Off</div>
      </div>
      <div class="info-part-wrapper d-flex">
        <div class="info-part2">
          <div class="info-part2-1 c-purple flex-center">PR<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
          <div class="info-part2-2 c-purple flex-center">PI<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
          <div class="info-part2-3 c-purple">80</div>
          <div class="info-part2-4 c-purple">2.79</div>
        </div>
        <div class="info-part3">
          <div class="info-part3-1 c-blue flex-center">SpO<sub>2</sub><img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
          <div class="info-part3-2 c-blue">97</div>
          <div class="info-part3-3 c-darken-blue">100<br>89</div>
          <div class="info-part3-4"><img :src="images['img_battery']" alt=""></div>
        </div>
      </div>
      <div class="drag-info" v-if="drag">P1</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"
        :lines="[
          { color: '#671D1D', width: 1, value: 40 },
          { color: '#671D1D', width: 1, value: 98 },
          { color: '#671D1D', width: 1, value: -18 },
          { color: '#671D1D', width: 1, value: 70 },
          { color: '#671D1D', width: 1, value: 10 }
        ]"/>
      <div class="chart-info">
        <div class="chart-info-1 c-red">P1</div>
        <div class="chart-info-2 c-red">2.7</div>
        <div class="chart-info-3 c-red">0.0</div>
        <div class="chart-info-4 c-blue">SpO<sub>2</sub></div>
        <div class="chart-info-5 c-white">x1/2</div>
        <div class="chart-info-6 c-white">Auto</div>
      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
import { mapState } from 'vuex'

const IMAGES_PATH = require.context('./images/', false, /(\.png)$/)
const IMAGES = {}
IMAGES_PATH.keys().forEach(key => {
  const keyname = key.split('/').slice(1)[0].split('.')[0]
  IMAGES[keyname] = IMAGES_PATH(key)
})
export default {
  data () {
    return {
      images: IMAGES,
      drag: false
    }
  },

  computed: {
    ...mapState('app', ['hiqviewMode'])
  },

  methods: {
    genData () {
      const data1 = _map(new Array(13 * 6), (item, idx) => {
        switch (idx % 6) {
          case 0:
            return _random(-20, 0)
          case 1:
            return _random(50, 95)
          case 2:
            return _random(-20, -0)
          case 3:
            return _random(-55, -50)
          case 4:
            return _random(-60, -55)
          case 5:
            return _random(-85, -80)
          default:
            return 0
        }
      })
      const data2 = _map(new Array(13 * 6), (item, idx) => {
        switch (idx % 6) {
          case 0:
            return _random(-95, -90)
          case 1:
            return _random(-100, -95)
          case 2:
            return _random(-65, -60)
          case 3:
            return _random(-25, -20)
          case 4:
            return _random(-65, -60)
          case 5:
            return _random(-70, -65)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FF4848',
          width: 2,
          data: data1
        },
        {
          color: '#00F8F8',
          width: 2,
          data: data2
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part4{
  .info{
    height: 100%;
    width: calc(100% / 3);
    border: 1px solid #808080;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #FF4848;
      background-color: #333;
      color: #FF4848;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-part1{
      height: calc(100% * 2 / 3);
      box-sizing: border-box;
      border-bottom: 1px solid #808080;
      position: relative;
      >div{
        position: absolute;
        text-align: right;
        display: flex;
        align-items: flex-start;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
      &-1{ font-size: 16px; left: 5px; top: 5px; }
      &-2{ font-size: 14px; left: 20px; top: 30px; }
      &-3{ font-size: 60px; left: 50px; top: 30px; }
      &-4{ font-size: 14px; left: 300px; top: 30px; }
      &-5{ font-size: 48px; right: 40px; top: 50px; }
      &-6{ font-size: 14px; right: 5px; top: 50px; }
    }
    &-part-wrapper{
      height: calc(100% * 1 / 3);
      box-sizing: border-box;
      border-bottom: 1px solid #808080;
      .info-part2{
        width: 50%;
        height: 100%;
        border-right: 1px solid #808080;
        box-sizing: border-box;
        position: relative;
        >div{
          position: absolute;
          text-align: right;
          display: flex;
          align-items: flex-start;
          >.small{
            font-size: 14px;
            margin-left: 5px;
          }
        }
        &-1{ font-size: 16px; left: 5px; top: 5px; }
        &-2{ font-size: 16px; left: calc(50% - 20px); top: 5px; }
        &-3{ font-size: 24px; left: calc(50% - 60px); bottom: 5px; }
        &-4{ font-size: 24px; left: calc(50% + 20px); bottom: 5px; }
      }
      .info-part3{
        width: 50%;
        height: 100%;
        position: relative;
        >div{
          position: absolute;
          text-align: right;
          >.small{
            font-size: 14px;
            margin-left: 5px;
          }
        }
        &-1{ font-size: 16px; left: 5px; top: 5px; }
        &-2{ font-size: 48px; left: calc(50% + 20px); top: 0px; }
        &-3{ font-size: 14px; right: 5px; top: 5px; }
        &-4{ left: 5px; bottom: 5px; }
      }
    }
  }
  .chart{
    height: 100%;
    width: calc(100% * 2 / 3);
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
      &-2{ right: 10px; top: 10px; }
      &-3{ right: 10px; bottom: calc(30% + 20px); }
      &-4{ right: 90px; top: calc(60% + 20px); }
      &-5{ right: 10px; top: calc(60% + 20px); }
      &-6{ right: 10px; bottom: 10px; }
    }
  }
}
</style>
