<template>
  <div class="part7 d-flex">
    <div class="info d-flex">
      <div class="info-part1">
        <div class="info-part1-1 c-atrovirens">SEF</div>
        <div class="info-part1-2 c-atrovirens">TP</div>
        <div class="info-part1-3 c-atrovirens">22.5</div>
        <div class="info-part1-4 c-atrovirens">3.41</div>
      </div>
      <div class="info-part2">
        <div class="info-part2-1 c-yellow flex-center">SpO<sub>2</sub>-2<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
        <div class="info-part2-2 c-yellow">98</div>
        <div class="info-part2-3 c-darken-yellow">Off<br>89</div>
      </div>
      <div class="drag-info" v-if="drag">SEF</div>
    </div>
    <div class="chart">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"/>
      <div class="chart-info">
        <div class="chart-info-1 c-yellow">SpO<sub>2</sub>-2</div>
        <div class="chart-info-2 c-white">x1<br>Auto</div>
      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      drag: false
    }
  },

  computed: {
    ...mapState('app', ['hiqviewMode'])
  },

  methods: {
    genData () {
      const data1 = _map(new Array(19 * 8), (item, idx) => {
        switch (idx % 8) {
          case 0:
            return _random(-52, -48)
          case 1:
            return _random(-77, -73)
          case 2:
            return _random(-52, -48)
          case 3:
            return _random(-5, -5)
          case 4:
            return _random(48, 52)
          case 5:
            return _random(73, 77)
          case 6:
            return _random(48, 52)
          case 7:
            return _random(-5, 5)
          default:
            return 0
        }
      })
      return [
        {
          color: '#FFFF00',
          width: 2,
          data: data1
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part7{
  .info{
    height: 100%;
    width: 25%;
    border-top: none;
    box-sizing: border-box;
    position: relative;
    .drag-info{
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #68E8A8;
      background-color: #333;
      color: #68E8A8;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    >div{
      box-sizing: border-box;
      position: relative;
      border-bottom: 1px solid #808080;
      border-right: 1px solid #808080;
      >div{
        position: absolute;
        text-align: right;
        >.small{
          font-size: 14px;
          margin-left: 5px;
        }
      }
    }
    &-part1{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 16px; left: 50%; top: 0px; }
      &-3{ font-size: 30px; left: 40px; top: 10px; }
      &-4{ font-size: 30px; left: calc(50% + 40px); top: 10px; }
    }
    &-part2{
      width: 50%;
      &-1{ font-size: 16px; left: 0px; top: 0px; }
      &-2{ font-size: 48px; right: 40px; top: 0px; }
      &-3{ font-size: 14px; right: 5px; top: 5px; }
    }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 10px; }
      &-2{ right: 10px; top: 10px; }
    }
  }
}
</style>
