<template>
<div class="ecg-button"
    :class="{ active, disabled }"
    :style="{ width, height }"
    @click="handleClick">
  <slot/>
</div>
</template>

<script>
export default {
  props: {
    width: { type: String, default: '80px' },
    height: { type: String, default: '40px' },
    active: { type: Boolean, default: false },
    disabled: { type: Boolean, default: false }
  },

  methods: {
    handleClick () {
      if (this.disabled) { return }

      this.$emit('click')
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-button {
  box-sizing: border-box;
  border: 2px outset #aaa;
  color: #fff;
  background: linear-gradient(to bottom, #9999a6, #777784);
  display: flex; justify-content: center; align-items: center;
  cursor: pointer;
  font-size: 14px; font-weight: bold;
  text-align: center;
  &:active { border-style: inset; -webkit-tap-highlight-color: transparent; }
  &.active { border-style: inset; background: #02A6D7; }
  &.disabled {
    background: #A4A4B1; color: darken(#A4A4B1, 10);
    cursor: default;
    &:active { border-style: outset; -webkit-tap-highlight-color: transparent; }
  }
}
</style>
