<template>
<div class="ecg-alarm-box-list">
  <div class="ecg-alarm-box-list-row"
      v-for="(row, ridx) of list"
      :key="ridx">
    <ecg-alarm-box
      v-for="(item, cidx) of row"
      :key="`${ridx}-${cidx}`"
      :active="`${ridx}-${cidx}` === current"
      :disabled="item.disabled"
      :title="item.title"
      :content="item.content"
      :text1="item.text1"
      :text2="item.text2"
      @click="current = `${ridx}-${cidx}`"/>
  </div>
</div>
</template>

<script>
export default {
  props: {
    list: { type: Array, required: true }
  },

  data () {
    return {
      current: '0-0'
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-alarm-box-list {
  &-row { display: flex; }
}
</style>
