<template>
<ecg-embeded-dialog class="discharge-dlg" ref="dlg" title="Discharge">
  <div class="t-1">Delete previous data and initialize to the Master settings.</div>
  <div class="flex-center">
    <ecg-button class="mr-40" @click="reset">New Patient</ecg-button>
    <ecg-button @click="close">Cancel</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  methods: {
    ...mapMutations('patient', ['RESET']),

    open () {
      this.$refs.dlg.open()
    },

    close () {
      this.$refs.dlg.close()
    },

    reset () {
      this.RESET()
      this.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.discharge-dlg {
  .t-1 {
    font-size: 24px;
    color: #000;
    width: 500px;
    margin: 0 auto;
    padding: 200px 0;
  }
}
