<template>
  <div class="part2 d-flex">
    <div class="info" @click="$dialog.open('BasicParameter', [1, 0, 0])">
      <div class="info-1 c-blue flex-center">SpO<sub>2</sub><img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-2 c-blue">98</div>
      <div class="info-3 c-darken-blue">Off<br>90</div>
      <div class="info-4 c-purple flex-center">PR<img v-if="hiqviewMode" width="30" src="./images/wifi.jpg"></div>
      <div class="info-5 c-purple">68</div>
      <div class="info-6 c-darken-purple">Off<br>Off</div>
      <div class="drag-info" v-if="drag">SpO<sub>2</sub></div>
    </div>
    <div class="chart" @click="handleThumbnail">
      <ecg-heart-beat
        width="100%"
        height="100%"
        :dataFunc="genData"/>
      <div class="chart-info">
        <div class="chart-info-1 c-blue">SpO<sub>2</sub></div>
        <div class="chart-info-2 c-white">x1<br>Auto</div>
      </div>
      <div class="thumbnail" v-if="thumbnail">
        <ecg-heart-beat-static
          width="100%"
          height="100%"
          :dataFunc="thumbnailGenData1"/>
        <div class="thumbnail-line1"></div>
        <div class="thumbnail-line2"></div>
        <div class="thumbnail-line3"></div>
        <div class="thumbnail-info1 c-atrovirens">SpO<sub>2</sub></div>
        <div class="thumbnail-info2 c-atrovirens">100</div>
        <div class="thumbnail-info3 c-atrovirens">80</div>
      </div>
    </div>
  </div>
</template>

<script>
import { random as _random, map as _map } from 'lodash'
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      drag: false
    }
  },

  computed: {
    ...mapState('app', ['hiqviewMode', 'thumbnail'])
  },

  methods: {
    ...mapMutations('app', ['SET_THUMBNAIL']),
    genData () {
      const data1 = _map(new Array(15 * 10), (item, idx) => {
        switch (idx % 10) {
          case 0:
            return _random(-5, 5)
          case 1:
            return _random(-20, -10)
          case 2:
            return _random(-40, -30)
          case 3:
            return _random(-20, -10)
          case 4:
            return _random(-5, 5)
          case 5:
            return _random(10, 20)
          case 6:
            return _random(30, 40)
          case 7:
            return _random(20, 30)
          case 8:
            return _random(10, 20)
          case 9:
            return _random(0, 10)
          default:
            return 0
        }
      })
      return [
        {
          color: '#00F8F8',
          width: 2,
          data: data1
        }
      ]
    },
    handleThumbnail () {
      this.SET_THUMBNAIL()
    },
    thumbnailGenData1 () {
      const data = _map(new Array(20 * 10), (item, idx) => {
        switch (idx % 10) {
          case 0:
            return _random(-5, 5)
          case 1:
            return _random(-15, -5)
          case 2:
            return _random(-25, -20)
          case 3:
            return _random(-15, -5)
          case 4:
            return _random(-5, 5)
          case 5:
            return _random(5, 15)
          case 6:
            return _random(20, 25)
          case 7:
            return _random(15, 20)
          case 8:
            return _random(5, 15)
          case 9:
            return _random(0, 5)
          default:
            return 0
        }
      })
      return [
        {
          color: '#00F8F8',
          width: 2,
          data: data
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.part2{
  .info{
    height: 100%;
    width: 25%;
    position: relative;
    border: 1px solid #808080;
    border-top: none;
    box-sizing: border-box;
    >div{
      position: absolute;
      text-align: right;
      >.small{
        font-size: 14px;
        margin-left: 5px;
      }
    }
    .drag-info{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 80px;
      border: 1px solid #00F8F8;
      background-color: #333;
      color: #00F8F8;
      font-size: 24px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    &-1{ font-size: 16px; left: calc(50% - 80px); top: 30px; }
    &-2{ font-size: 160px; left: calc(50% + 20px); bottom: 0px; }
    &-3{ font-size: 14px; right: 5px; top: 60px; }
    &-4{ font-size: 16px; left: 5px; top: calc(50% + 5px); }
    &-5{ font-size: 48px; left: 70px; top: calc(50% + 40px); }
    &-6{ font-size: 14px; left: 130px; top: calc(50% + 40px); }
  }
  .chart{
    height: 100%;
    width: 75%;
    position: relative;
    &-info{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      >div{
        position: absolute;
        font-size: 16px;
        text-align: right;
      }
      &-1{ right: 90px; top: 20%; transform: translateY(-50%); }
      &-2{ right: 10px; top: 36px; }
    }
    .thumbnail{
      width: calc(100% / 6);
      height: 95%;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      z-index: 10;
      border-top: 2px solid #008787;
      border-right: 2px solid #008787;
      border-bottom: 2px solid #008787;
      box-sizing: border-box;
      &-line1{ position: absolute; height: 100%; border-left: 2px solid #008787; left: 45%; top: 0px;}
      &-line2{ position: absolute; height: 100%; border-left: 2px dashed #008787; left: calc(45% / 2); top: 0px;}
      &-line3{ position: absolute; height: 100%; border-left: 2px dashed #008787; left: calc(45% + (55% / 2)); top: 0px;}
      &-info1{ position: absolute; font-size: 16px; left: 5px; top: 5px; }
      &-info2{ position: absolute; font-size: 16px; right: 5px; top: 5px; }
      &-info3{ position: absolute; font-size: 16px; right: 5px; bottom: 5px; }
    }
  }
}
</style>
