<template>
  <div class="ecg-scale3">
    <div class="scale" :style="{ backgroundColor: scaleColor }">
      <div
        class="number-stick"
        v-for="(item, idx) of scale"
        :key="idx"
        :style="{ bottom: `calc(${item.position} - 7px)` }">
        <div class="number" :style="{ color: scaleColor, marginRight: item.type === 'long' ? '10px' : '20px' }">{{ item.text }}</div>
        <div class="stick" :style="{ width: item.type === 'long' ? '20px' : '10px', backgroundColor: scaleColor }"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      scaleColor: '#68E8A8',
      scale: [
        { text: '0', type: 'long', position: '0%' },
        { text: '5', type: 'short', position: '25%' },
        { text: '10', type: 'long', position: '50%' },
        { text: '25', type: 'short', position: '66.66%' },
        { text: '50', type: 'short', position: '83.33%' },
        { text: '100', type: 'long', position: '100%' }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.ecg-scale3{
  width: 80px;
  height: 100%;
  position: relative;
  .scale{
    width: 1px;
    height: 90%;
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    .number-stick{
      display: flex;
      align-items: center;
      height: 14px;
      position: absolute;
      right: 0px;
      &:first-of-type{
        >.number{
          position: relative;
          bottom: 5px;
        }
      }
      &:last-of-type{
        >.number{
          position: relative;
          top: 5px;
        }
      }
      .number{
        font-size: 12px;
      }
      .stick{
        height: 1px;
        margin-left: 2px;
      }
    }
  }
}
</style>
