<script>
import { mapMutations, mapActions } from 'vuex'

export default {
  methods: {
    ...mapMutations('app', ['SET_HIQVIEW_MODE']),
    ...mapActions('app', ['flashBlackScreen']),
    open () {
      this.$refs.dlg.open()
    },

    close () {
      this.$refs.dlg.close()
    },

    handleClickMonitor () {
      this.SET_HIQVIEW_MODE(true)
      this.$dialog.close()
      this.flashBlackScreen()
    }
  }
}
</script>

