<template>
<div class="MenuHome-dialog">
  <ecg-tree-tab :tabTree="tabTree" ref="treeTab"/>
</div>
</template>

<script>
export default {
  data () {
    return {
      tabTree: [
        { type: 'item', text: 'Menu', component: require('@/dialogs/MenuHome/Menu').default },
        { type: 'item', text: 'Setup', component: require('@/dialogs/MenuHome/Setup').default }
      ]
    }
  },
  methods: {
    onOpen (path) {
      this.$refs.treeTab.changeCurrent(path)
    }
  }
}
</script>
