</template>

<script>
import Hammer from 'hammerjs'
import { mapState } from 'vuex'

const IMAGES_PATH = require.context('./images/', false, /(\.png)$/)
const IMAGES = {}
IMAGES_PATH.keys().forEach(key => {
  const keyname = key.split('/').slice(1)[0].split('.')[0]
  IMAGES[keyname] = IMAGES_PATH(key)
})
export default {
  data () {
    return {
      images: IMAGES,
      currentHammer: 0,
      hammerList: [
        require('./hammer/hammer1').default,
        require('./hammer/hammer2').default,
        require('./hammer/hammer3').default
      ]
    }
  },
  mounted () {
    this.hammer = new Hammer(this.$refs.hammer)
    this.hammer.get('swipe').set({
      direction: Hammer.DIRECTION_ALL
    })
    this.hammer.on('swipe', this.handlePanend)
  },
  beforeDestroy () {
    this.hammer.destroy()
  },

  computed: {
    ...mapState('app', ['hiqviewMode'])
  },

  methods: {
    handlePanend (e) {
      if (e.direction === Hammer.DIRECTION_RIGHT || e.direction === Hammer.DIRECTION_DOWN) {
        if (this.currentHammer > 0) {
          this.currentHammer -= 1
        } else {
          this.currentHammer = 2
        }
      } else if (e.direction === Hammer.DIRECTION_LEFT || e.direction === Hammer.DIRECTION_UP) {
        if (this.currentHammer < 2) {
          this.currentHammer += 1
        } else {
          this.currentHammer = 0
        }
      }
    }
  }
}
</script>

