<template>
<ecg-embeded-dialog class="gender-dlg" ref="dlg" title="Gender" width="350px" height="200px">
  <div class="wrapper">
    <ecg-button width="100px" :active="gender === 'Male'" @click="set('Male')">Male</ecg-button>
    <ecg-button width="100px" :active="gender === 'Female'" @click="set('Female')">Female</ecg-button>
    <ecg-button width="100px" :active="gender === 'Unknown'" @click="set('Unknown')">Unknown</ecg-button>
  </div>
</ecg-embeded-dialog>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState('patient', ['gender'])
  },

  methods: {
    ...mapMutations('patient', ['SET_GENDER']),

    open () {
      this.$refs.dlg.open()
    },

    set (gender) {
      this.SET_GENDER(gender)
      this.$refs.dlg.close()
    }
  }
}
</script>

<style lang="scss" scoped>
.gender-dlg {
  .wrapper {
    height: 100%;
    display: flex; justify-content: space-between; align-items: center;
  }
}
</style>
